import { FORMS_DATASOURCE_WORKSPACE_CONTEXT } from "../workspace/datasource-workspace.context-token.js";
import { FormsFieldTypeCollectionRepository } from "../../provider-types/field-types/repository/collection/index.js";
import { UMB_MODAL_MANAGER_CONTEXT } from "@umbraco-cms/backoffice/modal";
import {
  UmbWorkspaceActionBase,
  type UmbWorkspaceAction,
} from "@umbraco-cms/backoffice/workspace";
import { FORMS_CREATE_FORM_FROM_DATASOURCE_MODAL } from "@umbraco-forms/modals";

export class FormsCreateFormFromDataSourceWorkspaceAction
  extends UmbWorkspaceActionBase
  implements UmbWorkspaceAction
{
  async execute() {
    const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
    if (!modalManager) throw new Error("Modal manager context not found");

    const workspaceContext = await this.getContext(
      FORMS_DATASOURCE_WORKSPACE_CONTEXT,
    );

    const fieldTypesRepository = new FormsFieldTypeCollectionRepository(this);
    const { data } = await fieldTypesRepository.requestCollection();
    const fieldTypes = data?.items || [];

    const modalHandler = modalManager.open(
      this,
      FORMS_CREATE_FORM_FROM_DATASOURCE_MODAL,
      {
        data: {
          fieldTypes,
        },
        value: {
          wizard: await workspaceContext?.getWizardScaffold(),
        },
      },
    );

    modalHandler.onSubmit().catch(() => undefined);
  }
}

// Declare a api export, so Extension Registry can initialize this class:
export const api = FormsCreateFormFromDataSourceWorkspaceAction;
