import { UmbId } from "@umbraco-cms/backoffice/id";
import {
  FieldConditionActionType,
  FieldConditionLogicType,
  IncludeSensitiveData,
} from "@umbraco-forms/generated";
import type {
  FormWorkflowWithTypeSettings,
  Field,
  FieldCondition,
  FieldSet,
  FieldsetContainer,
  Page,
} from "@umbraco-forms/generated";

export class FormDesignScaffoldsGenerator {
  static getPageScaffold(): Page {
    const id = UmbId.new();
    const fieldSet = this.getFieldsetScaffold();
    fieldSet.page = id;

    return {
      caption: "",
      fieldSets: [fieldSet],
      sortOrder: 0,
      id,
      form: "",
      buttonCondition: null,
    };
  }

  static getFieldsetScaffold(): FieldSet {
    return {
      caption: "",
      sortOrder: 0,
      id: UmbId.new(),
      page: "",
      containers: [this.getContainerScaffold()],
      condition: null,
    };
  }

  static getContainerScaffold(): FieldsetContainer {
    return {
      id: UmbId.new(),
      caption: "",
      width: 12,
      fields: [],
    };
  }

  static getQuestionScaffold(): Field {
    return {
      caption: "",
      alias: "",
      tooltip: "",
      id: UmbId.new(),
      cssClass: "",
      fieldTypeId: "",
      mandatory: false,
      prevalueSourceId: "",
      preValues: [],
      dataSourceFieldKey: "",
      condition: this.getConditionScaffold(),
      regex: "",
      requiredErrorMessage: "",
      invalidErrorMessage: "",
      containsSensitiveData: false,
      settings: {},
      allowedUploadTypes: [],
      allowMultipleFileUploads: false,
    };
  }

  static getConditionScaffold(): FieldCondition {
    return {
      id: UmbId.new(),
      enabled: false,
      actionType: FieldConditionActionType.SHOW,
      logicType: FieldConditionLogicType.ALL,
      rules: [],
    };
  }

  static getWorkflowScaffold(): FormWorkflowWithTypeSettings {
    return {
      id: UmbId.new(),
      name: "",
      form: "",
      active: true,
      includeSensitiveData: IncludeSensitiveData.FALSE,
      isDeleted: false,
      sortOrder: 0,
      workflowTypeId: "",
      workflowTypeName: "",
      workflowTypeDescription: "",
      workflowTypeIcon: "",
      workflowTypeGroup: "",
      settings: {},
      isMandatory: false,
      condition: null,
    };
  }
}
