import { FormsPrevalueSourceDetailRepository } from "../../../../../prevaluesources/repository/index.js";
import { FormsFormDesignElementBaseElement } from "./forms-design-element-base.element.js";
import {
  html,
  customElement,
  property,
  when,
  css,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import type {
  FieldTypeWithSettings,
  Field,
  FieldPrevalue,
} from "@umbraco-forms/generated";
import type {
  UUITextareaElement,
  UUIInputEvent,
} from "@umbraco-cms/backoffice/external/uui";
import { UUITextareaEvent } from "@umbraco-cms/backoffice/external/uui";
import type { FormsEditFieldModalValue } from "@umbraco-forms/modals";
import { umbExtensionsRegistry } from "@umbraco-cms/backoffice/extension-registry";
import { GUID_EMPTY } from "@umbraco-forms/entities";

const elementName = "forms-form-field";

@customElement(elementName)
export class FormsFormFieldElement extends FormsFormDesignElementBaseElement {
  @property({ type: Object })
  field!: Field;

  @property()
  fieldsetId?: string;

  @property()
  pageId?: string;

  @property({ type: Number })
  pageIndex: number = 0;

  @property({ type: Number })
  fieldsetIndex: number = 0;

  @property({ type: Number })
  containerIndex: number = 0;

  @state()
  _aliasLocked = true;

  @state()
  _fieldPrevalues: Array<FieldPrevalue> = [];

  async connectedCallback() {
    super.connectedCallback();

    await this.#ensurePrevalues();
  }

  async #ensurePrevalues() {
    await this.#ensurePrevaluesFromFieldSettings(
      this.field.prevalueSourceId,
      this.field.preValues,
    );
  }

  async #ensurePrevaluesFromFieldSettings(
    prevalueSourceId: string | null | undefined,
    prevalues: FieldPrevalue[],
  ) {
    if (!this.workspaceContext) return;

    // Prevalues defined directly on the field will already be populated. Those defined
    // using a prevalue source need to be loaded so we have them when previewing.
    if (prevalueSourceId !== GUID_EMPTY) {
      const prevalueSourceDetailRepository =
        new FormsPrevalueSourceDetailRepository(this);
      const { data } = await prevalueSourceDetailRepository.requestPrevalues(
        this.field.prevalueSourceId,
        this.workspaceContext.getUnique(),
        this.field.id,
      );
      if (data) {
        this._fieldPrevalues = data.map((pv) => {
          return { value: pv.value, caption: pv.caption };
        });
      }
    } else {
      this._fieldPrevalues = prevalues;
    }
  }

  async #onEditClick() {
    const onSubmit = async (value: FormsEditFieldModalValue) => {
      this.#setFieldProperty("caption", value.caption);
      this.#setFieldProperty("alias", value.alias);
      this.#setFieldProperty("tooltip", value.tooltip);
      this.#setFieldProperty("fieldTypeId", value.fieldType.id);
      this.#setFieldProperty(
        "containsSensitiveData",
        value.containsSensitiveData,
      );
      this.#setFieldProperty("allowedUploadTypes", value.allowedUploadTypes);
      this.#setFieldProperty(
        "allowMultipleFileUploads",
        value.allowMultipleFileUploads,
      );
      this.#setFieldProperty("preValues", value.prevalues);
      this.#setFieldProperty("prevalueSourceId", value.prevalueSourceId);
      this.#setFieldProperty("mandatory", value.mandatory);
      this.#setFieldProperty(
        "requiredErrorMessage",
        value.requiredErrorMessage,
      );
      this.#setFieldProperty("regex", value.regex);
      this.#setFieldProperty("invalidErrorMessage", value.invalidErrorMessage);
      this.#setFieldProperty("settings", value.settings);
      this.#setFieldProperty("condition", value.condition);

      const containerIndexPath =
        this.workspaceContext!.getContainerIndexPathForField(this.field.id);
      this.workspaceContext!.moveField(
        this.field.id,
        containerIndexPath,
        value.containerIndexPath,
      );

      await this.#ensurePrevaluesFromFieldSettings(
        value.prevalueSourceId,
        value.prevalues,
      );
    };
    await this.editNewOrExistingField(this.field, false, onSubmit);
  }

  #onDeleteClick() {
    this.structureManager?.deleteQuestion(
      this.field,
      this.fieldsetId,
      this.pageId,
    );
  }

  #onCopyClick() {
    this.structureManager?.copyQuestion(
      this.field,
      this.fieldsetId,
      this.pageId,
    );
  }

  #onCaptionChange(e: UUIInputEvent) {
    if (this.field) {
      const fieldName = "caption";
      // Check the event was fired for the field in question (without this we'll get this event re-fired
      // when the property updates due to changes in other fields).
      if (e.target.name === fieldName) {
        this.#setFieldProperty(fieldName, e.target.value.toString());
      }
    }
  }

  #onToggleAliasLock() {
    this._aliasLocked = !this._aliasLocked;
  }

  #onAliasChange(e: UUIInputEvent) {
    if (this.field) {
      const fieldName = "alias";
      if (e.target.name === fieldName) {
        this.#setFieldProperty(fieldName, e.target.value.toString());
      }
    }
  }

  #onTooltipChange(e: Event) {
    if (e instanceof UUITextareaEvent && this.field) {
      const target = e.composedPath()[0] as unknown as UUITextareaElement;
      const fieldName = "tooltip";
      if (target.name === fieldName) {
        this.#setFieldProperty(fieldName, (target.value as string).toString());
      }
    }
  }

  #setFieldProperty(propertyAlias: string, value: unknown) {
    this.workspaceContext!.setFieldProperty(
      this.pageIndex,
      this.fieldsetIndex,
      this.containerIndex,
      this.index,
      propertyAlias,
      value,
    );
  }

  #renderFieldTypePreview(field: Field) {
    const fieldType = this.allFieldTypes.find(
      (ft) => ft.id == field.fieldTypeId,
    );
    if (!fieldType) return;

    return html`<div id="fieldType">
      <div id="tags">
        <uui-tag look="default">${fieldType.name}</uui-tag>
        ${when(
          field.mandatory,
          () =>
            html`<uui-tag look="default"
              ><span
                >* ${this.localize.term("general_mandatory")}</span
              ></uui-tag
            >`,
        )}
        ${when(
          field.containsSensitiveData,
          () =>
            html`<uui-tag look="default"
              ><span
                ><uui-icon name="icon-lock"></uui-icon>${this.localize.term(
                  "fieldSettings_sensitiveData",
                )}</span
              ></uui-tag
            >`,
        )}
      </div>
      ${this.#renderFieldTypePreviewForType(field, fieldType)}
    </div>`;
  }

  #renderFieldTypePreviewForType(
    field: Field,
    fieldType: FieldTypeWithSettings,
  ) {
    // If we have a preview registered, use it.
    const fieldPreviewManifest = umbExtensionsRegistry.getByAlias(
      fieldType.previewView,
    );
    if (fieldPreviewManifest) {
      return html`<umb-extension-slot
        type="formsFieldPreview"
        .filter=${(ext) => ext.alias === fieldType.previewView}
        .props=${{ settings: field.settings, prevalues: this._fieldPrevalues }}
      >
      </umb-extension-slot>`;
    }

    return html``;
  }

  renderSortableField() {
    return html`<div class="sortable">
      <uui-icon name="icon-navigation"></uui-icon>
      ${this.field.caption}
      <span style="color: var(--uui-color-disabled-contrast)"
        >(${this.field.alias})</span
      >
    </div>`;
  }

  render() {
    if (this.sortModeActive) {
      return this.renderSortableField();
    }

    return html`<div id="fieldHeader">
        <!-- TODO => lifted from doc type header, confirm changes are ok -->
        <uui-input
          id="caption"
          name="caption"
          .value=${this.field.caption}
          @change="${this.#onCaptionChange}"
          label="caption"
        >
          <!-- TODO: should use UUI-LOCK-INPUT, but that does not fire an event when its locked/unlocked -->
          <uui-input
            name="alias"
            slot="append"
            label="alias"
            @change=${this.#onAliasChange}
            .value=${this.field.alias}
            placeholder="Enter alias..."
            ?disabled=${this._aliasLocked}
          >
            <!-- TODO: validation for bad characters -->
            <div
              @click=${this.#onToggleAliasLock}
              @keydown=${() => ""}
              id="alias-lock"
              slot="prepend"
            >
              <uui-icon
                name=${this._aliasLocked ? "icon-lock" : "icon-unlocked"}
              ></uui-icon>
            </div>
          </uui-input>
        </uui-input>

        <uui-action-bar>
          <uui-button
            label="edit"
            look="secondary"
            color="default"
            @click=${this.#onEditClick}
          >
            <uui-icon name="settings"></uui-icon>
          </uui-button>
          <uui-button
            label="copy"
            look="secondary"
            color="default"
            @click=${this.#onCopyClick}
          >
            <uui-icon name="copy"></uui-icon>
          </uui-button>
          <uui-button
            label=${this.localize.term("general_delete")}
            look="secondary"
            color="default"
            @click=${this.#onDeleteClick}
          >
            <uui-icon name="delete"></uui-icon>
          </uui-button>
        </uui-action-bar>
      </div>

      <div id="fieldContent">
        <div id="help">
          <uui-textarea
            name="tooltip"
            .placeholder=${this.localize.term("formEdit_helpText")}
            .value=${this.field.tooltip}
            @change="${this.#onTooltipChange}"
            label="tooltip"
          ></uui-textarea>
        </div>
        ${this.#renderFieldTypePreview(this.field)}
      </div>

      ${when(
        !this.sortModeActive,
        () =>
          html` <forms-form-condition-summary
            formElement="field"
            .condition=${this.field.condition ?? undefined}
            .operatorTypes=${this.workspaceContext?.getConditionOperators ?? []}
            .allFields=${this.allFields}
          >
          </forms-form-condition-summary>`,
      )} `;
  }

  static styles = [
    css`
      :host {
        display: block;
        border-bottom: 1px solid #e9e9eb;
        padding: var(--uui-size-layout-1) 0;
      }

      #fieldHeader {
        display: flex;
        margin-bottom: var(--uui-size-3);
      }

      uui-action-bar {
        margin-left: var(--uui-size-3);
      }

      #caption {
        flex: 1;
        margin-right: var(--uui-size-3);
      }

      #help {
        width: 300px;
      }

      #fieldContent {
        display: flex;
      }

      #fieldType {
        flex: 1 1 auto;
        background-color: var(--uui-color-surface-alt);
        margin-left: var(--uui-size-3);
        padding: var(--uui-size-3);
      }

      #tags {
        margin-bottom: var(--uui-size-3);
        display: flex;
        gap: var(--uui-size-space-2);
      }

      :host([sort-mode-active]) {
        position: relative;
        display: flex;
        padding: 0;
        margin-bottom: var(--uui-size-3);
      }

      :host([sort-mode-active]:last-of-type) {
        margin-bottom: 0;
      }

      :host([sort-mode-active]:not([inherited])) {
        cursor: grab;
      }

      :host([sort-mode-active]) .sortable {
        flex: 1;
        display: flex;
        background-color: var(--uui-color-divider);
        align-items: center;
        padding: 0 var(--uui-size-3);
        gap: var(--uui-size-3);
      }

      :host([sort-mode-active]) uui-input {
        max-width: 75px;
      }

      /* Placeholder style, used when property is being dragged.*/
      :host(.--umb-sorter-placeholder) > * {
        visibility: hidden;
      }

      :host(.--umb-sorter-placeholder)::after {
        content: "";
        inset: 0;
        position: absolute;
        border: 1px dashed var(--uui-color-divider-emphasis);
        border-radius: var(--uui-border-radius);
      }

      uui-tag uui-icon {
        font-size: xx-small;
        margin-right: 4px;
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormFieldElement;
  }
}
