import type { SubmitMessageDetail } from "../types.js";
import { FormsFormDesignElementBaseElement } from "./forms-design-element-base.element.js";
import {
  html,
  customElement,
  property,
  when,
  css,
} from "@umbraco-cms/backoffice/external/lit";
import {
  IncludeSensitiveData,
  type FormWorkflowWithTypeSettings,
} from "@umbraco-forms/generated";
import {
  FORMS_EDIT_SUBMIT_MESSAGE_MODAL,
  FORMS_EDIT_WORKFLOW_MODAL,
} from "@umbraco-forms/modals";

const elementName = "forms-form-workflow-summary-stage";

@customElement(elementName)
export class FormsFormWorkflowSummaryStageElement extends FormsFormDesignElementBaseElement {
  @property({ type: Array })
  workflows: Array<FormWorkflowWithTypeSettings> = [];

  @property()
  collectionName = "";

  @property({ type: Object })
  submitMessageDetail?: SubmitMessageDetail;

  @property()
  label = "";

  @property()
  icon = "";

  async #onSubmitMessageClick() {
    if (!this.modalContext) return;
    if (!this.workspaceContext) return;

    const richTextConfiguration =
      await this.workspaceContext!.getRichTextConfiguration();

    const modalHandler = this.modalContext.open(
      this,
      FORMS_EDIT_SUBMIT_MESSAGE_MODAL,
      {
        data: {
          richTextConfiguration,
        },
        value: {
          messageOnSubmit: this.submitMessageDetail?.messageOnSubmit,
          messageOnSubmitIsHtml:
            this.submitMessageDetail?.messageOnSubmitIsHtml || false,
          goToPageOnSubmit: this.submitMessageDetail?.goToPageOnSubmit,
        },
      },
    );

    const value = await modalHandler.onSubmit().catch(() => undefined);
    if (!value) return;

    this.workspaceContext!.setFormProperty(
      "messageOnSubmit",
      value.messageOnSubmit,
    );
    this.workspaceContext!.setFormProperty(
      "messageOnSubmitIsHtml",
      value.messageOnSubmitIsHtml,
    );
    this.workspaceContext!.setFormProperty(
      "goToPageOnSubmit",
      value.goToPageOnSubmit,
    );
  }

  async #onWorkflowClick(index: number) {
    if (!this.modalContext) return;

    const workflow = this.workflows[index];

    const workflowType = await this.workspaceContext!.loadWorkflowType(
      workflow.workflowTypeId,
    );

    if (!workflowType) {
      throw new Error(
        `Workflow type with id ${workflow.workflowTypeId} could not be found.`,
      );
    }

    const modalHandler = this.modalContext.open(
      this,
      FORMS_EDIT_WORKFLOW_MODAL,
      {
        data: {
          fields: this.allFields,
          workflowType,
          isNew: false,
        },
        value: {
          name: workflow.name,
          active: workflow.active,
          includeSensitiveData:
            workflow.includeSensitiveData === IncludeSensitiveData.TRUE,
          collectionName: this.collectionName,
          settings: workflow.settings,
          condition: workflow.condition,
        },
      },
    );

    const value = await modalHandler.onSubmit().catch(() => undefined);
    if (!this.workspaceContext || !value) return;

    this.workspaceContext.setWorkflowProperty(
      this.collectionName,
      index,
      "name",
      value.name,
    );
    this.workspaceContext.setWorkflowProperty(
      this.collectionName,
      index,
      "active",
      value.active,
    );
    this.workspaceContext.setWorkflowProperty(
      this.collectionName,
      index,
      "includeSensitiveData",
      value.includeSensitiveData
        ? IncludeSensitiveData.TRUE
        : IncludeSensitiveData.FALSE,
    );
    this.workspaceContext.setWorkflowProperty(
      this.collectionName,
      index,
      "settings",
      value.settings,
    );
    this.workspaceContext.setWorkflowProperty(
      this.collectionName,
      index,
      "condition",
      value.condition,
    );

    if (value.collectionName != this.collectionName) {
      this.workspaceContext.moveWorkflow(
        index,
        this.collectionName,
        value.collectionName,
      );
    }
  }

  render() {
    return html` <uui-icon .name=${this.icon}></uui-icon>
      <span>${this.label}</span>
      <uui-icon name="icon-arrow-right"></uui-icon>
      ${when(
        this.submitMessageDetail,
        () =>
          html`<uui-button
            compact
            look="outline"
            color="positive"
            .label=${this.localize.term("formWorkflows_submitMessage")}
            @click=${this.#onSubmitMessageClick}
          ></uui-button>`,
      )}
      ${this.workflows?.map(
        (workflow, idx) =>
          html` ${when(
              this.submitMessageDetail || idx > 0,
              () => html`<span>and</span>`,
            )}
            <uui-button
              compact
              label=${workflow.name}
              look=${workflow.active ? "outline" : "placeholder"}
              color=${workflow.active ? "positive" : "default"}
              @click=${() => this.#onWorkflowClick(idx)}
            ></uui-button>`,
      )}`;
  }

  static styles = [
    css`
      :host {
        display: flex;
        align-items: center;
        column-gap: var(--uui-size-3);
      }

      uui-icon:first-of-type {
        border: 1px solid var(--uui-color-border-standalone);
        border-radius: 50%;
        padding: var(--uui-size-2);
        width: var(--uui-size-6);
        height: var(--uui-size-6);
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormWorkflowSummaryStageElement;
  }
}
