import { FORMS_PREVALUESOURCE_ENTITY_TYPE } from "../entities.js";
import type { FormsSecurityPermissionConditionConfig } from "../../security/conditions/index.js";
import { FORMS_SECURITY_PERMISSION_CONDITION } from "../../security/conditions/manifests.js";
import type {
  ManifestWorkspace,
  ManifestWorkspaceAction,
  ManifestWorkspaceView,
} from "@umbraco-cms/backoffice/workspace";
import { UmbSaveWorkspaceAction } from "@umbraco-cms/backoffice/workspace";
import type { FormSecurityForUser } from "@umbraco-forms/generated";

export const PREVALUESOURCE_WORKSPACE_ALIAS = "Forms.Workspace.PrevalueSource";

const workspace: ManifestWorkspace = {
  type: "workspace",
  kind: "routable",
  alias: PREVALUESOURCE_WORKSPACE_ALIAS,
  name: "Prevalue Source Workspace",
  api: () => import("./prevaluesource-workspace.context.js"),
  meta: {
    entityType: FORMS_PREVALUESOURCE_ENTITY_TYPE,
  },
};

const workspaceViews: Array<ManifestWorkspaceView> = [
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.PrevalueSource.Design",
    name: "Form Workspace Design View",
    element: () =>
      import("./views/design/workspace-view-prevaluesource-design.element.js"),
    weight: 90,
    meta: {
      label: "Design",
      pathname: "design",
      icon: "document",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: PREVALUESOURCE_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.managePreValueSources,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.PrevalueSource.Info",
    name: "Form Workspace Info View",
    element: () =>
      import("./views/info/workspace-view-prevaluesource-info.element.js"),
    weight: 90,
    meta: {
      label: "Info",
      pathname: "info",
      icon: "info",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: PREVALUESOURCE_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.managePreValueSources,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
];

const workspaceActions: Array<ManifestWorkspaceAction> = [
  {
    type: "workspaceAction",
    kind: "default",
    alias: "Forms.WorkspaceAction.PrevalueSource.Save",
    name: "Save Prevalue Source Workspace Action",
    api: UmbSaveWorkspaceAction,
    meta: {
      label: "Save",
      look: "primary",
      color: "positive",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: PREVALUESOURCE_WORKSPACE_ALIAS,
      },
    ],
  },
];

export const manifests = [workspace, ...workspaceViews, ...workspaceActions];
