import { FORMS_FORM_ITEM_REPOSITORY_ALIAS } from "../../section/forms/repository/index.js";
import type { FormTreeItemModel } from "../../section/forms/tree/index.js";
import { UmbPickerInputContext } from "@umbraco-cms/backoffice/picker-input";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import { FORMS_FORM_PICKER_MODAL } from "@umbraco-forms/modals";
import type { UmbPickerModalData } from "@umbraco-cms/backoffice/modal";

export class FormsFormPickerContext extends UmbPickerInputContext<
  FormTreeItemModel,
  FormTreeItemModel,
  UmbPickerModalData<FormTreeItemModel> & { hideTreeRoot: boolean }
> {
  constructor(host: UmbControllerHost) {
    super(
      host,
      FORMS_FORM_ITEM_REPOSITORY_ALIAS,
      FORMS_FORM_PICKER_MODAL,
      (entry) => entry.unique,
    );
  }
}
