import { FormsDataSourceDetailRepository } from "./datasource-detail.repository.js";
import { FormsDataSourceDetailStore } from "./datasource-detail.store.js";
import type {
  ManifestRepository,
  ManifestStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_FORM_DETAIL_REPOSITORY_ALIAS =
  "Forms.Repository.DataSource.Detail";
export const FORMS_FORM_DETAIL_STORE_ALIAS = "Forms.Store.DataSource.Detail";

const repository: ManifestRepository = {
  type: "repository",
  alias: FORMS_FORM_DETAIL_REPOSITORY_ALIAS,
  name: "Data Source Detail Repository",
  api: FormsDataSourceDetailRepository,
};

const store: ManifestStore = {
  type: "store",
  alias: FORMS_FORM_DETAIL_STORE_ALIAS,
  name: "Data Source Detail Store",
  api: FormsDataSourceDetailStore,
};

export const manifests = [repository, store];
