import type { FieldPrevalueDetail } from "../../section/forms/types.js";
import { UmbModalToken } from "@umbraco-cms/backoffice/modal";
import type {
  EntrySearchResultSchema,
  FieldData,
  MemberData,
} from "@umbraco-forms/generated";

export const FORMS_ENTRY_DETAILS_MODAL_ALIAS = "Forms.EntryDetails.Modal";

export interface FormsEntryDetailsModalData {
  schema: Array<EntrySearchResultSchema>;
  fieldPrevalues: Array<FieldPrevalueDetail>;
}

export interface FormsEntryDetailsModalValue {
  id: number;
  unique: string;
  created: Date;
  updated: Date;
  state: string;
  pageName?: string;
  documentUnique?: string;
  fields: Array<FieldData>;
  member?: MemberData | null;
  updateCollection?: boolean;
}

export const FORMS_ENTRY_DETAILS_MODAL = new UmbModalToken<
  FormsEntryDetailsModalData,
  FormsEntryDetailsModalValue
>(FORMS_ENTRY_DETAILS_MODAL_ALIAS, {
  modal: {
    type: "sidebar",
    size: "large",
  },
});
