import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import {
  css,
  customElement,
  html,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { FORMS_CONTEXT } from "@umbraco-forms/context";

const elementName = "forms-settings-data-retention";

@customElement(elementName)
export class FormsSettingsDataRetentionElement extends FormsSettingsElementBaseElement {
  #scheduledRecordDeletionEnabled: boolean = false;

  constructor() {
    super();

    this.consumeContext(FORMS_CONTEXT, (instance) => {
      if (!instance) return;
      this.observe(instance.config, (config) => {
        if (config) {
          this.#scheduledRecordDeletionEnabled =
            config?.scheduledRecordDeletionEnabled;
        }
      });
    });
  }

  render() {
    return html`<umb-property-layout
      alias="dataRetention"
      .label=${this.localize.term("formSettings_dataRetention")}
      .description=${this.localize.term(
        "formSettings_dataRetentionDescription",
      )}
    >
      <div slot="editor">
        <forms-settings-data-retention-stage stage="Submitted">
        </forms-settings-data-retention-stage>
        <forms-settings-data-retention-stage stage="Approved">
        </forms-settings-data-retention-stage>
        <forms-settings-data-retention-stage stage="Rejected">
        </forms-settings-data-retention-stage>

        ${when(
          !this.#scheduledRecordDeletionEnabled,
          () =>
            html`<div class="note">
              ${this.localize.term(
                "formSettings_scheduledRecordDeletionNotEnabled",
              )}
            </div>`,
        )}
      </div>
    </umb-property-layout>`;
  }

  static styles = [
    css`
      .note {
        font-style: italic;
        margin-top: 10px;
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsDataRetentionElement;
  }
}
