import type { ManifestEntityAction } from "@umbraco-cms/backoffice/entity-action";
import type { ManifestModal } from "@umbraco-cms/backoffice/modal";
import type { SecurityOptionsConditionConfig } from "../../conditions/index.js";
import { FORMS_SECURITY_OPTIONS_CONDITION } from "../../conditions/manifests.js";
import { FORMS_SECURITY_USER_ENTITY_TYPE } from "../../entities.js";
import { FormsDeleteUserSecurityEntityAction } from "./delete.action.js";
import type { BackOfficeConfig } from "@umbraco-forms/generated";

const entityActions: Array<ManifestEntityAction | ManifestModal> = [
  {
    type: "entityAction",
    kind: "default",
    alias: "Forms.EntityAction.Security.User.Delete",
    name: "Delete Form Entity Action",
    weight: 100,
    api: FormsDeleteUserSecurityEntityAction,
    forEntityTypes: [FORMS_SECURITY_USER_ENTITY_TYPE],
    meta: {
      icon: "icon-delete",
      label: "Delete...",
    },
    conditions: [
      {
        alias: FORMS_SECURITY_OPTIONS_CONDITION,
        match: (config: BackOfficeConfig) =>
          config.manageSecurityWithUserGroups,
      } as SecurityOptionsConditionConfig, // This is necessary as ConditionType is defined in core, and we don't have the ability to extend it.
    ],
  },
  {
    type: "modal",
    alias: "Forms.Modal.UserSecurityDeleteConfirm",
    name: "User Security Delete Confirm Modal",
    js: () => import("./modal/user-security-delete-confirm-modal.element.js"),
  },
];

export const manifests: Array<ManifestEntityAction | ManifestModal> = [...entityActions];
