import { FORMS_DATASOURCE_ENTITY_TYPE } from "../entities.js";
import type { FormsDataSourceWorkspaceContext } from "./datasource-workspace.context.js";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbWorkspaceContext } from "@umbraco-cms/backoffice/workspace";

export const FORMS_DATASOURCE_WORKSPACE_CONTEXT = new UmbContextToken<
  UmbWorkspaceContext,
  FormsDataSourceWorkspaceContext
>(
  "UmbWorkspaceContext",
  undefined,
  (context): context is FormsDataSourceWorkspaceContext =>
    context.getEntityType?.() === FORMS_DATASOURCE_ENTITY_TYPE,
);
