import { RecordService } from "@umbraco-forms/generated";
import type { BasicForm, UserSecurity } from "@umbraco-forms/generated";
import {
  UUIRefNodeElement,
  UUIRefNodeFormElement,
} from "@umbraco-cms/backoffice/external/uui";
import {
  html,
  customElement,
  property,
  when,
  css,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import type { BasicFormExtended } from "@umbraco-forms/entities";
import { tryExecute } from "@umbraco-cms/backoffice/resources";

type FormEventType = "view" | "edit";

const elementName = "ref-form";

@customElement(elementName)
export class FormsRefFormElement extends UmbElementMixin(
  UUIRefNodeFormElement,
) {
  selectable = false;

  @property({ type: Object })
  model?: BasicForm | BasicFormExtended;

  @property({ type: Object })
  config?: UserSecurity;

  @state()
  _count = 0;

  async connectedCallback() {
    super.connectedCallback();

    if (!this.model || !this.config?.viewEntries) return;

    if ("count" in this.model) {
      this._count = (<BasicFormExtended>this.model).count;
    } else {
      const { data } = await tryExecute(
        this,
        RecordService.getFormByFormIdRecordMetadata({
          path: { formId: this.model?.id },
        }),
      );

      this._count = data.count;
    }
  }

  #emit(event: FormEventType) {
    this.dispatchEvent(new CustomEvent(event));
  }

  #renderButton(type: FormEventType, labelKey: string) {
    return html`<uui-button
      @click=${() => this.#emit(type)}
      label=${this.localize.term(labelKey)}
    ></uui-button>`;
  }

  override render() {
    return html`
      <div id="open-part" tabindex="0">
        <div id="content">
          <span id="icon"><uui-icon .svg=${this.fallbackIcon}></uui-icon></span>
          <div id="info">
            <div id="name">${this.model!.name}</div>
            ${this.renderDetail()}
          </div>
        </div>
      </div>
      <div id="select-border"></div>

      <slot name="actions" id="actions-container">
        <uui-action-bar>
          ${when(this.config?.manageForms, () =>
            this.#renderButton("edit", "formsDashboard_editForm"),
          )}
          ${when(this.config?.viewEntries, () =>
            this.#renderButton("view", "formsDashboard_viewEntries"),
          )}
        </uui-action-bar>
      </slot>
      ${when(
        this.config?.viewEntries,
        () =>
          html` <uui-tag size="s" slot="tag" color="positive"
            >${this.localize.term(
              "formsDashboard_entriesCount",
              this._count,
            )}</uui-tag
          >`,
      )}
    `;
  }

  static styles = [
    ...UUIRefNodeElement.styles,
    css`
      #open-part {
        cursor: default;
      }

      #open-part:hover #name,
      #open-part:hover #icon {
        color: var(--uui-color-text) !important;
        text-decoration: none !important;
      }

      #info {
        min-width: 150px;
      }

      #actions-container {
        margin-left: auto;
      }

      uui-action-bar {
        justify-content: end;
      }

      uui-tag {
        margin: 0 var(--uui-size-8);
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsRefFormElement;
  }
}
