import type { FormsChooseFieldTypeModalValue } from "../token/form-choose-field-type-modal.token.js";
import { FormsFieldTypeCollectionRepository } from "../../section/provider-types/field-types/repository/collection/field-type-collection.repository.js";
import { customElement, html } from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";
import type { FieldTypeWithSettings } from "@umbraco-forms/generated";

const elementName = "form-choose-field-type-modal";

@customElement(elementName)
export class FormsChooseFieldTypeModalElement extends UmbModalBaseElement<
  object,
  FormsChooseFieldTypeModalValue
> {
  #fieldTypes: Array<FieldTypeWithSettings> = [];

  constructor() {
    super();

    this.#initialize();
  }

  async #initialize() {
    const repository = new FormsFieldTypeCollectionRepository(this);
    const { data } = await repository.requestCollection();
    this.#fieldTypes = data?.items || [];
    this.requestUpdate();
  }

  #onOpen(selectedValue: FieldTypeWithSettings) {
    this.modalContext?.updateValue({ selectedValue });
    this.modalContext?.submit();
  }

  render() {
    return html`<umb-body-layout
      .headline=${this.localize.term("formEdit_chooseFieldType")}
    >
      <uui-box>
        <uui-ref-list>
          ${this.#fieldTypes.map(
            (fieldType) =>
              html`<umb-ref-item
                .name=${fieldType.name}
                .detail=${fieldType.description}
                .icon=${fieldType.icon}
                @open=${() => this.#onOpen(fieldType)}
              ></umb-ref-item>`,
          )}
        </uui-ref-list>
      </uui-box>
      <div slot="actions">
        <uui-button
          label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }
}

export default FormsChooseFieldTypeModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsChooseFieldTypeModalElement;
  }
}
