import {
  FORMS_FORM_ENTITY_TYPE,
  FORMS_FORM_FOLDER_ENTITY_TYPE,
  FORMS_FORM_ROOT_ENTITY_TYPE,
} from "../entities.js";
import { FormsFormTreeRepository } from "./forms-tree.repository.js";
import { FormsFormTreeStore } from "./forms-tree.store.js";
import { manifests as menuManifests } from "./menu-item/manifests.js";
import type {
  ManifestTree,
  ManifestTreeItem,
} from "@umbraco-cms/backoffice/tree";
import type {
  ManifestRepository,
  ManifestTreeStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_FORM_TREE_REPOSITORY_ALIAS = "Forms.Repository.Forms.Tree";
export const FORMS_FORM_TREE_STORE_ALIAS = "Forms.Store.Forms.Tree";
export const FORMS_FORM_TREE_ALIAS = "Forms.Tree.Forms";

const treeRepository: ManifestRepository = {
  type: "repository",
  alias: FORMS_FORM_TREE_REPOSITORY_ALIAS,
  name: "Form Tree Repository",
  api: FormsFormTreeRepository,
};

const treeStore: ManifestTreeStore = {
  type: "treeStore",
  alias: FORMS_FORM_TREE_STORE_ALIAS,
  name: "Form Tree Store",
  api: FormsFormTreeStore,
};

const tree: ManifestTree = {
  type: "tree",
  kind: "default",
  alias: FORMS_FORM_TREE_ALIAS,
  name: "Forms Tree",
  meta: {
    repositoryAlias: FORMS_FORM_TREE_REPOSITORY_ALIAS,
  },
  // No condition on this tree, as it's accessible from content for picking forms.
};

const treeItem: ManifestTreeItem = {
  type: "treeItem",
  kind: "default",
  alias: "Forms.TreeItem.Form",
  name: "Form Tree Item",
  forEntityTypes: [
    FORMS_FORM_ROOT_ENTITY_TYPE,
    FORMS_FORM_ENTITY_TYPE,
    FORMS_FORM_FOLDER_ENTITY_TYPE,
  ],
};

export const manifests = [
  treeRepository,
  treeStore,
  tree,
  treeItem,
  ...menuManifests,
];
