import { FormDesignScaffoldsGenerator } from "../../../form-design-scaffolds-generator.class.js";
import { FormsFormDesignElementBaseElement } from "./forms-design-element-base.element.js";
import {
  html,
  customElement,
  property,
  css,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import type { FieldSet, FieldTypeWithSettings } from "@umbraco-forms/generated";
import { FORMS_EDIT_FIELDSET_MODAL } from "@umbraco-forms/modals";
import type { UUIInputEvent } from "@umbraco-cms/backoffice/external/uui";

const elementName = "forms-form-fieldset";

@customElement(elementName)
export class FormsFormFieldsetElement extends FormsFormDesignElementBaseElement {
  @property({ type: Object })
  fieldset?: FieldSet;

  @property({ type: Number })
  pageIndex: number = 0;

  @property({ type: Array })
  fieldTypes: Array<FieldTypeWithSettings> = [];

  async #onEditClick() {
    if (!this.modalContext || !this.workspaceContext || !this.fieldset) return;

    const pages = this.workspaceContext.getAllPages();
    const pageIndex = this.pageIndex;

    const condition = this.fieldset.condition
      ? structuredClone(this.fieldset.condition)
      : FormDesignScaffoldsGenerator.getConditionScaffold();

    const containers = structuredClone(this.fieldset.containers);

    const modalHandler = this.modalContext.open(
      this,
      FORMS_EDIT_FIELDSET_MODAL,
      {
        data: {
          fields: this.allFields,
          pages: pages,
        },
        value: {
          condition,
          containers,
          pageIndex,
        },
      },
    );

    const value = await modalHandler.onSubmit().catch(() => undefined);
    if (!value) return;

    if (this.fieldset) {
      this.workspaceContext!.moveFieldSet(
        this.pageIndex,
        this.index,
        value.pageIndex,
      );
    }

    this.#setFieldsetProperty("condition", value.condition);
    this.#setFieldsetProperty("containers", value.containers);
  }

  #onDeleteClick() {
    this.structureManager?.deleteFormGroup(this.fieldset);
  }

  #onCopyClick() {
    this.structureManager?.copyFormGroup(this.fieldset);
  }

  #onCaptionChange(e: UUIInputEvent) {
    if (this.fieldset) {
      const caption = e.target.value.toString();
      this.#setFieldsetProperty("caption", caption);
    }
  }

  #setFieldsetProperty(propertyAlias: string, value: unknown) {
    this.workspaceContext!.setFieldsetProperty(
      this.pageIndex,
      this.index,
      propertyAlias,
      value,
    );
  }

  render() {
    return html`<uui-box
      ><div slot="header">
        ${when(
          !this.sortModeActive,
          () =>
            html` <uui-input
              .value=${this.fieldset?.caption}
              label="caption"
              .placeholder=${this.localize.term(
                "formEdit_groupTitlePlaceholder",
              )}
              @change=${this.#onCaptionChange}
            ></uui-input>`,
          () => html`<uui-icon name="icon-navigation"></uui-icon>
          ${
            this.fieldset?.caption && this.fieldset?.caption !== ""
              ? this.fieldset?.caption
              : this.localize.term("formEdit_groupTitlePlaceholder")
          }
        </div>`,
        )}
      </div>
      ${when(
        !this.sortModeActive,
        () =>
          html`<div slot="header-actions">
            <uui-action-bar>
              <uui-button
                label="edit"
                look="secondary"
                color="default"
                @click=${this.#onEditClick}
              >
                <uui-icon name="settings"></uui-icon>
              </uui-button>
              <uui-button
                label="copy"
                look="secondary"
                color="default"
                @click=${this.#onCopyClick}
              >
                <uui-icon name="copy"></uui-icon>
              </uui-button>
              <uui-button
                label=${this.localize.term("general_delete")}
                look="secondary"
                color="default"
                @click=${this.#onDeleteClick}
              >
                <uui-icon name="delete"></uui-icon>
              </uui-button>
            </uui-action-bar>
          </div>`,
      )}
      ${when(
        !this.sortModeActive,
        () =>
          html` <forms-form-condition-summary
            formElement="fieldset"
            .condition=${this.fieldset?.condition ?? undefined}
            .operatorTypes=${this.workspaceContext?.getConditionOperators ?? []}
            .allFields=${this.allFields}
          >
          </forms-form-condition-summary>`,
      )}

      <div id="fieldsetContainers">
        ${this.fieldset?.containers.map(
          (container, idx) =>
            html`<forms-form-fieldset-container
              .fieldset=${this.fieldset}
              .container=${container}
              .index=${idx}
              .fieldsetIndex=${this.index}
              .pageIndex=${this.pageIndex}
              .allFields=${this.allFields}
              .allFieldTypes=${this.allFieldTypes}
              .sortModeActive=${this.sortModeActive}
            ></forms-form-fieldset-container>`,
        )}
      </div>
    </uui-box>`;
  }

  static styles = [
    css`
      uui-input {
        flex: 1;
      }

      #header {
        display: flex;
        align-items: center;
        column-gap: var(--uui-size-2);
      }

      #fieldsetContainers {
        display: flex;
        column-gap: var(--uui-size-space-5);
      }

      forms-form-fieldset-container {
        box-sizing: border-box;
        display: flex;
        flex: 1;
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormFieldsetElement;
  }
}
