import { FormDesignScaffoldsGenerator } from "../../../form-design-scaffolds-generator.class.js";
import { FormsSorterConfig } from "../../../../../forms-sorter-config.js";
import { FormsFormDesignElementBaseElement } from "./forms-design-element-base.element.js";
import {
  html,
  customElement,
  property,
  css,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import type {
  FieldSet,
  FieldTypeWithSettings,
  FieldsetContainer,
} from "@umbraco-forms/generated";
import type { FormsEditFieldModalValue } from "@umbraco-forms/modals";
import { FORMS_CHOOSE_FIELD_TYPE_MODAL } from "@umbraco-forms/modals";
import { UmbSorterController } from "@umbraco-cms/backoffice/sorter";
import { GUID_EMPTY } from "@umbraco-forms/entities";

const elementName = "forms-form-fieldset-container";

@customElement(elementName)
export class FormsFormFieldsetContainerElement extends FormsFormDesignElementBaseElement {
  @property({ type: Object })
  fieldset?: FieldSet;

  @property({ type: Object })
  container?: FieldsetContainer;

  @property({ type: Number })
  pageIndex: number = 0;

  @property({ type: Number })
  fieldsetIndex: number = 0;

  @property({ type: Array })
  fieldTypes: Array<FieldTypeWithSettings> = [];

  #sortModel: Array<string> = [];
  #sorter?: UmbSorterController<string, HTMLElement>;

  sortModeEnabled() {
    this.#setSorter();
    this.#setSortModel();
  }

  #setSorter() {
    this.#sorter = new UmbSorterController(this, {
      ...new FormsSorterConfig(
        "Forms.SorterIdentifier.Field",
        `forms-form-field.container-${this.container?.id}`,
        `.fieldset-container-${this.container?.id}`,
      ).config,

      onChange: ({ model }) => {
        this.#sortModel = model;
      },
      onEnd: () => {
        this.structureManager?.reorderQuestions(
          this.fieldset!,
          this.container!,
          this.#sortModel,
        );
      },
    });
  }

  #setSortModel() {
    this.#sortModel = [];

    const fields = this.container!.fields;
    for (let i = 0; i < fields.length; i++) {
      const field = fields[i];
      this.#sortModel.push(field.id);
    }

    this.#sorter?.setModel(this.#sortModel);
  }

  #onAddQuestionClick() {
    // Open a dialog to get the new field's type.
    const selectTypeModalHandler = this.modalContext!.open(
      this,
      FORMS_CHOOSE_FIELD_TYPE_MODAL,
    );

    selectTypeModalHandler
      .onSubmit()
      .then(async (value) => {
        if (!value.selectedValue) return;
        if (!this.workspaceContext) throw new Error("No workspace context");

        // Create new field from scaffold and selected type.
        const field = FormDesignScaffoldsGenerator.getQuestionScaffold();
        field.fieldTypeId = value.selectedValue.id;

        // Open the "edit" modal to populate the details.
        const onSubmit = async (value: FormsEditFieldModalValue) => {
          field.caption = value.caption;
          field.alias = value.alias;
          field.tooltip = value.tooltip;
          field.fieldTypeId = value.fieldType.id;
          field.containsSensitiveData = value.containsSensitiveData;
          field.allowedUploadTypes = value.allowedUploadTypes;
          field.allowMultipleFileUploads = value.allowMultipleFileUploads;
          field.preValues = value.prevalues;
          field.prevalueSourceId = value.prevalueSourceId || GUID_EMPTY;
          field.mandatory = value.mandatory;
          field.requiredErrorMessage = value.requiredErrorMessage;
          field.regex = value.regex;
          field.invalidErrorMessage = value.invalidErrorMessage;
          field.settings = value.settings;
          field.condition = value.condition;

          this.structureManager?.insertQuestion(
            field,
            this.fieldset,
            this.container,
          );
        };
        await this.editNewOrExistingField(field, true, onSubmit);
      })
      .catch(() => undefined);
  }

  render() {
    return html`<div
      class="fieldset-container fieldset-container-${this.container?.id} ${this
        .container?.fields.length
        ? ""
        : "empty"}"
    >
      ${this.container?.fields.map(
        (field, idx) =>
          html`<forms-form-field
            class="container-${this.container?.id}"
            sort-unique=${field.id}
            .field=${field}
            .fieldsetId=${this.fieldset!.id}
            .pageId=${this.fieldset!.page}
            .pageIndex=${this.pageIndex}
            .fieldsetIndex=${this.fieldsetIndex}
            .containerIndex=${this.index}
            .index=${idx}
            .allFields=${this.allFields}
            .allFieldTypes=${this.allFieldTypes}
            .sortModeActive=${this.sortModeActive}
          ></forms-form-field>`,
      )}
      ${when(
        !this.sortModeActive,
        () =>
          html` <uui-button
            class="add-question-button"
            @click=${this.#onAddQuestionClick}
            look="secondary"
            label=${this.localize.term("formEdit_addQuestion")}
          ></uui-button>`,
      )}
    </div>`;
  }

  static styles = [
    css`
      uui-input,
      .fieldset-container {
        flex: 1;
      }

      .fieldset-container + .fieldset-container {
        border-left: 1px solid var(--uui-color-divider-standalone);
      }

      .add-question-button {
        margin-top: var(--add-button-margin, var(--uui-size-3));
      }

      .empty {
        --add-button-margin: 0;

        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
        border: 2px dashed var(--uui-color-divider-standalone);
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormFieldsetContainerElement;
  }
}
