import { FORMS_FORM_WORKSPACE_CONTEXT } from "../../../form-workspace.context-token.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import {
  LitElement,
  html,
  css,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import type {
  UUIBooleanInputEvent,
  UUIInputElement,
  UUIInputEvent,
  UUIRadioEvent,
  UUISelectEvent,
} from "@umbraco-cms/backoffice/external/uui";
import type { FormDesign } from "@umbraco-forms/generated";

export class FormsSettingsElementBaseElement extends UmbElementMixin(
  LitElement,
) {
  @state()
  form!: FormDesign;

  #workspaceContext!: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_WORKSPACE_CONTEXT, (instance) => {
      if (!instance) return;
      this.#workspaceContext = instance;
      this.#observeForm();
    });
  }

  #observeForm() {
    this.observe(this.#workspaceContext.data, (instance) => {
      if (!instance) return;
      this.form = instance;
      this.onObserveForm();
    });
  }

  protected onObserveForm() {}

  getAllFieldsForForm() {
    return this.#workspaceContext?.getAllFields();
  }

  onInputChange(e: UUIInputEvent, alias: string) {
    const target = e.composedPath()[0] as UUIInputElement;
    this.setPropertyValue(alias, target.value.toString());
  }

  #onToggleChange(e: UUIBooleanInputEvent, alias: string) {
    this.setPropertyValue(alias, e.target.checked);
  }

  onRadioChange(e: UUIRadioEvent, alias: string) {
    this.setPropertyValue(alias, e.target.value);
  }

  onSelectChange(e: UUISelectEvent, alias: string) {
    this.setPropertyValue(alias, e.target.value);
  }

  setPropertyValue(alias: string, value: unknown) {
    this.#workspaceContext?.setFormProperty(alias, value);
    this.dispatchEvent(new CustomEvent("valueChange"));
  }

  renderToggle(value: boolean, alias: string, labelKey: string) {
    const label = this.localize.term(labelKey);

    return html`<uui-toggle
      ?checked=${value}
      .label=${label}
      @change=${(e) => this.#onToggleChange(e, alias)}
    ></uui-toggle>`;
  }

  static styles = [
    css`
      .flex {
        display: flex;
        flex-direction: column;
      }

      .flex + .flex {
        margin-top: var(--uui-size-3);
      }

      .flex.gap {
        margin-top: var(--uui-size-5);
      }

      .flex + uui-toggle {
        display: block;
        margin-top: var(--uui-size-3);
      }
    `,
  ];
}
