import {
  ConditionAppliedTo,
  FORMS_CHOOSE_FIELD_TYPE_MODAL,
  type FormsEditAllowedFileUploadTypesElement,
  type FormsEditConditionsElement,
  type FormsEditFieldModalData,
  type FormsEditFieldModalValue,
} from "../index.js";
import { ProviderTypeSettingsHelper } from "../../section/provider-types/provider-type-settings-helper.class.js";
import type { FormsEditPrevaluesElement } from "./form-edit-prevalues.element.js";
import {
  css,
  customElement,
  html,
  state,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import {
  UMB_MODAL_MANAGER_CONTEXT,
  UmbModalBaseElement,
} from "@umbraco-cms/backoffice/modal";
import type {
  UUIBooleanInputEvent,
  UUIInputEvent,
  UUISelectEvent,
} from "@umbraco-cms/backoffice/external/uui";
import type {
  UmbPropertyDatasetElement,
  UmbPropertyValueData,
} from "@umbraco-cms/backoffice/property";
import { generateAlias } from "@umbraco-cms/backoffice/utils";
import type { UmbPropertyEditorConfig } from "@umbraco-cms/backoffice/property-editor";
import { tryExecute } from "@umbraco-cms/backoffice/resources";
import { FormService } from "@umbraco-forms/generated";
import { UMB_NOTIFICATION_CONTEXT } from "@umbraco-cms/backoffice/notification";
import { firstValueFrom } from "@umbraco-cms/backoffice/external/rxjs";
import type { UmbCurrentUserModel } from "@umbraco-cms/backoffice/current-user";
import { UMB_CURRENT_USER_CONTEXT } from "@umbraco-cms/backoffice/current-user";
import { GUID_EMPTY } from "@umbraco-forms/entities";

const elementName = "form-edit-field-modal";

export type ContainersSelectOptions = {
  id: string;
  name: string;
  selected: boolean;
};

@customElement(elementName)
export class FormsEditFieldModalElement extends UmbModalBaseElement<
  FormsEditFieldModalData,
  FormsEditFieldModalValue
> {
  #providerTypeSettingsHelper?: ProviderTypeSettingsHelper;

  @state()
  _aliasLocked = true;

  @state()
  _settingValues: Array<UmbPropertyValueData> = [];

  @state()
  _settingsConfig: Array<{ alias: string; value: UmbPropertyEditorConfig }> =
    [];

  @state()
  _settingsConfigLoaded: boolean = false;

  @state()
  _showRegex = false;

  @state()
  _showRegexReadonly = false;

  @state()
  _conditionEnabled = false;

  #currentUser?: UmbCurrentUserModel;

  async connectedCallback() {
    super.connectedCallback();

    const currentUserContext = await this.getContext(UMB_CURRENT_USER_CONTEXT);
    if (!currentUserContext) throw new Error("Current user context not found");

    this.#currentUser = await firstValueFrom(currentUserContext.currentUser);

    this.#providerTypeSettingsHelper = new ProviderTypeSettingsHelper(
      this,
      this.value!.fieldType,
      "formProviderFieldTypes",
    );
    await this.#providerTypeSettingsHelper.loadSettingValueConverterApis();
    await this.#initializeSettingsForEditor();

    this._settingsConfig =
      await this.#providerTypeSettingsHelper.getSettingsConfig(
        this._settingValues,
        this.value!.fieldType.settings,
      );
    this._settingsConfigLoaded = true;

    if (this.data?.isNew && this.value.fieldType?.mandatoryByDefault) {
      this.modalContext?.updateValue({ mandatory: true });
    }
    this._conditionEnabled = this.value?.condition?.enabled ?? false;
    this.#refreshRegexDisplay();
  }

  async #initializeSettingsForEditor() {
    // ensure all settings from the workflow type exist on the value
    // when the modal is submitted, any settings with the default value
    // are ignored and not persisted.
    const settings = { ...this.value.settings };
    this.value?.fieldType.settings.forEach((setting) => {
      if (settings[setting.alias]) return;

      settings[setting.alias] = setting.defaultValue;
    });

    this._settingValues =
      await this.#providerTypeSettingsHelper!.getSettingValues(settings);
  }

  #onCaptionChange(e: UUIInputEvent) {
    const caption = e.target.value.toString();

    const oldCaption = this.value.caption;
    const oldAlias = this.value.alias;
    if (this._aliasLocked) {
      const expectedOldAlias = generateAlias(oldCaption ?? "");

      // Only update the alias if the alias matches a generated alias of the old name (otherwise the alias is considered one written by the user.)
      if (expectedOldAlias === oldAlias) {
        this.modalContext?.updateValue({ alias: generateAlias(caption) });
      }
    }

    this.modalContext?.updateValue({ caption });
  }

  #onToggleAliasLock() {
    this._aliasLocked = !this._aliasLocked;
  }

  #onAliasChange(e: UUIInputEvent) {
    const alias = e.target.value.toString();

    this.modalContext?.updateValue({ alias });
  }

  #onTooltipChange(e: UUIInputEvent) {
    const tooltip = e.target.value.toString();

    this.modalContext?.updateValue({ tooltip });
  }

  async #changeFieldType() {
    const modalManager = await this.getContext(UMB_MODAL_MANAGER_CONTEXT);
    if (!modalManager) throw new Error("Modal manager not found");

    const selectTypeModalHandler = modalManager.open(
      this,
      FORMS_CHOOSE_FIELD_TYPE_MODAL,
    );

    selectTypeModalHandler
      .onSubmit()
      .then(async (value) => {
        if (!value.selectedValue) return;

        this.modalContext?.updateValue({ fieldType: value.selectedValue });
        this.#providerTypeSettingsHelper?.setProviderType(value.selectedValue);
        this.requestUpdate();
      })
      .catch(() => undefined);
  }

  #getContainersSelectOptions() {
    const options: Array<ContainersSelectOptions> = [];
    if (!this.data) return options;

    for (let p = 0; p < this.data.pages.length; p++) {
      const page = this.data.pages[p];
      for (let fs = 0; fs < page.fieldSets.length; fs++) {
        const fieldset = page.fieldSets[fs];
        for (let c = 0; c < fieldset.containers.length; c++) {
          const container = fieldset.containers[c];
          const containerIndexPath = p + "_" + fs + "_" + c;
          options.push({
            id: containerIndexPath,
            name:
              (page.caption || "Page " + (p + 1)) +
              " > " +
              (fieldset.caption || "Group " + (fs + 1)) +
              " > " +
              (container.caption || "Container " + (c + 1)),
            selected: this.value.containerIndexPath === containerIndexPath,
          });
        }
      }
    }

    return options;
  }

  #onContainerChange(e: UUISelectEvent) {
    const containerIndexPath = e.target.value.toString();
    this.modalContext?.updateValue({ containerIndexPath });
  }

  async #onSettingsChange(e: CustomEvent) {
    const value = (e.target as UmbPropertyDatasetElement).value;
    this._settingValues = value;
  }

  #onAllowedUploadTypesChange(
    e: CustomEvent & { target: FormsEditAllowedFileUploadTypesElement },
  ) {
    const allowedUploadTypes = e.target.value;
    this.modalContext?.updateValue({ allowedUploadTypes });
  }

  #onContainsSensitiveDataChange(e: UUIBooleanInputEvent) {
    const containsSensitiveData = e.target.checked;
    this.modalContext?.updateValue({ containsSensitiveData });
  }

  #onAllowMultipleFileUploadsChange(e: UUIBooleanInputEvent) {
    const allowMultipleFileUploads = e.target.checked;
    this.modalContext?.updateValue({ allowMultipleFileUploads });
  }

  #onPrevaluesChange(e: CustomEvent) {
    const prevalues = (e.target as FormsEditPrevaluesElement).value;
    this.modalContext?.updateValue({ prevalues });
  }

  #getPrevalueSourceOptions() {
    const options =
      this.data?.prevalueSources.map((pvs) => ({
        name: pvs.name,
        value: pvs.id,
        selected: pvs.id === this.value.prevalueSourceId,
      })) ?? [];
    options.unshift({
      name: "---" + this.localize.term("general_choose") + "---",
      value: GUID_EMPTY,
      selected: this.value.prevalueSourceId === GUID_EMPTY,
    });
    return options;
  }

  #onPrevalueSourceChange(e: UUISelectEvent) {
    const prevalueSourceId = e.target.value.toString();
    this.modalContext?.updateValue({ prevalueSourceId });
  }

  #onMandatoryChange(e: UUIBooleanInputEvent) {
    const mandatory = e.target.checked;
    this.modalContext?.updateValue({ mandatory });
  }

  #onRequiredErrorMessageChange(e: UUIInputEvent) {
    const requiredErrorMessage = e.target.value.toString();
    this.modalContext?.updateValue({ requiredErrorMessage });
  }

  #onRegexSelectChange(e: UUISelectEvent) {
    const regex = e.target.value.toString();
    this.modalContext?.updateValue({ regex });
    this.#refreshRegexDisplay();
  }

  #isCustomRegex() {
    if (!this.value.regex) {
      return false;
    }

    const matchedPattern = this.#getProvidedRegexPattern(this.value.regex!);
    return matchedPattern === undefined;
  }

  #onRegexEntryChange(e: CustomEvent) {
    const textElement = e.target as HTMLTextAreaElement;
    const regex = textElement.value.trim();
    this.modalContext?.updateValue({ regex: regex.length > 0 ? regex : null });
  }

  #refreshRegexDisplay() {
    if (this.value?.regex?.length) {
      const matchedPattern = this.#getProvidedRegexPattern(this.value.regex!);
      this._showRegex = true;
      this._showRegexReadonly = matchedPattern !== undefined;
    } else {
      this._showRegex = false;
      this._showRegexReadonly = false;
    }
  }

  #getProvidedRegexPattern(regex: string) {
    return this.data?.validationPatterns.find((p) => p.pattern === regex);
  }

  #onInvalidErrorMessageChange(e: UUIInputEvent) {
    const invalidErrorMessage = e.target.value.toString();
    this.modalContext?.updateValue({ invalidErrorMessage });
  }

  #onConditionEnabledChange(e: UUIBooleanInputEvent) {
    this._conditionEnabled = e.target.checked;
    const condition = structuredClone(this.value.condition);
    condition.enabled = this._conditionEnabled;
    this.modalContext?.updateValue({ condition });
  }

  #onConditionChange(e: CustomEvent) {
    const condition =
      (e.target as FormsEditConditionsElement).value ?? undefined;
    this.modalContext?.updateValue({ condition });
  }

  async #submitModal() {
    const settings =
      await this.#providerTypeSettingsHelper!.getUpdatedSettingsForPersistence(
        this._settingValues,
        this.value!.fieldType.settings,
      );
    const validateRequest = {
      path: {
        id: this.value!.fieldType.id,
      },
      body: {
        caption: this.value!.caption,
        alias: this.value!.alias,
        settings,
        allowedUploadTypes: this.value!.allowedUploadTypes,
      },
    };

    const { error } = await tryExecute(
      this,
      FormService.postFormFieldByIdValidateSettings(validateRequest),
      {
        disableNotifications: true,
      },
    );

    if (error) {
      const notification =
        this.#providerTypeSettingsHelper!.createValidationErrorNotification(
          "formEdit_saveFieldFailedTitle",
          error,
        );
      const notificationContext = await this.getContext(
        UMB_NOTIFICATION_CONTEXT,
      );

      notificationContext?.peek("danger", notification);
    } else {
      this.modalContext?.updateValue({ settings });

      this._submitModal();
    }
  }

  render() {
    return html`<umb-body-layout
      headline=${this.localize.term("formEdit_editField")}
    >
      <div id="main">
        <uui-box>
          <uui-input
            id="caption"
            .value=${this.value?.caption}
            @change=${this.#onCaptionChange}
            label="caption"
          >
            <uui-input
              name="alias"
              slot="append"
              label="alias"
              .value=${this.value?.alias}
              @change=${this.#onAliasChange}
              placeholder="Enter alias..."
              ?disabled=${this._aliasLocked}
            >
              <!-- TODO: validation for bad characters -->
              <div
                @click=${this.#onToggleAliasLock}
                @keydown=${() => ""}
                id="alias-lock"
                slot="prepend"
              >
                <uui-icon
                  name=${this._aliasLocked ? "icon-lock" : "icon-unlocked"}
                ></uui-icon>
              </div> </uui-input
          ></uui-input>

          <umb-ref-property-editor-ui
            standalone
            .name=${this.value?.fieldType.name}
            .alias=${this.value?.fieldType.alias}
            .propertyEditorSchemaAlias=${this.value?.fieldType.description}
          >
            <umb-icon name=${this.value?.fieldType.icon} slot="icon"></umb-icon>
            <uui-action-bar slot="actions">
              <uui-button
                label="Change"
                @click=${this.#changeFieldType}
              ></uui-button>
            </uui-action-bar>
          </umb-ref-property-editor-ui>

          <umb-property-layout
            alias="tooltip"
            label=${this.localize.term("formEdit_helpText")}
          >
            <uui-textarea
              slot="editor"
              name="tooltip"
              .value=${this.value?.tooltip}
              @change=${this.#onTooltipChange}
              label="tooltip"
            ></uui-textarea>
          </umb-property-layout>

          ${when(
            this.data && !this.data.isNew,
            () =>
              html`<umb-property-layout alias="group" label="Group">
                <uui-select
                  label="Group"
                  slot="editor"
                  @change=${this.#onContainerChange}
                  .options=${this.#getContainersSelectOptions().map((o) => ({
                    name: o.name,
                    value: o.id,
                    selected: o.selected,
                  })) ?? []}
                >
                </uui-select>
              </umb-property-layout>`,
          )}

          <umb-property-layout
            alias="fieldSettings_sensitiveData"
            .label=${this.localize.term("fieldSettings_sensitiveData")}
          >
            <!--
            // Verify that the current user is allowed to view & change the property 'containsSensitiveData'.
            // We allow a user that doesn't have the permission to set the value to true or false, but if
            // they or anyone else has previously set it to true, they aren't allowed to see or edit it.
            // See: https://github.com/umbraco/Umbraco.Forms.Issues/issues/1233
            -->
            ${this.#currentUser?.hasAccessToSensitiveData ||
            this.value?.containsSensitiveData === false
              ? html` <uui-toggle
                  slot="editor"
                  ?checked=${this.value?.containsSensitiveData}
                  .label=${this.localize.term(
                    "fieldSettings_sensitiveDataLabel",
                  )}
                  @change=${this.#onContainsSensitiveDataChange}
                ></uui-toggle>`
              : html` <div slot="editor">
                  ${this.localize.term("fieldSettings_sensitiveDataLabel")}
                </div>`}
          </umb-property-layout>

          ${when(
            this.value?.fieldType.supportsUploadTypes,
            () =>
              html`<umb-property-layout
                  alias="allowedUploadTypes"
                  .label=${this.localize.term(
                    "fieldSettings_allowedFileUploadTypes",
                  )}
                >
                  <form-edit-allowed-file-upload-types
                    slot="editor"
                    .value=${this.value.allowedUploadTypes ?? []}
                    @change=${this.#onAllowedUploadTypesChange}
                  ></form-edit-allowed-file-upload-types>
                </umb-property-layout>
                <umb-property-layout
                  alias="allowMultipleFileUploads"
                  .label=${this.localize.term(
                    "fieldSettings_allowMultipleFileUploads",
                  )}
                >
                  <uui-toggle
                    slot="editor"
                    ?checked=${this.value?.allowMultipleFileUploads}
                    label=${this.value?.allowedUploadTypes
                      ? "Multiple files"
                      : "Single file only"}
                    @change=${this.#onAllowMultipleFileUploadsChange}
                  ></uui-toggle>
                </umb-property-layout>`,
          )}
          ${when(
            this.value?.fieldType.supportsPrevalues,
            () =>
              html`<umb-property-layout
                alias="prevalues"
                .label=${this.localize.term("fieldSettings_prevalues")}
                .description=${this.localize.term(
                  (this.data?.prevalueSources?.length ?? 0) > 0
                    ? "fieldSettings_prevaluesProvideWithSources"
                    : "fieldSettings_prevaluesProvide",
                )}
              >
                <div slot="editor">
                  ${when(
                    !this.value.prevalueSourceId ||
                      this.value.prevalueSourceId === GUID_EMPTY,
                    () =>
                      html`<form-edit-prevalues
                        .value=${this.value.prevalues}
                        @change=${this.#onPrevaluesChange}
                      ></form-edit-prevalues>`,
                  )}
                  ${when(
                    (this.data?.prevalueSources?.length ?? 0) > 0,
                    () =>
                      html`<div id="prevalueSource">
                        <label for="prevalueSource"
                          >${this.localize.term(
                            "fieldSettings_prevaluesSource",
                          )}</label
                        >
                        <uui-select
                          label="Prevalue Source"
                          name="prevalueSource"
                          @change=${this.#onPrevalueSourceChange}
                          .options=${this.#getPrevalueSourceOptions()}
                        >
                        </uui-select>
                      </div>`,
                  )}
                </div>
              </umb-property-layout>`,
          )}
          ${when(
            this.#providerTypeSettingsHelper &&
              this.value &&
              this._settingsConfigLoaded,
            () =>
              html` <umb-property-dataset
                .value=${this._settingValues}
                @change=${this.#onSettingsChange}
              >
                ${this.value!.fieldType.settings.map(
                  (setting) => html`
                    <umb-property
                      ?inert=${setting.isReadOnly}
                      .label=${this.#providerTypeSettingsHelper!.getLocalizedSettingDetail(
                        setting,
                        "Label",
                        setting.name,
                      )}
                      .description=${this.#providerTypeSettingsHelper!.getLocalizedSettingDetail(
                        setting,
                        "Description",
                        setting.description,
                      )}
                      alias=${setting.alias}
                      .config=${this.#providerTypeSettingsHelper!.getPropertyConfigForSetting(
                        this._settingsConfig,
                        setting,
                      )}
                      .appearance=${this.#providerTypeSettingsHelper!.getPropertyAppearanceForSetting(
                        setting.view,
                      )}
                      property-editor-ui-alias=${setting.view}
                    >
                    </umb-property>
                  `,
                )}
              </umb-property-dataset>`,
          )}
          ${when(
            this.value?.fieldType.supportsMandatory,
            () =>
              html`<umb-property-layout
                alias="mandatory"
                .label=${this.localize.term("fieldSettings_mandatory")}
              >
                <div slot="editor">
                  <uui-toggle
                    ?checked=${this.value?.mandatory}
                    .label=${this.value?.mandatory ? "On" : "Off"}
                    @change=${this.#onMandatoryChange}
                  ></uui-toggle>
                  ${when(
                    this.value?.mandatory,
                    () =>
                      html`<uui-input
                        id="requiredErrorMessage"
                        name="requiredErrorMessage"
                        .value=${this.value?.requiredErrorMessage}
                        @change=${this.#onRequiredErrorMessageChange}
                        label="Error message"
                        placeholder=${this.localize.term(
                          "validation_mandatoryMessage",
                        )}
                      ></uui-input>`,
                  )}
                </div>
              </umb-property-layout>`,
          )}
          <!-- TODO => make this a component-->
          ${when(
            this.value?.fieldType.supportsRegex,
            () =>
              html`<umb-property-layout
                alias="regex"
                .label=${this.localize.term("formSettings_validation")}
              >
                <div slot="editor">
                  <uui-select
                    name="regex"
                    @change=${this.#onRegexSelectChange}
                    .options=${[
                      {
                        name: "",
                        value: "",
                        selected: !this.value.regex,
                      },
                      ...(this.data?.validationPatterns.map((vp) => ({
                        name:
                          vp.labelKey.length > 0
                            ? this.localize.term(vp.labelKey)
                            : vp.name,
                        value: vp.pattern,
                        selected: vp.pattern === this.value.regex,
                      })) ?? []),
                      {
                        name: this.localize.term(
                          "validation_enterCustomValidation",
                        ),
                        value: ".*",
                        selected: this.#isCustomRegex(),
                      },
                    ]}
                  >
                  </uui-select>

                  ${when(
                    this._showRegex,
                    () => html`
                      <textarea
                        placeholder=${this.localize.term(
                          "fieldSettings_enterRegex",
                        )}
                        ?disabled=${this._showRegexReadonly}
                        @change=${this.#onRegexEntryChange}
                      >
${this.value?.regex}</textarea
                      >
                    `,
                  )}
                  ${when(
                    (this.value?.regex?.length ?? 0) > 0,
                    () => html`
                      <uui-input
                        id="invalidErrorMessage"
                        name="invalidErrorMessage"
                        .value=${this.value?.invalidErrorMessage}
                        @change=${this.#onInvalidErrorMessageChange}
                        label="Error message"
                        placeholder=${this.localize.term(
                          "validation_mandatoryMessage",
                        )}
                      ></uui-input>
                    `,
                  )}
                </div>
              </umb-property-layout>`,
          )}

          <umb-property-layout
            alias="conditions"
            .label=${this.localize.term("formConditions_title")}
          >
            <div slot="editor">
              <uui-toggle
                ?checked=${this._conditionEnabled}
                .label=${this._conditionEnabled ? "On" : "Off"}
                @change=${this.#onConditionEnabledChange}
              ></uui-toggle>
              ${when(
                this._conditionEnabled,
                () =>
                  html`<form-edit-conditions
                    .value=${this.value.condition}
                    .fields=${this.data?.fields ?? []}
                    .appliedTo=${ConditionAppliedTo.FIELD}
                    @change=${this.#onConditionChange}
                  ></form-edit-conditions>`,
              )}
            </div>
          </umb-property-layout>
        </uui-box>
      </div>
      <div slot="actions">
        <uui-button
          label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
        <uui-button
          color="positive"
          look="primary"
          label=${this.localize.term("general_submit")}
          @click=${this.#submitModal}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }

  static styles = [
    css`
      #caption {
        display: flex;
        flex: 1 1 auto;
        margin-bottom: var(--uui-size-6);
      }

      uui-toggle {
        display: block;
      }

      textarea {
        box-sizing: border-box;
        resize: vertical;
        width: 100%;
      }

      textarea + *,
      uui-select + *,
      uui-toggle + * {
        width: 100%;
        margin-top: var(--uui-size-3);
      }

      uui-toggle + form-edit-conditions {
        display: block;
      }

      #prevalueSource {
        margin-top: var(--uui-size-5);
      }

      [inert] {
        opacity: 0.5;
      }
    `,
  ];
}

export default FormsEditFieldModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditFieldModalElement;
  }
}
