import { FORMS_CONTEXT } from "../../context/forms.context-token.js";
import {
  customElement,
  html,
  property,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import type { AllowedUploadType } from "@umbraco-forms/generated";
import { UMB_NOTIFICATION_CONTEXT } from "@umbraco-cms/backoffice/notification";
import type { UUIInputElement } from "@umbraco-cms/backoffice/external/uui";

const elementName = "form-edit-allowed-file-upload-types";

@customElement(elementName)
export class FormsEditAllowedFileUploadTypesElement extends UmbLitElement {
  @property()
  set value(value: Array<AllowedUploadType>) {
    this._value = structuredClone(value);
    this.#populateValueWithDefaultAllowedUploadTypes();
  }
  get value(): Array<AllowedUploadType> {
    return this._value;
  }

  #notificationContext?: typeof UMB_NOTIFICATION_CONTEXT.TYPE;

  #defaultAllowedUploadTypes: Array<AllowedUploadType> = [
    {
      name: "Allow all files",
      type: "",
      checked: "false",
    },
    {
      name: "PDF",
      type: "pdf",
      checked: "false",
    },
    {
      name: "DOCX",
      type: "docx",
      checked: "false",
    },
    {
      name: "XLSX",
      type: "xlsx",
      checked: "false",
    },
    {
      name: "TXT",
      type: "txt",
      checked: "false",
    },
    {
      name: "PNG",
      type: "png",
      checked: "false",
    },
    {
      name: "JPG",
      type: "jpg",
      checked: "false",
    },
    {
      name: "GIF",
      type: "gif",
      checked: "false",
    },
  ];

  #disallowedFileUploadExtensions: Array<string> = [];
  #allowedFileUploadExtensions: Array<string> = [];

  private _value: Array<AllowedUploadType> = structuredClone(
    this.#defaultAllowedUploadTypes,
  );

  constructor() {
    super();

    this.consumeContext(UMB_NOTIFICATION_CONTEXT, (instance) => {
      this.#notificationContext = instance;
    });

    this.consumeContext(FORMS_CONTEXT, (instance) => {
      if (!instance) return;
      this.observe(instance.config, (config) => {
        if (config) {
          this.#disallowedFileUploadExtensions =
            config?.disallowedFileUploadExtensions.split(",").filter(Boolean);
          this.#allowedFileUploadExtensions =
            config?.allowedFileUploadExtensions.split(",").filter(Boolean);
        }
      });
    });
  }

  #populateValueWithDefaultAllowedUploadTypes() {
    let triggerUpdate: boolean = false;
    if (this._value.length === 0) {
      triggerUpdate = true;
    }

    const defaultAllowedUploadTypes = this.#defaultAllowedUploadTypes.filter(
      (ft) => this.#isExtensionAllowed(ft.type),
    );

    // Add any default and permitted allowed types that aren't in the current value.
    defaultAllowedUploadTypes.forEach((dt) => {
      if (!this._value.find((t) => t.type === dt.type)) {
        this._value.push({
          name: dt.name,
          type: dt.type,
          checked: "false",
        });
      }
    });

    // If extension that was previously allowed has been removed in configuration, remove from the value.
    if (
      this._value.filter((ft) => this.#isExtensionAllowed(ft.type)).length > 0
    ) {
      this._value = this._value.filter((ft) =>
        this.#isExtensionAllowed(ft.type),
      );
    }

    if (triggerUpdate) {
      this.#dispatchChangeEvent();
    }
  }

  #isExtensionAllowed(extension: string) {
    if (extension === "") {
      return true;
    }

    if (this.#disallowedFileUploadExtensions.includes(extension)) {
      return false;
    }

    if (
      this.#allowedFileUploadExtensions.length > 0 &&
      this.#allowedFileUploadExtensions.includes(extension) === false
    ) {
      return false;
    }

    return true;
  }

  #areaAllFileTypesAllowed() {
    return this.value[0].checked === "true";
  }

  #onAllowedFileUploadTypeToggle(index: number) {
    const currentChecked = this._value[index].checked;
    this._value[index].checked = currentChecked === "true" ? "false" : "true";

    this.#dispatchChangeEvent();
  }

  #onUserDefinedAllowedFileUploadTypeAddClick() {
    const addNewTypeElement = this.shadowRoot?.getElementById(
      "addNew",
    ) as UUIInputElement;
    const type = addNewTypeElement.value
      .toString()
      .replace(/[^a-zA-Z0-9]/g, "")
      .toLowerCase();
    addNewTypeElement.value = "";
    if (type.length === 0) {
      return;
    }

    // Check extension is allowed.
    if (!this.#isExtensionAllowed(type)) {
      this.#notificationContext?.peek("danger", {
        data: {
          headline: this.localize.term(
            "formFileUpload_disallowedFileExtensionErrorTitle",
          ),
          message: this.localize.term(
            "formFileUpload_disallowedFileExtensionErrorMessage",
          ),
        },
      });
      return;
    }

    // Check for duplicates and allowed extensions.
    const indexOfExisting = this._value.findIndex(function (t) {
      return t.type.toLowerCase() === type;
    });
    if (indexOfExisting >= 0) {
      this.#notificationContext?.peek("danger", {
        data: {
          headline: this.localize.term(
            "formFileUpload_duplicateFileTypeErrorTitle",
          ),
          message: this.localize.term(
            "formFileUpload_duplicateFileTypeErrorMessage",
          ),
        },
      });
      return;
    }

    this._value.push({
      type,
      name: type.toUpperCase(),
      checked: "",
    });

    this.#dispatchChangeEvent();
  }

  #onUserDefinedAllowedFileUploadTypeDeleteClick(index: number) {
    const numberOfDefaultAllowedTypes = this.value.filter(
      (t) => t.checked.length > 0,
    ).length;
    this._value.splice(index + numberOfDefaultAllowedTypes, 1);
    this.#dispatchChangeEvent();
  }

  #dispatchChangeEvent() {
    this.requestUpdate();
    this.dispatchEvent(
      new CustomEvent("change", { composed: true, bubbles: true }),
    );
  }

  render() {
    return html`<div>
      ${this.value
        .filter((t) => t.checked.length > 0)
        .map(
          (t, idx) =>
            html`<div>
              <uui-toggle
                ?checked=${t.checked === "true"}
                ?disabled=${idx > 0 && this.#areaAllFileTypesAllowed()}
                .label=${t.name}
                @change=${() => this.#onAllowedFileUploadTypeToggle(idx)}
              ></uui-toggle>
            </div>`,
        )}
      <div>
        <b
          >${this.localize.term(
            "formFileUpload_userDefinedAllowedFileTypes",
          )}</b
        >
      </div>
      <div>
        ${this.value
          .filter((t) => t.checked.length === 0)
          .map(
            (t, idx) =>
              html`<div>
                <span>${t.name}</span>
                <uui-button
                  label=${this.localize.term("general_delete")}
                  look="secondary"
                  color="default"
                  @click=${() =>
                    this.#onUserDefinedAllowedFileUploadTypeDeleteClick(idx)}
                >
                  <uui-icon name="icon-delete"></uui-icon>
                </uui-button>
              </div>`,
          )}
      </div>
      <form>
        <uui-input
          id="addNew"
          type="text"
          size="30"
          .placeholder=${this.localize.term(
            "formFileUpload_addAllowedFileType",
          )}
        ></uui-input>
        <uui-button
          label=${this.localize.term("general_add")}
          look="secondary"
          color="default"
          .disabled="${this.#areaAllFileTypesAllowed()}"
          @click=${() => this.#onUserDefinedAllowedFileUploadTypeAddClick()}
        >
          <uui-icon name="icon-add"></uui-icon>
        </uui-button>
      </form>
    </div>`;
  }
}

export default FormsEditAllowedFileUploadTypesElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditAllowedFileUploadTypesElement;
  }
}
