import type {
  FormEntryCollectionFilterModel,
  FormEntryCollectionModel,
} from "../types.js";
import { FORMS_COLLECTION_VIEW_ENTRY_TABLE } from "./views/index.js";
import { UmbDefaultCollectionContext } from "@umbraco-cms/backoffice/collection";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";

export class FormsEntryCollectionContext extends UmbDefaultCollectionContext<
  FormEntryCollectionModel,
  FormEntryCollectionFilterModel
> {
  constructor(host: UmbControllerHost) {
    super(host, FORMS_COLLECTION_VIEW_ENTRY_TABLE);
  }

  oneMonthAgo() {
    const today = new Date();
    const oneMonthAgo = new Date(today.setMonth(today.getMonth() - 1));
    return this.#formatDate(oneMonthAgo);
  }

  today() {
    return this.#formatDate(new Date());
  }

  #formatDate(date: Date) {
    const dd = String(date.getDate()).padStart(2, "0");
    const mm = String(date.getMonth() + 1).padStart(2, "0");
    const yyyy = date.getFullYear();

    return yyyy + "-" + mm + "-" + dd;
  }
}

export { FormsEntryCollectionContext as api };
