import type { FormsSecurityPermissionConditionConfig } from "./security-permission-condition.config.js";
import type {
  UmbConditionControllerArguments,
  UmbExtensionCondition,
} from "@umbraco-cms/backoffice/extension-api";
import { UmbConditionBase } from "@umbraco-cms/backoffice/extension-registry";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import { FORMS_CONTEXT } from "@umbraco-forms/context";

export class FormsSecurityPermissionCondition
  extends UmbConditionBase<FormsSecurityPermissionConditionConfig>
  implements UmbExtensionCondition
{
  constructor(
    host: UmbControllerHost,
    args: UmbConditionControllerArguments<FormsSecurityPermissionConditionConfig>,
  ) {
    super(host, args);

    this.consumeContext(FORMS_CONTEXT, (context) => {
      this.observe(context?.userSecurity, (userSecurity) => {
        if (!userSecurity) return;

        this.permitted = this.config.match(userSecurity);
      });
    });
  }
}

export default FormsSecurityPermissionCondition;
