import { FORMS_FORM_WORKSPACE_CONTEXT } from "../../section/forms/workspace/form-workspace.context-token.js";
import {
  css,
  customElement,
  html,
  property,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import {
  FieldConditionActionType,
  FieldConditionLogicType,
  FieldConditionRuleOperator,
} from "@umbraco-forms/generated";
import type {
  Field,
  FieldConditionRule,
  FieldCondition,
} from "@umbraco-forms/generated";

import { UmbId } from "@umbraco-cms/backoffice/id";
import type {
  UUIInputEvent,
  UUISelectEvent,
} from "@umbraco-cms/backoffice/external/uui";
import { GUID_EMPTY } from "@umbraco-forms/entities";

const elementName = "form-edit-conditions";

export enum ConditionAppliedTo {
  FIELD,
  FIELDSET,
  PAGE,
  WORKFLOW,
}

@customElement(elementName)
export class FormsEditConditionsElement extends UmbLitElement {
  #workspaceContext?: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;

  #value: FieldCondition = {
    id: "",
    enabled: false,
    actionType: FieldConditionActionType.SHOW,
    logicType: FieldConditionLogicType.ALL,
    rules: [],
  };

  @property({ type: Object })
  set value(value: FieldCondition | null | undefined) {
    this.#value = value ? structuredClone(value) : this.#value;
    for (let i = 0; i < this.#value.rules.length; i++) {
      this.#value.rules[i] = structuredClone(this.#value.rules[i]);
    }
  }

  get value(): FieldCondition | null | undefined {
    return this.#value;
  }

  @property({ type: Array })
  fields: Array<Field> = [];

  @property({ type: ConditionAppliedTo })
  appliedTo: ConditionAppliedTo = ConditionAppliedTo.FIELD;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_WORKSPACE_CONTEXT, (instance) => {
      this.#workspaceContext = instance;
    });
  }

  #getPrevaluesForField(fieldId: string) {
    const field = this.fields.find((f) => f.id === fieldId);
    return field?.preValues.map((pv) => pv.value) ?? [];
  }

  #onActionTypeChange(e: UUISelectEvent) {
    this.#value.actionType = e.target.value as FieldConditionActionType;
    this.#dispatchChangeEvent();
  }

  #onLogicTypeChange(e: UUISelectEvent) {
    this.#value.logicType = e.target.value as FieldConditionLogicType;
    this.#dispatchChangeEvent();
  }

  #onAddRuleClick() {
    this.#value.rules.push({
      id: UmbId.new(),
      field: GUID_EMPTY,
      operator: FieldConditionRuleOperator.IS,
      value: "",
    });
    this.#dispatchChangeEvent();
  }

  #onRuleFieldChange(e: UUISelectEvent, index: number) {
    this.#value.rules[index].field = e.target.value.toString();
    this.#dispatchChangeEvent();
  }

  #onRuleOperatorChange(e: UUISelectEvent, index: number) {
    this.#value.rules[index].operator = e.target
      .value as FieldConditionRuleOperator;
    this.#dispatchChangeEvent();
  }

  #onRuleValueChange(value: string, index: number) {
    this.#value.rules[index].value = value;
    this.#dispatchChangeEvent();
  }

  #onDeleteRuleClick(index: number) {
    this.#value.rules.splice(index, 1);
    this.#dispatchChangeEvent();
  }

  #dispatchChangeEvent() {
    this.requestUpdate();
    this.dispatchEvent(
      new CustomEvent("change", { composed: true, bubbles: true }),
    );
  }

  #getAppliedToLabel() {
    switch (this.appliedTo) {
      case ConditionAppliedTo.FIELDSET:
        return "thisFieldSetIf";
      case ConditionAppliedTo.PAGE:
        return "buttonsForThisPageIf";
      case ConditionAppliedTo.WORKFLOW:
        return "thisWorkflowIf";
      default:
        return "thisFieldIf";
    }
  }

  #renderRule(rule: FieldConditionRule, ruleIndex: number) {
    const prevaluesForField = this.#getPrevaluesForField(rule.field);

    return html`<div class="flex center condition-rule">
      <div class="grow">
        <uui-select
          name="field"
          .placeholder=${this.localize.term("formConditions_selectField")}
          @change=${(e) => this.#onRuleFieldChange(e, ruleIndex)}
          .options=${this.fields.map((f) => ({
            name: f.caption,
            value: f.id,
            selected: f.id === rule.field,
          }))}
        >
        </uui-select>
      </div>

      <div class="grow">
        <uui-select
          name="operator"
          @change=${(e: UUISelectEvent) =>
            this.#onRuleOperatorChange(e, ruleIndex)}
          .options=${this.#workspaceContext?.getConditionOperators.map((t) => ({
            name: this.localize.term("formConditions_operator" + t.value),
            value: t.value,
            selected: t.value === rule.operator,
          })) ?? []}
        >
        </uui-select>
      </div>

      <div class="grow">
        ${when(
          prevaluesForField.length > 0,
          () =>
            html`<uui-select
              name="value"
              @change=${(e: UUISelectEvent) =>
                this.#onRuleValueChange(e.target.value.toString(), ruleIndex)}
              .options=${prevaluesForField.map((pv) => ({
                name: pv,
                value: pv,
                selected: pv === rule.value,
              }))}
            >
            </uui-select>`,
          () =>
            html`<uui-input
              name="value"
              type="text"
              value=${rule.value}
              @change=${(e: UUIInputEvent) =>
                this.#onRuleValueChange(e.target.value.toString(), ruleIndex)}
            ></uui-input>`,
        )}
      </div>

      <uui-button
        label=${this.localize.term("general_delete")}
        look="secondary"
        color="default"
        @click=${() => this.#onDeleteRuleClick(ruleIndex)}
      >
        <uui-icon name="delete"></uui-icon>
      </uui-button>
    </div>`;
  }

  render() {
    return html` <div class="flex center">
        <uui-select
          name="actionType"
          @change=${this.#onActionTypeChange}
          .options=${this.#workspaceContext?.getConditionActionTypes.map(
            (t) => ({
              name: this.localize.term(
                `formConditions_${this.appliedTo === ConditionAppliedTo.WORKFLOW ? "workflow" : ""}actionType${t.value}`,
              ),
              value: t.value,
              selected: t.value === this.#value.actionType,
            }),
          ) ?? []}
        ></uui-select>
        <span
          >${this.localize.term(
            "formConditions_" + this.#getAppliedToLabel(),
          )}</span
        >
        <uui-select
          name="logicType"
          @change=${this.#onLogicTypeChange}
          .options=${this.#workspaceContext?.getConditionLogicTypes.map(
            (t) => ({
              name: this.localize.term("formConditions_logicType" + t.value),
              value: t.value,
              selected: t.value === this.#value.logicType,
            }),
          ) ?? []}
        >
        </uui-select>
        <span
          >${this.localize.term("formConditions_ofTheFollowingMatch")}:</span
        >
      </div>

      ${this.#value.rules.map((rule, idx) => this.#renderRule(rule, idx))}

      <uui-button
        label=${this.localize.term("formConditions_addCondition")}
        look="secondary"
        color="default"
        @click=${this.#onAddRuleClick}
        >${this.localize.term("formConditions_addCondition")}</uui-button
      >`;
  }

  static styles = [
    css`
      .flex {
        display: flex;
        column-gap: var(--uui-size-3);
      }

      .grow {
        flex: 1;

        * {
          width: 100%;
        }
      }

      .center {
        align-items: center;
      }

      :host > uui-button {
        margin-top: var(--uui-size-5);
      }

      .condition-rule {
        margin-top: var(--uui-size-3);
      }

      .condition-rule:first-of-type {
        margin-top: var(--uui-size-5);
      }
    `,
  ];
}

export default FormsEditConditionsElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditConditionsElement;
  }
}
