import type { PerFormSecurityBase } from "../../../entities.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import {
  LitElement,
  css,
  customElement,
  html,
  property,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { DateTimeOptions } from "@umbraco-forms/entities";

const elementName = "form-security-table";

@customElement(elementName)
export class FormsFormSecurityTableElement extends UmbElementMixin(LitElement) {
  @property()
  set?: (formId: string, value: boolean) => void;

  @property()
  toggle?: (formId: string) => void;

  @property({ type: Array })
  records: Array<PerFormSecurityBase> = [];

  @state()
  get _allFormsAccessible() {
    return this.records.every((form) => {
      return form.hasAccess;
    });
  }

  #sortBy: string = "formName";
  #sortDirection: string = "asc";

  #headerCellConfig: Array<{
    key: string;
    label: string;
    labelSuffix?: string;
  }> = [
    {
      key: "formName",
      label: "formSecurity_formName",
    },
    {
      key: "formCreated",
      label: "content_createDate",
      labelSuffix: "(UTC)",
    },
    {
      key: "hasAccess",
      label: "formSecurity_hasAccess",
    },
  ];

  #onToggleAllFormsAccess() {
    const value = this._allFormsAccessible ? false : true;

    this.records.forEach((record) => {
      // Update the context data.
      this.set?.(record.form, value);

      // Update also in the sortable local copy.
      record = { ...record, ...{ hasAccess: value } };
    });

    this.dispatchEvent(new CustomEvent("valueChange"));
  }

  #sortForms(field: string) {
    // If switching column, revert to ascending sort. Otherwise switch from whatever was previously selected.
    if (this.#sortBy !== field) {
      this.#sortDirection = "asc";
    } else {
      this.#sortDirection = this.#sortDirection === "asc" ? "desc" : "asc";
    }

    // Sort by the new column.
    this.#sortBy = field;
    this.records = [...this.records].sort((a, b) => {
      switch (field) {
        case "hasAccess":
          return Number(a.hasAccess) - Number(b.hasAccess);
        case "formCreated":
          return Date.parse(a.formCreated) - Date.parse(b.formCreated);
        default:
          return a[field].localeCompare(b[field]);
      }
    });

    // Reverse the order if sorting descending.
    if (this.#sortDirection === "desc") {
      this.records.reverse();
    }
  }

  #onToggleHasAccess(formId: string) {
    // Update the context data.
    this.toggle?.(formId);

    // Update also in the sortable local copy.
    let record = this.records.find((f) => f.form === formId);
    if (record) {
      record = { ...record, ...{ hasAccess: !record?.hasAccess } };
    }

    this.dispatchEvent(new CustomEvent("valueChange"));
  }

  #renderHeaderCells() {
    return html`${this.#headerCellConfig.map(
      (cell) =>
        html`<uui-table-head-cell>
          <button @click=${() => this.#sortForms(cell.key)}>
            ${this.localize.term(cell.label) +
            (cell.labelSuffix ? ` ${cell.labelSuffix}` : "")}
            <uui-symbol-sort
              ?active=${this.#sortBy === cell.key}
              ?descending=${this.#sortDirection === "desc"}
            ></uui-symbol-sort>
          </button>
        </uui-table-head-cell>`,
    )}`;
  }

  render() {
    return html`<div id="header">
        ${this.localize.term("formSecurity_selectAndDeselect")}
        <uui-toggle
          ?checked=${this._allFormsAccessible}
          @change=${this.#onToggleAllFormsAccess}
        ></uui-toggle>
      </div>
      <uui-table>
        <uui-table-head> ${this.#renderHeaderCells()} </uui-table-head>
        ${this.records.map(
          (record) =>
            html`<uui-table-row>
              <uui-table-cell>
                <div>${record.formName}</div>
                <small>${record.fields}</small>
              </uui-table-cell>
              <uui-table-cell>
                <umb-localize-date
                  .date=${record.formCreated}
                  .options=${DateTimeOptions}
                ></umb-localize-date>
              </uui-table-cell>
              <uui-table-cell>
                <uui-toggle
                  ?checked=${record.hasAccess}
                  label=${this.localize.term("formSecurity_hasAccess")}
                  @change=${() => this.#onToggleHasAccess(record.form)}
                ></uui-toggle>
              </uui-table-cell>
            </uui-table-row>`,
        )}
      </uui-table>`;
  }

  static styles = css`
    #header {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      column-gap: var(--uui-size-3);
    }

    uui-table-head-cell button {
      background-color: transparent;
      color: inherit;
      border: none;
      cursor: pointer;
      font-weight: inherit;
      font-size: inherit;
      display: inline-flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: var(--uui-size-5) var(--uui-size-1);
    }
  `;
}

export default FormsFormSecurityTableElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormSecurityTableElement;
  }
}
