import { FORMS_FORM_ENTITY_TYPE } from "../entities.js";
import type { FormsSecurityPermissionConditionConfig } from "../../security/conditions/security-permission-condition.config.js";
import type { SecurityOptionsConditionConfig } from "../../security/conditions/index.js";
import {
  FORMS_SECURITY_PERMISSION_CONDITION,
  FORMS_SECURITY_OPTIONS_CONDITION,
} from "../../security/conditions/manifests.js";
import { manifests as entriesManifests } from "./views/entries/manifests.js";
import type {
  BackOfficeConfig,
  FormSecurityForUser,
} from "@umbraco-forms/generated";
import { UmbSaveWorkspaceAction } from "@umbraco-cms/backoffice/workspace";
import type {
  ManifestWorkspace,
  ManifestWorkspaceAction,
  ManifestWorkspaceView,
} from "@umbraco-cms/backoffice/workspace";

export const FORM_WORKSPACE_ALIAS = "Forms.Workspace.Form";

const workspace: ManifestWorkspace = {
  type: "workspace",
  kind: "routable",
  alias: FORM_WORKSPACE_ALIAS,
  name: "Form Workspace",
  api: () => import("./form-workspace.context.js"),
  meta: {
    entityType: FORMS_FORM_ENTITY_TYPE,
  },
};

const workspaceViews: Array<ManifestWorkspaceView> = [
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.Form.Design",
    name: "Form Workspace Design View",
    element: () =>
      import("./views/design/workspace-view-form-design.element.js"),
    weight: 50,
    meta: {
      label: "Design",
      pathname: "design",
      icon: "document",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.manageForms,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.Form.Settings",
    name: "Form Workspace Settings View",
    element: () =>
      import("./views/settings/workspace-view-form-settings.element.js"),
    weight: 40,
    meta: {
      label: "Settings",
      pathname: "settings",
      icon: "settings",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.manageForms,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.Form.Advanced",
    name: "Form Workspace Advanced View",
    element: () =>
      import("./views/advanced/workspace-view-form-advanced.element.js"),
    weight: 30,
    meta: {
      label: "Advanced",
      pathname: "advanced",
      icon: "code",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.manageForms,
      } as FormsSecurityPermissionConditionConfig,
      {
        alias: FORMS_SECURITY_OPTIONS_CONDITION,
        match: (config: BackOfficeConfig) =>
          config.enableAdvancedValidationRules,
      } as SecurityOptionsConditionConfig, // This is necessary as ConditionType is defined in core, and we don't have the ability to extend it.
    ],
  },
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.Form.Entries",
    name: "Form Workspace Entries View",
    element: () =>
      import("./views/entries/workspace-view-form-entries.element.js"),
    weight: 20,
    meta: {
      label: "Entries",
      pathname: "entries",
      icon: "icon-categories",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.viewEntries,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.Form.Info",
    name: "Form Workspace Info View",
    element: () => import("./views/info/workspace-view-form-info.element.js"),
    weight: 10,
    meta: {
      label: "Info",
      pathname: "info",
      icon: "info",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
    ],
  },
];

const workspaceActions: Array<ManifestWorkspaceAction> = [
  {
    type: "workspaceAction",
    kind: "default",
    alias: "Forms.WorkspaceAction.Form.Save",
    name: "Save Form Workspace Action",
    weight: 80,
    api: UmbSaveWorkspaceAction,
    meta: {
      label: "#buttons_save",
      look: "primary",
      color: "positive",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
    ],
  },
  {
    type: "workspaceAction",
    kind: "default",
    alias: "Forms.WorkspaceAction.Form.SaveAndPreview",
    name: "Save And Preview Form Workspace Action",
    weight: 90,
    api: () => import("./actions/save-and-preview.action.js"),
    meta: {
      label: "#buttons_saveAndPreview",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: FORM_WORKSPACE_ALIAS,
      },
    ],
  },
];

export const manifests = [
  workspace,
  ...workspaceViews,
  ...workspaceActions,
  ...entriesManifests,
];
