import { FormsFieldTypeDetailRepository } from "./field-type-detail.repository.js";
import { FormsFieldTypeDetailStore } from "./field-type-detail.store.js";
import type {
  ManifestRepository,
  ManifestStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_FIELD_TYPE_DETAIL_REPOSITORY_ALIAS =
  "Forms.Repository.FieldType.Detail";
export const FORMS_FIELD_TYPE_DETAIL_STORE_ALIAS =
  "Forms.Store.FieldType.Detail";

const repository: ManifestRepository = {
  type: "repository",
  alias: FORMS_FIELD_TYPE_DETAIL_REPOSITORY_ALIAS,
  name: "Field Type Detail Repository",
  api: FormsFieldTypeDetailRepository,
};

const store: ManifestStore = {
  type: "store",
  alias: FORMS_FIELD_TYPE_DETAIL_STORE_ALIAS,
  name: "Field Type Detail Store",
  api: FormsFieldTypeDetailStore,
};

export const manifests = [repository, store];
