export const FORMS_SECURITY_USER_ENTITY_TYPE = "forms-security-user";
export const FORMS_SECURITY_USER_GROUP_ENTITY_TYPE =
  "forms-security-user-group";
export const FORMS_SECURITY_USER_FOLDER_ENTITY_TYPE =
  "forms-security-user-folder";
export const FORMS_SECURITY_USER_GROUP_FOLDER_ENTITY_TYPE =
  "forms-security-user-group-folder";
export const FORMS_SECURITY_ROOT_ENTITY_TYPE = "forms-security-root";

export type FormsSecurityUserEntityType =
  typeof FORMS_SECURITY_USER_ENTITY_TYPE;
export type FormsSecurityUserGroupEntityType =
  typeof FORMS_SECURITY_USER_GROUP_ENTITY_TYPE;
export type FormsSecurityUserFolderEntityType =
  typeof FORMS_SECURITY_USER_FOLDER_ENTITY_TYPE;
export type FormsSecurityUserGroupFolderEntityType =
  typeof FORMS_SECURITY_USER_GROUP_FOLDER_ENTITY_TYPE;
export type FormsSecurityRootEntityType =
  typeof FORMS_SECURITY_ROOT_ENTITY_TYPE;

export interface PerFormSecurityBase {
  formName: string;
  formCreated: string;
  fields: string;
  hasAccess: boolean;
  form: string;
}

export interface PackagePermissionsSecurityBase {
  manageDataSources: boolean;
  managePreValueSources: boolean;
  manageWorkflows: boolean;
  manageForms: boolean;
  viewEntries: boolean;
  editEntries: boolean;
  deleteEntries: boolean;
}
