import { tryExecute } from "@umbraco-cms/backoffice/resources";
import type { UmbCollectionDataSource } from "@umbraco-cms/backoffice/collection";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import type { WorkflowTypeWithSettings } from "@umbraco-forms/generated";
import { WorkflowTypeService } from "@umbraco-forms/generated";

export class FormsWorkflowTypeCollectionServerDataSource
  implements UmbCollectionDataSource<WorkflowTypeWithSettings>
{
  #host: UmbControllerHost;

  constructor(host: UmbControllerHost) {
    this.#host = host;
  }

  async getCollection() {
    const { data, error } = await tryExecute(
      this.#host,
      WorkflowTypeService.getWorkflowType(),
    );

    return error
      ? { error }
      : {
          data: {
            items: data,
            total: data.length,
          },
        };
  }
}
