import { FORMS_FORM_ENTRY_COLLECTION_CONTEXT } from "./form-entry-collection.context-token.js";
import { UmbTextStyles } from "@umbraco-cms/backoffice/style";
import {
  css,
  html,
  customElement,
  queryAll,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import type { UUIInputElement } from "@umbraco-cms/backoffice/external/uui";

@customElement("form-entry-filter-date-range-selector")
export class FormsFormEntryFilterDateRangeSelectorElement extends UmbLitElement {
  @state()
  private _startDate?: string;

  @state()
  private _endDate?: string;

  @queryAll("uui-input")
  private _inputs!: NodeListOf<UUIInputElement>;

  #collectionContext?: typeof FORMS_FORM_ENTRY_COLLECTION_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_ENTRY_COLLECTION_CONTEXT, (context) => {
      this.#collectionContext = context;
      this._startDate = this.#collectionContext?.oneMonthAgo();
      this._endDate = this.#collectionContext?.today();
    });
  }

  #setDates() {
    this._inputs.forEach((input) => {
      if (input.id === "start-date") {
        this._startDate = input.value.toString();
      } else if (input.id === "end-date") {
        this._endDate = input.value.toString();
      }
    });

    this.#collectionContext?.setFilter({
      startDate: this._startDate,
      endDate: this._endDate,
    });
  }

  render() {
    return html`
      <div class="input-container">
        <uui-label for="start-date">From:</uui-label>
        <uui-input
          id="start-date"
          type="date"
          label="From"
          .max=${this.#collectionContext?.today()}
          .value=${this._startDate}
          @change=${this.#setDates}
        ></uui-input>
      </div>
      <div class="input-container">
        <uui-label for="end-date">To: </uui-label>
        <uui-input
          id="end-date"
          type="date"
          label="To"
          .min=${this._startDate}
          .max=${this.#collectionContext?.today()}
          .value=${this._endDate}
          @change=${this.#setDates}
        ></uui-input>
      </div>
    `;
  }

  static styles = [
    UmbTextStyles,
    css`
      :host {
        display: flex;
        gap: var(--uui-size-space-5);
      }

      .input-container {
        display: flex;
        align-items: baseline;
        column-gap: var(--uui-size-space-3);
      }
    `,
  ];
}

export default FormsFormEntryFilterDateRangeSelectorElement;

declare global {
  interface HTMLElementTagNameMap {
    "form-entry-filter-date-range-selector": FormsFormEntryFilterDateRangeSelectorElement;
  }
}
