import { FormsSorterConfig } from "../../../../../forms-sorter-config.js";
import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import {
  customElement,
  html,
  property,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import type {
  UUIInputEvent,
  UUISelectElement,
} from "@umbraco-cms/backoffice/external/uui";
import type { Field } from "@umbraco-forms/generated";
import { UmbSorterController } from "@umbraco-cms/backoffice/sorter";

const elementName = "forms-settings-fields-display";

export type EntryViewDisplaySystemField = { name: string; labelKey: string };

@customElement(elementName)
export class FormsSettingsFieldsDisplayElement extends FormsSettingsElementBaseElement {
  #sortModel: Array<string> = [];

  #sorter = new UmbSorterController(this, {
    ...new FormsSorterConfig(
      "Forms.SorterIdentifier.DisplayField",
      ".display-field-row",
    ).config,

    onChange: ({ model }) => {
      this.#sortModel = model;
    },
    onEnd: () => {
      const selectedDisplayFields = structuredClone(
        this.form.selectedDisplayFields,
      );
      selectedDisplayFields.sort(
        (a, b) =>
          this.#sortModel.indexOf(a.alias) - this.#sortModel.indexOf(b.alias),
      );
      this.setPropertyValue("selectedDisplayFields", selectedDisplayFields);
    },
  });

  @property({ type: Array })
  formFields: Array<Field> = [];

  #systemFields: Array<EntryViewDisplaySystemField> = [
    {
      name: "member",
      labelKey: "formEntries_member",
    },
    {
      name: "state",
      labelKey: "formEntries_state",
    },
    {
      name: "created",
      labelKey: "formEntries_submittedOn",
    },
    {
      name: "update",
      labelKey: "formEntries_updatedOn",
    },
    {
      name: "workflows",
      labelKey: "formEdit_workflows",
    },
  ];

  onObserveForm(): void {
    this.#setSortModel();
  }

  #setSortModel() {
    this.#sortModel = [];

    const fields = this.form.selectedDisplayFields;
    for (let i = 0; i < fields.length; i++) {
      const field = fields[i];
      this.#sortModel.push(field.alias);
    }

    this.#sorter.setModel(this.#sortModel);
  }

  #isFieldSelectedForDisplay(alias: string, isSystemField: boolean) {
    return this.form.selectedDisplayFields.some(
      (f) => f.alias === alias && f.isSystem === isSystemField,
    );
  }

  #onAddFieldClick() {
    const selectElement = this.shadowRoot?.getElementById(
      "fields",
    ) as UUISelectElement;
    const selectedOption = selectElement.value.toString();

    const selectedDisplayFields = structuredClone(
      this.form.selectedDisplayFields,
    );

    if (selectedOption.startsWith("_system_")) {
      const selectedSystemField = this.#systemFields.find(
        (f) => "_system_" + f.name === selectedOption,
      );

      if (selectedSystemField) {
        selectedDisplayFields.push({
          alias: selectedSystemField.name,
          caption: this.localize.term(selectedSystemField.labelKey),
          isSystem: true,
        });
      }
    } else {
      const selectedFormField = this.formFields.find(
        (f) => f.alias === selectedOption,
      );

      if (selectedFormField) {
        selectedDisplayFields.push({
          alias: selectedFormField.alias,
          caption: selectedFormField.caption,
          isSystem: false,
        });
      }
    }

    this.setPropertyValue("selectedDisplayFields", selectedDisplayFields);
    selectElement.value = "";
  }

  #onEditFieldCaptionChange(e: UUIInputEvent, index: number) {
    const caption = e.target.value.toString();
    const selectedDisplayFields = structuredClone(
      this.form.selectedDisplayFields,
    );
    selectedDisplayFields[index].caption = caption || "";
    this.setPropertyValue("selectedDisplayFields", selectedDisplayFields);
  }

  #onDeleteFieldClick(index: number) {
    const selectedDisplayFields = structuredClone(
      this.form.selectedDisplayFields,
    );
    selectedDisplayFields.splice(index, 1);
    this.setPropertyValue("selectedDisplayFields", selectedDisplayFields);
  }

  #renderSelect() {
    return html`<div>
      <uui-select
        style="width:300px"
        id="fields"
        .options=${[
          ...this.formFields
            .filter((f) => !this.#isFieldSelectedForDisplay(f.alias, false))
            .map((f) => ({
              name: this.localize.term(f.caption),
              value: f.alias,
              group: this.localize.term("formSettings_formFields"),
            })),
          ...this.#systemFields
            .filter((f) => !this.#isFieldSelectedForDisplay(f.name, true))
            .map((f) => ({
              name: this.localize.term(f.labelKey),
              value: `_system_${f.name}`,
              group: this.localize.term("formSettings_systemFields"),
            })),
        ]}
      >
      </uui-select>
      <uui-button
        label=${this.localize.term("general_add")}
        look="secondary"
        color="default"
        @click=${this.#onAddFieldClick}
      ></uui-button>
    </div>`;
  }

  #renderTable() {
    return this.form.selectedDisplayFields.length === 0
      ? html` ${this.localize.term("formSettings_noSelectedDisplayFields")} `
      : html`<uui-table>
          <uui-table-head>
            <uui-table-head-cell></uui-table-head-cell>
            <uui-table-head-cell
              >${this.localize.term("general_alias")}</uui-table-head-cell
            >
            <uui-table-head-cell
              >${this.localize.term("general_header")}</uui-table-head-cell
            >
            <uui-table-head-cell></uui-table-head-cell>
          </uui-table-head>
          ${this.form.selectedDisplayFields.map(
            (f, idx) =>
              html`<uui-table-row
                class="display-field-row"
                sort-unique=${f.alias}
              >
                <uui-table-cell
                  ><uui-icon name="icon-navigation"></uui-icon
                ></uui-table-cell>
                <uui-table-cell>
                  ${f.alias}
                  ${when(
                    f.isSystem,
                    () =>
                      html`<span
                        >(${this.localize.term("general_systemField")})</span
                      >`,
                  )}
                </uui-table-cell>
                <uui-table-cell>
                  ${f.isSystem
                    ? html`<uui-input
                        id="caption"
                        name="caption"
                        .value=${f.caption}
                        @change=${(e: UUIInputEvent) =>
                          this.#onEditFieldCaptionChange(e, idx)}
                        label="Caption"
                      ></uui-input>`
                    : html`${f.caption}`}
                </uui-table-cell>
                <uui-table-cell>
                  <uui-button
                    label="Remove Field"
                    look="secondary"
                    color="default"
                    @click=${() => this.#onDeleteFieldClick(idx)}
                    ><uui-icon name="icon-trash"></uui-icon
                  ></uui-button>
                </uui-table-cell>
              </uui-table-row>`,
          )}
        </uui-table>`;
  }

  render() {
    return html`<umb-property-layout
      alias="fieldsDisplayed"
      .label=${this.localize.term("formSettings_fieldsDisplayed")}
      .description=${this.localize.term(
        "formSettings_fieldsDisplayedDescription",
      )}
    >
      <div slot="editor">
        <div class="flex">
          ${this.renderToggle(
            this.form.displayDefaultFields,
            "displayDefaultFields",
            "formSettings_displayDefaultFields",
          )}
        </div>
        <div class="flex">
          ${when(
            this.form.displayDefaultFields,
            () => html`
              ${this.localize.term(
                "formSettings_displayDefaultFieldsDescription",
              )}
            `,
            () => html` ${this.#renderSelect()} ${this.#renderTable()} `,
          )}
        </div>
      </div>
    </umb-property-layout>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsFieldsDisplayElement;
  }
}
