import type {
  FormsEditFieldsetModalData,
  FormsEditFieldsetModalValue,
} from "../token/form-edit-fieldset-modal.token.js";
import { FormDesignScaffoldsGenerator } from "../../section/forms/workspace/form-design-scaffolds-generator.class.js";
import {
  ConditionAppliedTo,
  type FormsEditConditionsElement,
} from "./form-edit-conditions.element.js";
import {
  css,
  customElement,
  html,
  state,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";
import type { FieldsetContainer } from "@umbraco-forms/generated";
import { FORMS_CONTEXT } from "@umbraco-forms/context";
import type {
  UUIBooleanInputEvent,
  UUISelectEvent,
} from "@umbraco-cms/backoffice/external/uui";

const elementName = "form-edit-fieldset-modal";

@customElement(elementName)
export class FormsEditFieldsetModalElement extends UmbModalBaseElement<
  FormsEditFieldsetModalData,
  FormsEditFieldsetModalValue
> {
  @state()
  _conditionEnabled = false;

  @state()
  _containers: Array<FieldsetContainer> = [];

  #maxContainers: number = 12;

  constructor() {
    super();

    this.consumeContext(FORMS_CONTEXT, (instance) => {
      if (!instance) return;
      this.observe(instance.config, (config) => {
        if (config) {
          this.#maxContainers = config?.maxNumberOfColumnsInFormGroup;
        }
      });
    });
  }

  connectedCallback(): void {
    super.connectedCallback();
    this._conditionEnabled = this.value?.condition?.enabled ?? false;
    this._containers = structuredClone(this.value?.containers) ?? [];
  }

  #onPageChange(e: UUISelectEvent) {
    const pageIndex = e.target["_input"].selectedIndex - 1;
    this.modalContext?.updateValue({ pageIndex });
  }

  #addContainer() {
    this._containers.push(FormDesignScaffoldsGenerator.getContainerScaffold());
    this.#setContainerWidths();
  }

  #removeContainer(idx: number) {
    this._containers.splice(idx, 1);
    this.#setContainerWidths();
  }

  #setContainerWidths() {
    const width = 12 / this._containers.length;
    this._containers.forEach((c) => (c.width = width));
    this.requestUpdate();
  }

  #onConditionEnabledChange(e: UUIBooleanInputEvent) {
    this._conditionEnabled = e.target.checked;
    const condition = structuredClone(this.value.condition);
    condition.enabled = this._conditionEnabled;
    this.modalContext?.updateValue({ condition });
  }

  #onConditionChange(e: CustomEvent) {
    const condition =
      (e.target as FormsEditConditionsElement).value ?? undefined;
    this.modalContext?.updateValue({ condition });
  }

  #handleSubmit() {
    this.modalContext?.updateValue({ containers: this._containers });
    this.modalContext?.submit();
  }

  get #hasMaxContainers() {
    return this._containers.length === this.#maxContainers;
  }

  render() {
    return html`<umb-body-layout
      headline=${this.localize.term("formEdit_editGroup")}
    >
      <div id="main">
        <uui-box>
          <umb-property-layout alias="page" label="Page">
            <div slot="editor">
              <uui-select
                label="Page"
                @change=${this.#onPageChange}
                .options=${this.data?.pages.map((p, i) => ({
                  name: p.caption ?? `Page ${i + 1}`,
                  value: p.id,
                  selected: i === this.value?.pageIndex,
                })) ?? []}
              >
              </uui-select>
            </div>
          </umb-property-layout>

          <umb-property-layout
            alias="columns"
            orientation="vertical"
            .label=${this.localize.term("fieldSetColumns_title")}
            .description=${this.localize.term("fieldSetColumns_setNumber")}
          >
            <div slot="editor">
              <strong
                >${this.localize.term(
                  "fieldSetColumns_columnNumberDescription",
                  this._containers.length,
                )}</strong
              >
              <div id="columnsTemplate">
                ${this._containers.map((c, i) =>
                  when(
                    this._containers.length === 1,
                    () => html`<div></div>`,
                    () =>
                      html`<uui-button
                        id="container-${i}"
                        label="Remove column"
                        color="default"
                        look="primary"
                        @click=${() => this.#removeContainer(i)}
                      ></uui-button>`,
                  ),
                )}
              </div>
              <uui-button
                id="addColumn"
                label="Add column"
                look="primary"
                color="default"
                ?disabled=${this.#hasMaxContainers}
                @click=${this.#addContainer}
              ></uui-button>
            </div>
          </umb-property-layout>
          <umb-property-layout
            alias="conditions"
            .label=${this.localize.term("formConditions_title")}
          >
            <div slot="editor">
              <uui-toggle
                ?checked=${this._conditionEnabled}
                .label=${this._conditionEnabled ? "On" : "Off"}
                @change=${this.#onConditionEnabledChange}
              ></uui-toggle>
              ${when(
                this._conditionEnabled,
                () =>
                  html`<form-edit-conditions
                    .value=${this.value.condition}
                    .fields=${this.data?.fields ?? []}
                    .appliedTo=${ConditionAppliedTo.FIELDSET}
                    @change=${this.#onConditionChange}
                  ></form-edit-conditions>`,
              )}
            </div>
          </umb-property-layout>
        </uui-box>
      </div>
      <div slot="actions">
        <uui-button
          .label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
        <uui-button
          color="positive"
          look="primary"
          label=${this.localize.term("general_submit")}
          @click=${this.#handleSubmit}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }

  static styles = [
    css`
      #columnsTemplate {
        width: 100%;
        height: var(--uui-size-24);
        box-sizing: border-box;
        padding: var(--uui-size-1);
        border: 2px solid var(--uui-color-border);
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        margin-bottom: var(--uui-size-4);
        gap: var(--uui-size-1);
        border-radius: var(--uui-border-radius);
      }

      #columnsTemplate > * {
        background: var(--uui-color-interactive);
        flex: 1;
      }
    `,
  ];
}

export default FormsEditFieldsetModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditFieldsetModalElement;
  }
}
