import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import { customElement, html } from "@umbraco-cms/backoffice/external/lit";

const elementName = "forms-settings-styling";

@customElement(elementName)
export class FormsSettingsStylingElement extends FormsSettingsElementBaseElement {
  render() {
    return html` <umb-property-layout
      alias="styling"
      .label=${this.localize.term("formSettings_styling")}
      .description=${this.localize.term("formSettings_stylingDescription")}
    >
      <div slot="editor">
        <div class="flex">
          <uui-label
            >${this.localize.term("formSettings_formCssClass")}</uui-label
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_formCssClass")}
            .value=${this.form?.cssClass ?? ""}
            @change=${(e) => this.onInputChange(e, "cssClass")}
          ></uui-input>
        </div>
        ${this.renderToggle(
          this.form?.disableDefaultStylesheet,
          "disableDefaultStylesheet",
          "formSettings_disableDefaultStylesheet",
        )}
      </div>
    </umb-property-layout>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsStylingElement;
  }
}
