import { FormsFieldPreviewBaseElement } from "./field-preview-base.element.js";
import {
  customElement,
  html,
  repeat,
  css,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbTextStyles } from "@umbraco-cms/backoffice/style";

const elementName = "forms-field-preview-radiobuttonlist";
@customElement(elementName)
export class FormsFieldPreviewRadioButtonList extends FormsFieldPreviewBaseElement {
  render() {
    return html`
      ${repeat(
        this.prevalues.slice(0, 5),
        (pv) => html`
          <div
            class=${this.getSettingValue("DisplayLayout")
              ? this.getSettingValue("DisplayLayout").toLowerCase()
              : ""}
          >
            <span>
              <input type="radio" disabled tabindex="-1" />
              <label>${pv.caption || pv.value}</label>
            </span>
          </div>
        `,
      )}
      ${when(
        this.prevalues.length > 5,
        () => html` <span class="ellipsis">...</span> `,
      )}
    `;
  }

  static styles = [
    UmbTextStyles,
    css`
      .horizontal {
        display: inline;
      }

      .horizontal .span {
        margin-right: 10px;
      }

      .ellipsis {
        margin-left: 24px;
      }
    `,
  ];
}

export default FormsFieldPreviewRadioButtonList;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFieldPreviewRadioButtonList;
  }
}
