import { ThemeCollectionRepository } from "../../section/themes/repository/collection/index.js";
import {
  html,
  customElement,
  property,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import type { UUISelectEvent } from "@umbraco-cms/backoffice/external/uui";

const elementName = "forms-input-theme";

@customElement(elementName)
export class FormsInputThemeElement extends UmbLitElement {
  @state()
  private _options: Array<string> = [];

  @property()
  value = "";

  async connectedCallback() {
    super.connectedCallback();

    await this.#loadThemes();
  }

  async #loadThemes() {
    const themeRepository = new ThemeCollectionRepository(this);
    const { data } = await themeRepository.requestCollection();

    this._options = data?.items.map((i) => i.name) ?? [];
  }

  #onChange(e: UUISelectEvent) {
    const newValue = e.target.value.toString();
    if (newValue === this.value) return;
    this.value = newValue;
    this.dispatchEvent(
      new CustomEvent("change", { composed: true, bubbles: true }),
    );
  }

  render() {
    return html`
      <uui-select
        name="themePicker"
        label="Theme picker"
        @change=${this.#onChange}
        .options=${this._options.map((t) => ({
          name: t,
          value: t,
          selected: t === this.value,
        }))}
      >
      </uui-select>
    `;
  }
}

export default FormsInputThemeElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsInputThemeElement;
  }
}
