import { FORMS_FORM_FOLDER_ENTITY_TYPE } from "../../entities.js";
import type {
  UmbFolderModel,
  UmbTreeItemModel,
} from "@umbraco-cms/backoffice/tree";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import { tryExecute } from "@umbraco-cms/backoffice/resources";
import { FolderService } from "@umbraco-forms/generated";
import type {
  UmbDataSourceResponse,
  UmbDetailDataSource,
} from "@umbraco-cms/backoffice/repository";
import { UmbId } from "@umbraco-cms/backoffice/id";

export class FormsFormFolderServerDataSource
  implements UmbDetailDataSource<UmbFolderModel>
{
  #host: UmbControllerHost;

  constructor(host: UmbControllerHost) {
    this.#host = host;
  }

  async createScaffold(preset?: Partial<UmbFolderModel>) {
    const scaffold: UmbFolderModel = {
      entityType: FORMS_FORM_FOLDER_ENTITY_TYPE,
      unique: UmbId.new(),
      name: "",
      ...preset,
    };

    return { data: scaffold };
  }

  async read(unique: string): Promise<UmbDataSourceResponse<UmbTreeItemModel>> {
    if (!unique) throw new Error("Unique is missing");

    const { data, error } = await tryExecute(
      this.#host,
      FolderService.getFolderById({ path: { id: unique } }),
    );

    return error
      ? { error }
      : {
          data: {
            unique: data.id,
            entityType: FORMS_FORM_FOLDER_ENTITY_TYPE,
            name: data.name,
            isFolder: true,
            hasChildren: true, // TODO: Check if the folder has children
            parent: {
              unique: data.parentId ? data.parentId : null,
              entityType: FORMS_FORM_FOLDER_ENTITY_TYPE,
            },
          },
        };
  }

  async create(model: UmbFolderModel, parentUnique: string | null) {
    if (!model) throw new Error("Data is missing");
    if (!model.unique) throw new Error("Unique is missing");
    if (!model.name) throw new Error("Name is missing");

    const body = {
      id: model.unique,
      parent: parentUnique ? { id: parentUnique } : null,
      name: model.name,
    };

    const { error } = await tryExecute(
      this.#host,
      FolderService.postFolder({ body }),
    );

    // TODO: Return item in body, instead of having to fetch it again
    return error ? { error } : this.read(model.unique);
  }

  async update(
    model: UmbFolderModel,
  ): Promise<UmbDataSourceResponse<UmbFolderModel>> {
    if (!model) throw new Error("Data is missing");
    if (!model.unique) throw new Error("Unique is missing");
    if (!model.name) throw new Error("Folder name is missing");

    const { error } = await tryExecute(
      this.#host,
      FolderService.putFolderById({
        path: { id: model.unique },
        body: { name: model.name },
      }),
    );

    // TODO: Return item in body, instead of having to fetch it again
    return error ? { error } : this.read(model.unique);
  }

  async delete(unique: string) {
    if (!unique) throw new Error("Unique is missing");

    return await tryExecute(
      this.#host,
      FolderService.deleteFolderById({ path: { id: unique } }),
    );
  }
}
