import { FormDesignScaffoldsGenerator } from "../../../form-design-scaffolds-generator.class.js";
import { FormsSorterConfig } from "../../../../../forms-sorter-config.js";
import { FormsFormDesignElementBaseElement } from "./forms-design-element-base.element.js";
import {
  html,
  customElement,
  property,
  css,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import type { FieldCondition, Page } from "@umbraco-forms/generated";
import type { UUIInputEvent } from "@umbraco-cms/backoffice/external/uui";
import { FORMS_EDIT_PAGE_MODAL } from "@umbraco-forms/modals";
import { UmbSorterController } from "@umbraco-cms/backoffice/sorter";

const elementName = "forms-form-page";

@customElement(elementName)
export class FormsFormPageElement extends FormsFormDesignElementBaseElement {
  @property({ type: Object })
  page?: Page;

  #sorter?: UmbSorterController<string, HTMLElement>;
  #sortModel: Array<string> = [];

  sortModeEnabled() {
    this.#setSorter();
    this.#setSortModel();
  }

  #setSorter() {
    this.#sorter = new UmbSorterController(this, {
      ...new FormsSorterConfig(
        "Forms.SorterIdentifier.Fieldset",
        `forms-form-fieldset.fieldset-${this.page?.id}`,
        `.page-${this.page?.id}`,
      ).config,

      onChange: ({ model }) => {
        this.#sortModel = model;
      },
      onEnd: () => {
        this.structureManager?.reorderGroups(this.page!, this.#sortModel);
      },
    });
  }

  #setSortModel() {
    this.#sortModel = [];

    const fields = this.page!.fieldSets;
    for (let i = 0; i < fields.length; i++) {
      const field = fields[i];
      this.#sortModel.push(field.id);
    }

    this.#sorter?.setModel(this.#sortModel);
  }

  async #onEditClick() {
    if (!this.modalContext) return;
    const buttonCondition = this.page?.buttonCondition
      ? structuredClone(this.page.buttonCondition)
      : FormDesignScaffoldsGenerator.getConditionScaffold();

    const modalHandler = this.modalContext.open(this, FORMS_EDIT_PAGE_MODAL, {
      data: {
        fields: this.allFields,
      },
      value: {
        buttonCondition,
      },
    });

    const result = await modalHandler.onSubmit().catch(() => undefined);
    if (!result) return;

    this.#setPageProperty("buttonCondition", result.buttonCondition);
  }

  #onDeleteClick() {
    this.structureManager?.deleteFormPage(this.page);
  }

  #onAddGroupClick() {
    this.structureManager?.addFormGroup(this.page);
  }

  #onCaptionChange(e: UUIInputEvent) {
    if (this.page) {
      const caption = e.target.value.toString();
      this.workspaceContext!.setPageProperty(this.index, "caption", caption);
    }
  }

  #setPageProperty(propertyAlias: string, value: unknown) {
    this.workspaceContext!.setPageProperty(this.index, propertyAlias, value);
  }

  render() {
    return html`<div id="pageHeader">
        <div id="pageNumber">${this.index + 1}</div>
        <uui-input
          .value=${this.page?.caption}
          @change=${this.#onCaptionChange}
          label="caption"
          .placeholder=${this.localize.term("formEdit_pageTitlePlaceholder")}
        ></uui-input>
        ${when(
          !this.sortModeActive,
          () =>
            html`<div id="pageActions">
              <uui-action-bar>
                <uui-button
                  label="edit"
                  look="secondary"
                  color="default"
                  @click=${this.#onEditClick}
                >
                  <uui-icon name="settings"></uui-icon>
                </uui-button>
                <uui-button
                  label=${this.localize.term("general_delete")}
                  look="secondary"
                  color="default"
                  @click=${this.#onDeleteClick}
                >
                  <uui-icon name="delete"></uui-icon>
                </uui-button>
              </uui-action-bar>
            </div>`,
        )}
      </div>

      ${when(
        !this.sortModeActive,
        () =>
          html` <forms-form-condition-summary
            formElement="pageButton"
            .condition=${this.page?.buttonCondition as FieldCondition}
            .operatorTypes=${this.workspaceContext?.getConditionOperators ?? []}
            .allFields=${this.allFields}
          >
          </forms-form-condition-summary>`,
      )}

      <div id="pageFieldsets" class="page-${this.page?.id}">
        ${this.page?.fieldSets.map(
          (fieldset, idx) =>
            html`<forms-form-fieldset
              class="fieldset-${this.page?.id}"
              sort-unique="${fieldset.id}"
              .fieldset=${fieldset}
              .index=${idx}
              .pageIndex=${this.index}
              .allFields=${this.allFields}
              .allFieldTypes=${this.allFieldTypes}
              .sortModeActive=${this.sortModeActive}
            ></forms-form-fieldset>`,
        )}
      </div>
      <div id="pageFooter">
        ${when(
          !this.sortModeActive,
          () =>
            html` <uui-button
              @click=${this.#onAddGroupClick}
              look="outline"
              label=${this.localize.term("formEdit_addGroup")}
            ></uui-button>`,
        )}
      </div>`;
  }

  static styles = [
    css`
      :host {
        display: block;
        background: #e9e9eb;
        padding: 15px;
        margin-bottom: 30px;
        border-radius: 3px;
      }

      #pageHeader {
        margin-bottom: 15px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: center;
        font-size: 14px;
        font-weight: bold;
      }
      #pageNumber {
        width: 23px;
        height: 23px;
        line-height: 23px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #ffffff;
        border: 1px solid #bbbabf;
        margin-right: 5px;
      }
      #pageActions {
        color: #817f85;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        margin-left: auto;
        padding-left: 10px;
      }
      #pageFooter {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 15px;
      }

      forms-form-fieldset + forms-form-fieldset {
        display: block;
        margin-top: var(--uui-size-5);
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormPageElement;
  }
}
