import type {
  FormsEditSubmitMessageModalData,
  FormsEditSubmitMessageModalValue,
} from "../token/form-edit-submit-message-modal.token.js";
import {
  customElement,
  html,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";
import type { UmbPropertyEditorConfig } from "@umbraco-cms/backoffice/property-editor";
import type {
  UmbPropertyDatasetElement,
  UmbPropertyValueData,
} from "@umbraco-cms/backoffice/property";
import { partialUpdateFrozenArray } from "@umbraco-cms/backoffice/observable-api";

/**
 * type-safe property access
 */
function nameof(
  key: keyof FormsEditSubmitMessageModalValue,
): keyof FormsEditSubmitMessageModalValue {
  return key;
}

const elementName = "form-edit-submit-message-modal";

@customElement(elementName)
export class FormsEditSubmitMessageModalElement extends UmbModalBaseElement<
  FormsEditSubmitMessageModalData,
  FormsEditSubmitMessageModalValue
> {
  @state()
  private _richTextConfiguration?: UmbPropertyEditorConfig = [];

  @state()
  private _value: Array<UmbPropertyValueData> = [];

  @state()
  private _isHtml?: boolean;

  connectedCallback(): void {
    super.connectedCallback();

    this._richTextConfiguration = this.data?.richTextConfiguration;
    this._value = Object.keys(this.value ?? {}).map((v) => ({
      alias: v,
      value: this.value[v],
    }));

    this.#setIsHtml();
  }

  #byAlias(
    arr: Array<UmbPropertyValueData>,
    alias: keyof FormsEditSubmitMessageModalValue,
  ) {
    return arr.find((x) => x.alias === alias);
  }

  #setIsHtml() {
    this._isHtml = this.value.messageOnSubmitIsHtml;

    // ensure message is correctly structured - message is saved as a string, but
    // must be an object to pass to the RTE.
    if (this._isHtml) {
      const messageOnSubmit = this.#byAlias(this._value, "messageOnSubmit");
      messageOnSubmit!.value = {
        markup: messageOnSubmit?.value,
      };
    }
  }

  #formatAsHtml(text: string) {
    if (!text.trim().length) return "";

    // Replace double line breaks with surrounding <p></p> tags, and any remaining single breaks with <br/>.
    const lines = text.replace(/\r/g, "").split(/\n\n/);
    let markup = "<p>" + lines.join("</p><p>") + "</p>";
    markup = markup.replace(/\n/g, "<br/>");
    return { markup };
  }

  #stripHtml(html: string) {
    const doc = new DOMParser().parseFromString(html, "text/html");
    return doc.body.textContent || "";
  }

  #formatMessage(message: string | { markup: string }) {
    if (this._isHtml) {
      // Switching to HTML so initialize the rich text.
      return typeof message === "string"
        ? this.#formatAsHtml(message)
        : message;
    }

    // Switching to plain text so strip HTML.
    return typeof message === "object"
      ? this.#stripHtml(message?.markup)
      : message;
  }

  #onValueChange(e: CustomEvent) {
    let value = (e.target as UmbPropertyDatasetElement).value;

    this._isHtml = Boolean(
      this.#byAlias(value, "messageOnSubmitIsHtml")?.value,
    );

    // Ensure messageOnSubmit is formatted correctly
    const messageOnSubmit = this.#byAlias(value, "messageOnSubmit")?.value as
      | string
      | { markup: string };

    value = partialUpdateFrozenArray(
      value,
      {
        alias: nameof("messageOnSubmit"),
        value: this.#formatMessage(messageOnSubmit),
      },
      (x) => x.alias === nameof("messageOnSubmit"),
    );

    this._value = value;
  }

  #submitModal() {
    const value = Object.fromEntries(
      this._value.map(({ alias, value }) => [alias, value]),
    );

    // Only save the markup value - should we remove blocks from the config
    // for this rte? Can be done in the field-type request
    if (typeof value.messageOnSubmit === "object") {
      value.messageOnSubmit = value.messageOnSubmit?.["markup"] ?? "";
    }

    this.updateValue(value);
    this._submitModal();
  }

  render() {
    return html`<umb-body-layout
      headline=${this.localize.term("formEdit_editSubmitMessage")}
    >
      <uui-box>
        <umb-property-dataset .value=${this._value} @change=${
          this.#onValueChange
        }>
          <umb-property
            .alias=${nameof("messageOnSubmitIsHtml")}
            .label=${this.localize.term("formWorkflows_messageOnSubmitFormat")}
            .config=${[
              {
                alias: "showLabels",
                value: true,
              },
              {
                alias: "labelOn",
                value: this.localize.term(
                  "formWorkflows_messageOnSubmitIsHtmlToggleTextOn",
                ),
              },
              {
                alias: "labelOff",
                value: this.localize.term(
                  "formWorkflows_messageOnSubmitIsHtmlToggleTextOff",
                ),
              },
            ]}
            property-editor-ui-alias="Umb.PropertyEditorUi.Toggle"
          ></umb-property>
          <umb-property
            .alias=${nameof("messageOnSubmit")}
            .label=${this.localize.term("formWorkflows_messageOnSubmit")}
            .description=${this.localize.term(
              "formWorkflows_messageOnSubmitDescription",
            )}
            .appearance=${{ labelOnTop: this._isHtml ?? false }}
            .config=${this._richTextConfiguration}
            .propertyEditorUiAlias=${`Umb.PropertyEditorUi.${
              this._isHtml ? "Tiptap" : "TextArea"
            }`}
          >
          </umb-property>
          <umb-property 
            .alias=${nameof("goToPageOnSubmit")}
            .label=${this.localize.term("formWorkflows_goToPage")}
            .description=${this.localize.term(
              "formWorkflows_goToPageDescription",
            )}
            .config=${[{ alias: "validationLimit", value: { max: 1 } }]}
            property-editor-ui-alias="Umb.PropertyEditorUi.DocumentPicker"></umb-property>
          </umb-property>        
        </umb-property-dataset>
      </uui-box>
      <div slot="actions">
        <uui-button
          label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
        <uui-button
          color="positive"
          look="primary"
          label=${this.localize.term("general_submit")}
          @click=${this.#submitModal}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }
}

export default FormsEditSubmitMessageModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditSubmitMessageModalElement;
  }
}
