import type {
  FormsEditPageModalData,
  FormsEditPageModalValue,
} from "../token/form-edit-page-modal.token.js";
import {
  ConditionAppliedTo,
  type FormsEditConditionsElement,
} from "./form-edit-conditions.element.js";
import {
  customElement,
  html,
  state,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";
import type { UUIBooleanInputEvent } from "@umbraco-cms/backoffice/external/uui";

const elementName = "form-edit-page-modal";

@customElement(elementName)
export class FormsEditPageModalElement extends UmbModalBaseElement<
  FormsEditPageModalData,
  FormsEditPageModalValue
> {
  @state()
  _conditionEnabled = false;

  connectedCallback() {
    super.connectedCallback();
    this._conditionEnabled = this.value?.buttonCondition?.enabled ?? false;
  }

  #onConditionEnabledChange(e: UUIBooleanInputEvent) {
    this._conditionEnabled = e.target.checked;
    const buttonCondition = {
      ...this.value.buttonCondition,
      ...{ enabled: this._conditionEnabled },
    };

    this.modalContext?.updateValue({ buttonCondition });
  }

  #onConditionChange(e: CustomEvent) {
    const buttonCondition =
      (e.target as FormsEditConditionsElement).value ?? undefined;
    this.modalContext?.updateValue({ buttonCondition });
  }

  render() {
    return html`<umb-body-layout
      headline=${this.localize.term("formEdit_editPage")}
    >
      <div id="main">
        <uui-box>
          <umb-property-layout
            alias="conditions"
            .label=${this.localize.term("formConditions_title")}
            .description=${this.localize.term(
              "formConditions_pageConditionsDescription",
            )}
          >
            <div slot="editor">
              <uui-toggle
                ?checked=${this._conditionEnabled}
                .label=${this._conditionEnabled ? "On" : "Off"}
                @change=${this.#onConditionEnabledChange}
              ></uui-toggle>
              ${when(
                this._conditionEnabled,
                () =>
                  html`<form-edit-conditions
                    .value=${this.value.buttonCondition}
                    .fields=${this.data?.fields ?? []}
                    .appliedTo=${ConditionAppliedTo.PAGE}
                    @change=${this.#onConditionChange}
                  ></form-edit-conditions>`,
              )}
            </div>
          </umb-property-layout>
        </uui-box>
      </div>
      <div slot="actions">
        <uui-button
          label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
        <uui-button
          color="positive"
          look="primary"
          label=${this.localize.term("general_submit")}
          @click=${this._submitModal}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }
}

export default FormsEditPageModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditPageModalElement;
  }
}
