import type {
  FormsConfigureWorkflowModalData,
  FormsConfigureWorkflowModalValue,
} from "../token/form-configure-workflow-modal.token.js";
import type { FormsConfigureWorkflowStageElement } from "./form-configure-workflow-stage.element.js";
import {
  css,
  customElement,
  html,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";

const elementName = "form-configure-workflow-modal";

@customElement(elementName)
export class FormsConfigureWorkflowModalElement extends UmbModalBaseElement<
  FormsConfigureWorkflowModalData,
  FormsConfigureWorkflowModalValue
> {
  #onWorkflowStageChange(e: CustomEvent, collectionName: string) {
    const workflowStageElement = e.target as FormsConfigureWorkflowStageElement;
    const submitMessageDetail = workflowStageElement.submitMessageDetail;
    if (submitMessageDetail) {
      this.modalContext?.updateValue({ submitMessageDetail });
    }

    const workflowsForStage = workflowStageElement.workflows;
    const formWorkflows = structuredClone(this.value.workflows);
    if (formWorkflows) {
      formWorkflows[collectionName] = workflowsForStage;
      this.modalContext?.updateValue({ workflows: formWorkflows });
    }

    const workflowStages = workflowStageElement.workflowStages;
    for (let index = 0; index < workflowsForStage.length; index++) {
      const workflowForStage = workflowsForStage[index];
      const toCollectionName = workflowStages[workflowForStage.id];
      if (toCollectionName && toCollectionName !== collectionName) {
        this.#moveWorkflow(
          index,
          collectionName,
          workflowStages[workflowForStage.id],
        );
      }
    }
  }

  #moveWorkflow(
    workflowIndex: number,
    fromCollectionName: string,
    toCollectionName: string,
  ) {
    if (!this.value.workflows) return;
    const workflows = structuredClone(this.value.workflows);
    const fromCollection = workflows[fromCollectionName];
    const workflow = fromCollection.splice(workflowIndex, 1)[0];
    const toCollection = workflows[toCollectionName];
    toCollection.push(workflow);
    this.modalContext?.updateValue({ workflows });
  }

  render() {
    return html`<umb-body-layout
      headline=${this.localize.term("formWorkflows_configureWorkflow")}
    >
      <uui-box>
        <form-configure-workflow-stage
          collectionName="onSubmit"
          .workflows=${this.value.workflows?.onSubmit ?? []}
          .submitMessageDetail=${this.value.submitMessageDetail}
          .allFields=${this.data?.fields ?? []}
          .label=${this.localize.term("formWorkflows_onSubmit")}
          .description=${this.localize.term(
            "formWorkflows_onSubmitDescription",
          )}
          icon="icon-check"
          @change=${(e: CustomEvent) =>
            this.#onWorkflowStageChange(e, "onSubmit")}
        >
        </form-configure-workflow-stage>
        <form-configure-workflow-stage
          collectionName="onApprove"
          .workflows=${this.value.workflows?.onApprove ?? []}
          .allFields=${this.data?.fields ?? []}
          .label=${this.localize.term("formWorkflows_onApprove")}
          .description=${this.localize.term(
            "formWorkflows_onApproveDescription",
          )}
          icon="icon-thumb-up"
          @change=${(e: CustomEvent) =>
            this.#onWorkflowStageChange(e, "onApprove")}
        >
        </form-configure-workflow-stage>

        ${when(
          this.data?.manualApproval,
          () =>
            html`<form-configure-workflow-stage
              collectionName="onReject"
              .workflows=${this.value.workflows?.onReject ?? []}
              .allFields=${this.data?.fields ?? []}
              .label=${this.localize.term("formWorkflows_onReject")}
              .description=${this.localize.term(
                "formWorkflows_onRejectDescription",
              )}
              icon="icon-delete"
              @change=${(e: CustomEvent) =>
                this.#onWorkflowStageChange(e, "onReject")}
            >
            </form-configure-workflow-stage>`,
        )}
      </uui-box>
      <div slot="actions">
        <uui-button
          label=${this.localize.term("general_close")}
          @click="${this._rejectModal}"
        ></uui-button>
        <uui-button
          color="positive"
          look="primary"
          label=${this.localize.term("general_submit")}
          @click=${this._submitModal}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }

  static styles = css`
    form-configure-workflow-stage + form-configure-workflow-stage {
      margin-top: var(--uui-size-5);
    }
  `;
}

export default FormsConfigureWorkflowModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsConfigureWorkflowModalElement;
  }
}
