import {
  FORMS_SECURITY_USER_ENTITY_TYPE,
  FORMS_SECURITY_USER_GROUP_ENTITY_TYPE,
  FORMS_SECURITY_USER_FOLDER_ENTITY_TYPE,
  FORMS_SECURITY_USER_GROUP_FOLDER_ENTITY_TYPE,
  FORMS_SECURITY_ROOT_ENTITY_TYPE,
} from "../entities.js";
import { FormsSecurityTreeRepository } from "./security-tree.repository.js";
import { FormsSecurityTreeStore } from "./security-tree.store.js";

import { manifests as menuManifests } from "./menu-item/manifests.js";
import type {
  ManifestTree,
  ManifestTreeItem,
} from "@umbraco-cms/backoffice/tree";
import type {
  ManifestRepository,
  ManifestTreeStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_SECURITY_TREE_REPOSITORY_ALIAS =
  "Forms.Repository.Security.Tree";
export const FORMS_SECURITY_TREE_STORE_ALIAS = "Forms.Store.Security.Tree";
export const FORMS_SECURITY_TREE_ALIAS = "Forms.Tree.Security";

const treeRepository: ManifestRepository = {
  type: "repository",
  alias: FORMS_SECURITY_TREE_REPOSITORY_ALIAS,
  name: "Forms Security Tree Repository",
  api: FormsSecurityTreeRepository,
};

const treeStore: ManifestTreeStore = {
  type: "treeStore",
  alias: FORMS_SECURITY_TREE_STORE_ALIAS,
  name: "Forms Security Tree Store",
  api: FormsSecurityTreeStore,
};

const tree: ManifestTree = {
  type: "tree",
  kind: "default",
  alias: FORMS_SECURITY_TREE_ALIAS,
  name: "Forms Security Tree",
  meta: {
    repositoryAlias: FORMS_SECURITY_TREE_REPOSITORY_ALIAS,
  },
  conditions: [
    {
      alias: "Umb.Condition.SectionUserPermission",
      match: "Umb.Section.Users",
    },
  ],
};

const treeItem: ManifestTreeItem = {
  type: "treeItem",
  kind: "default",
  alias: "Forms.TreeItem.Security",
  name: "Forms Security Tree Item",
  forEntityTypes: [
    FORMS_SECURITY_USER_ENTITY_TYPE,
    FORMS_SECURITY_USER_GROUP_ENTITY_TYPE,
    FORMS_SECURITY_USER_FOLDER_ENTITY_TYPE,
    FORMS_SECURITY_USER_GROUP_FOLDER_ENTITY_TYPE,
    FORMS_SECURITY_ROOT_ENTITY_TYPE,
  ],
};

export const manifests = [
  treeRepository,
  treeStore,
  tree,
  treeItem,
  ...menuManifests,
];
