import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import { customElement, html } from "@umbraco-cms/backoffice/external/lit";

const elementName = "forms-settings-validation";

@customElement(elementName)
export class FormsSettingsValidationElement extends FormsSettingsElementBaseElement {
  #fieldIndicationTypes = [
    {
      label: this.localize.term("formSettings_markFieldsNoIndicator"),
      value: "NoIndicator",
    },
    {
      label: this.localize.term("formSettings_markMandatoryFields"),
      value: "MarkMandatoryFields",
    },
    {
      label: this.localize.term("formSettings_markOptionalFields"),
      value: "MarkOptionalFields",
    },
  ];

  render() {
    return html` <umb-property-layout
      alias="validation"
      .label=${this.localize.term("formSettings_validation")}
      .description=${this.localize.term("formSettings_validationDescription")}
    >
      <div slot="editor">
        <div class="flex">
          <uui-label
            >${this.localize.term(
              "formSettings_mandatoryErrorMessage",
            )}</uui-label
          >
          <small
            >${this.localize.term(
              "formSettings_mandatoryErrorMessageDescription",
            )}</small
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_mandatoryErrorMessage")}
            .value=${this.form?.requiredErrorMessage ?? ""}
            @change=${(e) => this.onInputChange(e, "requiredErrorMessage")}
          ></uui-input>
        </div>
        <div class="flex gap">
          <uui-label
            >${this.localize.term(
              "formSettings_invalidErrorMessage",
            )}</uui-label
          >
          <small
            >${this.localize.term(
              "formSettings_invalidErrorMessageDescription",
            )}</small
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_invalidErrorMessage")}
            .value=${this.form?.invalidErrorMessage ?? ""}
            @change=${(e) => this.onInputChange(e, "invalidErrorMessage")}
          ></uui-input>
        </div>
        ${this.renderToggle(
          this.form?.showValidationSummary,
          "showValidationSummary",
          "formSettings_showValidationSummary",
        )}
        ${this.renderToggle(
          this.form?.hideFieldValidation,
          "hideFieldValidation",
          "formSettings_hideFieldValidationLabels",
        )}
        <div class="flex gap">
          <uui-label
            >${this.localize.term("formSettings_markFields")}</uui-label
          >
          <uui-radio-group
            .value=${this.form?.fieldIndicationType}
            @change=${(e) => this.onRadioChange(e, "fieldIndicationType")}
          >
            ${this.#fieldIndicationTypes.map(
              (item) =>
                html`<uui-radio
                  name=${"fieldIndicationType"}
                  value=${item.value}
                  label=${item.label}
                ></uui-radio>`,
            )}
          </uui-radio-group>
        </div>
        <div class="flex gap">
          <uui-label>${this.localize.term("formSettings_indicator")}</uui-label>
          <small
            >${this.localize.term("formSettings_changeIndicatorSymbol")}</small
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_indicator")}
            .value=${this.form?.indicator ?? ""}
            @change=${(e) => this.onInputChange(e, "indicator")}
          ></uui-input>
        </div>
      </div>
    </umb-property-layout>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsValidationElement;
  }
}
