import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import { UmbUniqueTreeStore } from "@umbraco-cms/backoffice/tree";

export class FormsSecurityTreeStore extends UmbUniqueTreeStore {
  constructor(host: UmbControllerHost) {
    super(host, FORMS_SECURITY_TREE_STORE_CONTEXT.contextAlias);
  }
}

export const FORMS_SECURITY_TREE_STORE_CONTEXT =
  new UmbContextToken<FormsSecurityTreeStore>("FormsSecurityTreeStore");
