import type { FormsPreviewModalData } from "../token/form-preview-modal.token.js";
import type { RelationItemModel } from "@umbraco-forms/generated";
import { FormService } from "@umbraco-forms/generated";
import {
  customElement,
  html,
  when,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";
import { tryExecute } from "@umbraco-cms/backoffice/resources";
import {
  UMB_DOCUMENT_ENTITY_TYPE,
  UmbDocumentPreviewRepository,
} from "@umbraco-cms/backoffice/document";
import { UMB_SERVER_CONTEXT } from "@umbraco-cms/backoffice/server";
import { ensurePathEndsWithSlash } from "@umbraco-cms/backoffice/utils";

const elementName = "form-preview-modal";

@customElement(elementName)
export class FormsPreviewModalElement extends UmbModalBaseElement<
  FormsPreviewModalData,
  void
> {
  @state()
  _relations: Array<RelationItemModel> = [];

  async connectedCallback() {
    await super.connectedCallback();

    const id = this.data?.unique;
    if (id) {
      const { data } = await tryExecute(
        this,
        FormService.getFormByIdRelations({ path: { id } }),
      );

      if (data) {
        // Only documents can be previewed
        this._relations = data.items.filter(
          (x) => x.nodeType === UMB_DOCUMENT_ENTITY_TYPE,
        );
      }
    }
  }

  async #preview(unique: string) {
    // Tell the server that we're entering preview mode.
    await new UmbDocumentPreviewRepository(this).enter();

    const serverContext = await this.getContext(UMB_SERVER_CONTEXT);
    if (!serverContext) {
      throw new Error("Server context is missing");
    }

    const backofficePath = serverContext.getBackofficePath();
    const previewUrl = new URL(
      ensurePathEndsWithSlash(backofficePath) + "preview",
      window.location.origin,
    );
    previewUrl.searchParams.set("id", unique);

    const previewWindow = window.open(
      previewUrl.toString(),
      `umbpreview-${unique}`,
    );
    previewWindow?.focus();

    // Close the preview modal
    this._rejectModal();
  }

  render() {
    return html`
      <umb-body-layout headline=${this.localize.term("formPreview_headline")}>
        ${when(
          this._relations.length === 0,
          () => this.localize.term("formPreview_descriptionNoItems"),
          () => html`
            <p>${this.localize.term("formPreview_description")}</p>
            <uui-box>
              <uui-ref-list>
                ${this._relations.map(
                  (relation) => html`
                    <uui-ref-node
                      .name="${relation.nodeName}"
                      @open=${() => this.#preview(relation.nodeKey)}
                    >
                      ${when(
                        relation.contentTypeIcon,
                        () =>
                          html`<umb-icon slot="icon" name="${relation.contentTypeIcon}"></uui-icon>`,
                      )}
                      ${when(
                        relation.nodePublished,
                        () =>
                          html`<uui-tag size="s" slot="tag" color="positive"
                            >${this.localize.term("content_published")}</uui-tag
                          >`,
                      )}
                    </uui-ref-node>
                  `,
                )}
              </uui-ref-list>
            </uui-box>
          `,
        )}
        <uui-button
          slot="actions"
          label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
      </umb-body-layout>
    `;
  }
}

export default FormsPreviewModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsPreviewModalElement;
  }
}
