import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import { customElement, html } from "@umbraco-cms/backoffice/external/lit";

const elementName = "forms-settings-captions";

@customElement(elementName)
export class FormsSettingsCaptionsElement extends FormsSettingsElementBaseElement {
  render() {
    return html` <umb-property-layout
      alias="captions"
      .label=${this.localize.term("formSettings_captions")}
      .description=${this.localize.term("formSettings_captionsDescription")}
    >
      <div slot="editor">
        <div class="flex">
          <uui-label
            >${this.localize.term(
              "formSettings_captionSubmitButton",
            )}</uui-label
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_captionSubmitButton")}
            .value=${this.form?.submitLabel}
            @change=${(e) => this.onInputChange(e, "submitLabel")}
          ></uui-input>
        </div>
        <div class="flex">
          <uui-label
            >${this.localize.term("formSettings_captionNextButton")}</uui-label
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_captionNextButton")}
            .value=${this.form?.nextLabel}
            @change=${(e) => this.onInputChange(e, "nextLabel")}
          ></uui-input>
        </div>
        <div class="flex">
          <uui-label
            >${this.localize.term(
              "formSettings_captionPreviousButton",
            )}</uui-label
          >
          <uui-input
            type="text"
            .label=${this.localize.term("formSettings_captionPreviousButton")}
            .value=${this.form?.prevLabel}
            @change=${(e) => this.onInputChange(e, "prevLabel")}
          ></uui-input>
        </div>
      </div>
    </umb-property-layout>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsCaptionsElement;
  }
}
