import { FORMS_FORM_ENTITY_TYPE } from "../../../forms/entities.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import {
  LitElement,
  css,
  customElement,
  html,
  property,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { tryExecute } from "@umbraco-cms/backoffice/resources";
import type { BasicForm, UserSecurity } from "@umbraco-forms/generated";
import { FormService } from "@umbraco-forms/generated";

const elementName = "form-grid";

@customElement(elementName)
export class FormsDashboardFormGridElement extends UmbElementMixin(LitElement) {
  #formsLimit = 10;
  readonly #increment = 10;

  @property({ type: Object })
  config?: UserSecurity;

  @state()
  _forms: Array<BasicForm> = [];

  async connectedCallback() {
    super.connectedCallback();

    const { data } = await tryExecute(this, FormService.getForm());
    this._forms = data;
  }

  #editForm(id: string) {
    history.pushState(
      {},
      "",
      `/umbraco/section/forms/workspace/${FORMS_FORM_ENTITY_TYPE}/edit/${id}`,
    );
  }

  #viewSubmissions(id: string) {
    history.pushState(
      {},
      "",
      `/umbraco/section/forms/workspace/${FORMS_FORM_ENTITY_TYPE}/edit/${id}/view/entries`,
    );
  }

  #showMore() {
    this.#formsLimit += this.#increment;
    this.requestUpdate();
  }

  #renderMoreButton() {
    if (this._forms.length <= this.#formsLimit) return;

    return html` <uui-button
      @click=${this.#showMore}
      .label=${this.localize.term("formsDashboard_showMore")}
    ></uui-button>`;
  }

  render() {
    return html`<div class="header">
        <h4>${this.localize.term("formsDashboard_yourForms")}</h4>
        ${this.#renderMoreButton()}
      </div>
      <uui-ref-list>
        ${this._forms
          .slice(0, this.#formsLimit - 1)
          .map(
            (form) =>
              html`<ref-form
                .model=${form}
                .config=${this.config}
                @edit=${() => this.#editForm(form.id)}
                @view=${() => this.#viewSubmissions(form.id)}
              ></ref-form>`,
          )}
      </uui-ref-list>
      <div class="footer">${this.#renderMoreButton()}</div>`;
  }

  static styles = [
    css`
      .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .footer {
        margin-top: 10px;
        width: 100%;
        text-align: right;
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsDashboardFormGridElement;
  }
}
