import type { FORMS_FORM_WORKSPACE_CONTEXT } from "./form-workspace.context-token.js";
import type { FormsFormWorkspaceContext } from "./form-workspace.context.js";
import { FormDesignScaffoldsGenerator } from "./form-design-scaffolds-generator.class.js";
import {
  partialUpdateFrozenArray,
  filterFrozenArray,
  appendToFrozenArray,
  assignToFrozenObject,
} from "@umbraco-cms/backoffice/observable-api";
import { UmbId } from "@umbraco-cms/backoffice/id";
import { umbConfirmModal } from "@umbraco-cms/backoffice/modal";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import type {
  Field,
  FieldSet,
  FieldsetContainer,
  Page,
} from "@umbraco-forms/generated";

export class FormStructureManager {
  #workspaceContext: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;
  #host: UmbControllerHost;

  constructor(
    host: UmbControllerHost,
    workspaceContext?: FormsFormWorkspaceContext,
  ) {
    if (!workspaceContext) throw new Error("workspaceContext is missing");
    if (!host) throw new Error("host is missing");

    this.#host = host;
    this.#workspaceContext = workspaceContext;
  }

  getHostElement() {
    return this.#host.getHostElement();
  }

  // TODO => localize
  async #requestDelete(headline: string, action: () => void) {
    await umbConfirmModal(this.#host, {
      headline: `Delete ${headline}`,
      content: `Are you sure you want to delete this ${headline}?`,
      confirmLabel: "Delete",
      color: "danger",
    });

    action();
  }

  addFormPage(addToTop: boolean = false) {
    const data = this.#workspaceContext.getData();
    if (!data) return;

    const page = FormDesignScaffoldsGenerator.getPageScaffold();
    page.form = data.unique;

    const pages = [...this.#workspaceContext.getData()!.pages];
    if (addToTop) {
      pages.unshift(page);
    } else {
      pages.push(page);
    }
    this.#workspaceContext.setFormProperty("pages", pages);
  }

  async deleteFormPage(page?: Page) {
    if (!page) throw new Error("page is missing");

    this.#requestDelete("page", () => {
      const pages = this.#workspaceContext.getData()!.pages;
      this.#workspaceContext.setFormProperty(
        "pages",
        filterFrozenArray(pages, (p) => p.id !== page.id),
      );
    });
  }

  addFormGroup(page?: Page) {
    if (!page) throw new Error("page is missing");

    const fieldset = FormDesignScaffoldsGenerator.getFieldsetScaffold();
    fieldset.sortOrder = page.fieldSets.length;
    fieldset.page = page.id;

    this.#updatePage(page, fieldset);
  }

  deleteFormGroup(fieldset?: FieldSet) {
    if (!fieldset) throw new Error("fieldset is missing");

    this.#requestDelete("group", () => {
      const page = this.#getPageByUnique(fieldset.page);
      const fieldSets = [...page.fieldSets];
      fieldSets.splice(fieldSets.indexOf(fieldset), 1);

      this.#workspaceContext.setFormProperty(
        "pages",
        partialUpdateFrozenArray(
          this.#workspaceContext.getData()!.pages,
          {
            ...page,
            ...{
              fieldSets,
            },
          },
          (p) => p.id === page.id,
        ),
      );
    });
  }

  copyFormGroup(fieldset?: FieldSet) {
    if (!fieldset) throw new Error("fieldset is missing");

    const page = this.#getPageByUnique(fieldset.page);
    const copy = structuredClone(fieldset);

    copy.sortOrder = page.fieldSets.length;
    copy.page = page.id;
    copy.id = UmbId.new();

    // ensure all fields have unique ids after cloning
    copy.containers.forEach((c) =>
      c.fields.forEach((f) => (f.id = UmbId.new())),
    );

    this.#updatePage(page, copy);
  }

  deleteQuestion(field: Field, fieldsetId?: string, pageId?: string) {
    if (!fieldsetId) throw new Error("fieldsetId is missing");
    if (!pageId) throw new Error("pageId is missing");

    this.#requestDelete("question", () => {
      const { page, fieldset, container } = this.#getAncestors(
        field,
        fieldsetId,
        pageId,
      );

      // from inside out, this updates the field array by filtering out the provided field,
      // then updates the containers with the updated fields data,
      // then updates the fieldset with the modified container, and finally
      // updates the page with the rebuilt data model
      this.#updatePage(page!, {
        ...fieldset,
        ...{
          containers: partialUpdateFrozenArray(
            fieldset!.containers,
            {
              ...container,
              ...{
                fields: filterFrozenArray(
                  container!.fields,
                  (f) => f.id !== field.id,
                ),
              },
            },
            (c) => c === container,
          ),
        },
      } as FieldSet);
    });
  }

  copyQuestion(field: Field, fieldsetId?: string, pageId?: string) {
    if (!fieldsetId) throw new Error("fieldsetId is missing");
    if (!pageId) throw new Error("pageId is missing");

    const copy = structuredClone(field);
    copy.id = UmbId.new();
    copy.alias = this.#generateCopiedAlias(
      field.alias,
      this.#workspaceContext.getAllFieldAliases(),
    );

    const { fieldset, container } = this.#getAncestors(
      field,
      fieldsetId,
      pageId,
    );

    this.insertQuestion(
      copy,
      fieldset,
      container,
      container!.fields.indexOf(field) + 1,
    );
  }

  #generateCopiedAlias(alias: string, existingFieldAliases: Array<string>) {
    let result = "";

    // Check if the string already ends with a number, if so increment.
    const matches = alias.match(/\d+$/);
    if (matches) {
      const prefix = alias.substring(0, alias.length - matches[0].length);
      const existingNumberSuffix = parseInt(matches[0], 10);
      const newNumberSuffix = existingNumberSuffix + 1;
      result = prefix + newNumberSuffix;
    } else {
      // Otherwise just suffix with '2'
      result = alias + "2";
    }

    // Check it's not an existing alias.
    let clashesWithExistingAlias = false;
    for (let i = 0; i < existingFieldAliases.length; i++) {
      if (existingFieldAliases[i] === result) {
        clashesWithExistingAlias = true;
        break;
      }
    }

    // If it matches an existing alias, recursively call to generate another.  Otherwise return the new, unique alias.
    return clashesWithExistingAlias
      ? this.#generateCopiedAlias(result, existingFieldAliases)
      : result;
  }

  /**
   * Handles inserting a copied field into the correct fieldset
   */
  insertQuestion(
    field: Field,
    fieldset?: FieldSet,
    container?: FieldsetContainer,
    idx?: number,
  ) {
    if (!fieldset) throw new Error("fieldset is missing");
    if (!container) throw new Error("container is missing");

    const containerIdx = fieldset.containers.findIndex((c) => c === container);
    const fields = appendToFrozenArray(container.fields, field, (f) => f.id);

    // if index is provided, we need to shuffle the array
    if (idx !== undefined) {
      fields.splice(idx, 0, fields.pop()!);
    }

    this.#updateContainerFields(fieldset, container, containerIdx, fields);
  }

  /** returns an array of objects sorted against the sortedIds array */
  reorderArray<T extends { id: string }>(
    arr: Array<T>,
    sortedIds: Array<string>,
  ) {
    const clone = [...arr];
    clone.sort((a, b) => sortedIds.indexOf(a.id) - sortedIds.indexOf(b.id));
    return clone;
  }

  reorderGroups(page: Page, sortedFieldIds: Array<string>) {
    this.#workspaceContext!.setFormProperty(
      "pages",
      partialUpdateFrozenArray(
        this.#workspaceContext.getData()!.pages,
        assignToFrozenObject(page, {
          fieldSets: this.reorderArray<FieldSet>(
            page.fieldSets,
            sortedFieldIds,
          ),
        }),
        (p) => p.id === page.id,
      ),
    );
  }

  reorderQuestions(
    fieldset: FieldSet,
    container: FieldsetContainer,
    sortedFieldIds: Array<string>,
  ) {
    const fields = this.reorderArray<Field>(container.fields, sortedFieldIds);

    const containerIdx = fieldset.containers.findIndex((c) => c === container);
    this.#updateContainerFields(fieldset, container, containerIdx, fields);
  }

  #updateContainerFields(
    fieldset: FieldSet,
    container: FieldsetContainer,
    containerIdx: number,
    fields: Array<Field>,
  ) {
    const unfrozenContainer = {
      ...container,
      ...{
        fields,
      },
    };

    const updatedFieldset = {
      ...fieldset,
      ...{
        containers: partialUpdateFrozenArray(
          fieldset.containers,
          unfrozenContainer,
          (c) => fieldset.containers.indexOf(c) === containerIdx,
        ),
      },
    };

    const form = this.#workspaceContext.getData();
    const page = form!.pages.find((p) => p.id === updatedFieldset.page);

    this.#updatePage(page!, updatedFieldset);
  }

  #getAncestors(field: Field, fieldsetId: string, pageId: string) {
    function byId<T>(arr: Array<T>, searcher: (x) => boolean) {
      return arr.length === 1 ? arr[0] : arr.find(searcher);
    }

    const form = this.#workspaceContext!.getData()!;
    const page = byId(form.pages, (x) => x.id === pageId);
    const fieldset = byId(page!.fieldSets, (x) => x.id === fieldsetId);
    const container = byId(fieldset!.containers, (x) =>
      x.fields.some((f) => f.id === field.id),
    );

    return { page, fieldset, container };
  }

  #updatePage(page: Page, fieldset: FieldSet) {
    const unfrozenPage = {
      ...page,
      ...{
        fieldSets: appendToFrozenArray(page.fieldSets, fieldset, (f) => f.id),
      },
    };
    this.#workspaceContext!.setFormProperty(
      "pages",
      partialUpdateFrozenArray(
        this.#workspaceContext.getData()!.pages,
        unfrozenPage,
        (p) => p.id === unfrozenPage.id,
      ),
    );
  }

  #getPageByUnique(unique: string) {
    const page = this.#workspaceContext
      .getData()!
      .pages.find((p) => p.id === unique);
    if (!page) throw new Error("page is missing");
    return page;
  }
}

export const FORMS_STRUCTURE_MANAGER =
  new UmbContextToken<FormStructureManager>("FormStructureManager");
