import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import { customElement, html } from "@umbraco-cms/backoffice/external/lit";

const elementName = "forms-settings-moderation";

@customElement(elementName)
export class FormsSettingsModerationElement extends FormsSettingsElementBaseElement {
  readonly alias = "manualApproval";

  render() {
    return html` <umb-property-layout
      .alias=${this.alias}
      .label=${this.localize.term("formSettings_moderation")}
      .description=${this.localize.term("formSettings_moderationDescription")}
    >
      <div slot="editor">
        ${this.renderToggle(
          this.form?.manualApproval,
          this.alias,
          "formSettings_enablePostModeration",
        )}
      </div>
    </umb-property-layout>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsModerationElement;
  }
}
