import { FormsSorterConfig } from "../../section/forms-sorter-config.js";
import {
  css,
  customElement,
  html,
  property,
  state,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import { umbConfirmModal } from "@umbraco-cms/backoffice/modal";
import { UmbSorterController } from "@umbraco-cms/backoffice/sorter";
import type { FieldPrevalue } from "@umbraco-forms/generated";

const elementName = "form-edit-prevalues";

@customElement(elementName)
export class FormsEditPrevaluesElement extends UmbLitElement {
  #sortModel: Array<string> = [];

  #sorter = new UmbSorterController(this, {
    ...new FormsSorterConfig(
      "Forms.SorterIdentifier.Prevalues",
      "tr.prevalue-row",
      "tbody.prevalues-container",
    ).config,
    onChange: ({ model }) => {
      this.#sortModel = model;
    },
    onEnd: () => {
      const value = structuredClone(this._value);
      value.sort(
        (a, b) =>
          this.#sortModel.indexOf(a.value) - this.#sortModel.indexOf(b.value),
      );
      this._value = value;
      this.#dispatchChangeEvent();
    },
  });

  private _value: Array<FieldPrevalue> = [];

  @property({ type: Array })
  set value(value: Array<FieldPrevalue>) {
    this._value = structuredClone(value);
    this.#setSortModel();
  }
  get value(): Array<FieldPrevalue> {
    return this._value;
  }

  @state()
  _editIndex = -1;

  #setSortModel() {
    this.#sortModel = this._value.map((pv) => pv.value);
    this.#sorter.setModel(this.#sortModel);
  }

  #isEditing() {
    return this._editIndex > -1;
  }

  #getValueElement() {
    return this.#getInputElement("value");
  }

  #getCaptionElement() {
    return this.#getInputElement("caption");
  }

  #getInputElement(id: string) {
    return this.shadowRoot?.getElementById(id) as HTMLInputElement;
  }

  #clearEditing() {
    this.#getValueElement().value = "";
    this.#getCaptionElement().value = "";
    this._editIndex = -1;
  }

  #onEditClick(index: number) {
    this._editIndex = index;

    const prevalue = this._value[this._editIndex];

    this.#getValueElement().value = prevalue.value;
    this.#getCaptionElement().value = prevalue.caption || "";
  }

  #onSaveClick() {
    const value = this.#getValueElement().value;
    const caption = this.#getCaptionElement().value;

    if (this.#isEditing()) {
      this._value[this._editIndex].value = value;
      this._value[this._editIndex].caption = caption;
    } else {
      this._value.push({
        value,
        caption,
      });
    }

    this.#clearEditing();
    this.#dispatchChangeEvent();
  }

  #onCancelClick() {
    this.#clearEditing();
  }

  async #onDeleteClick(index: number) {
    await umbConfirmModal(this, {
      headline: this.localize.term("formPrevalues_deletePrevalueHeadline"),
      content: this.localize.term("formPrevalues_deletePrevalueMessage"),
      confirmLabel: this.localize.term("general_yes"),
      color: "danger",
    });

    this._value.splice(index, 1);
    this.#dispatchChangeEvent();
  }

  #dispatchChangeEvent() {
    this.requestUpdate();
    this.dispatchEvent(
      new CustomEvent("change", { composed: true, bubbles: true }),
    );
  }

  render() {
    return html` <table>
      <thead>
        <tr>
          <th></th>
          <th>Value</th>
          <th>Caption</th>
          <th></th>
        </tr>
      </thead>
      <tbody class="prevalues-container">
        ${this.value.map(
          (pv, idx) =>
            html`<tr class="prevalue-row" sort-unique=${pv.value}>
              <td><uui-icon name="icon-navigation"></uui-icon></td>
              <td>${pv.value}</td>
              <td>${pv.caption}</td>
              <td>
                <uui-action-bar>
                  <uui-button
                    label="edit"
                    look="secondary"
                    color="default"
                    @click=${() => this.#onEditClick(idx)}
                  >
                    <uui-icon name="edit"></uui-icon>
                  </uui-button>
                  <uui-button
                    label=${this.localize.term("general_delete")}
                    look="secondary"
                    color="default"
                    @click=${() => this.#onDeleteClick(idx)}
                  >
                    <uui-icon name="delete"></uui-icon>
                  </uui-button>
                </uui-action-bar>
              </td>
            </tr>`,
        )}

        <tr>
          <td></td>
          <td>
            <uui-input
              id="value"
              name="value"
              type="text"
              size="30"
              maxlength="255"
              .placeholder=${this.localize.term("formPrevalues_newValue")}
            ></uui-input>
          </td>
          <td>
            <uui-input
              id="caption"
              name="caption"
              type="text"
              size="30"
              maxlength="255"
              .placeholder=${this.localize.term("formPrevalues_newCaption")}
            ></uui-input>
          </td>
          <td>
            <uui-action-bar>
              <uui-button
                label="add"
                look="secondary"
                color="default"
                @click=${this.#onSaveClick}
              >
                <uui-icon
                  .name=${this.#isEditing() ? "icon-save" : "add"}
                ></uui-icon>
              </uui-button>
              ${when(
                this.#isEditing(),
                () =>
                  html`<uui-button
                    label="add"
                    look="secondary"
                    color="default"
                    @click=${this.#onCancelClick}
                    ><uui-icon name="wrong"></uui-icon
                  ></uui-button>`,
              )}
            </uui-action-bar>
          </td>
        </tr>
      </tbody>
    </table>`;
  }

  static styles = [
    css`
      table {
        width: 100%;
      }

      th:last-child {
        width: 120px;
      }

      /* match padding to uui-input */
      td:not(:has(uui-input, uui-button)) {
        padding: var(--uui-size-1, 3px) var(--uui-size-space-3, 9px);
      }

      th {
        text-align: left;
      }

      .prevalue-row {
        cursor: move;
      }
    `,
  ];
}

export default FormsEditPrevaluesElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsEditPrevaluesElement;
  }
}
