import { FormsFormDetailRepository } from "./form-detail.repository.js";
import { FormsFormDetailStore } from "./form-detail.store.js";
import { FormsFormFolderDetailRepository } from "./form-folder-detail.repository.js";
import { FormsFormFolderDetailStore } from "./form-folder-detail.store.js";
import type {
  ManifestRepository,
  ManifestStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_FORM_DETAIL_REPOSITORY_ALIAS =
  "Forms.Repository.Form.Detail";
export const FORMS_FORM_DETAIL_STORE_ALIAS = "Forms.Store.Form.Detail";

export const FORMS_FORM_FOLDER_DETAIL_REPOSITORY_ALIAS =
  "Forms.Repository.Folder.Detail";
export const FORMS_FORM_FOLDER_DETAIL_STORE_ALIAS = "Forms.Store.Folder.Detail";

const repositories: Array<ManifestRepository> = [
  {
    type: "repository",
    alias: FORMS_FORM_DETAIL_REPOSITORY_ALIAS,
    name: "Form Detail Repository",
    api: FormsFormDetailRepository,
  },
  {
    type: "repository",
    alias: FORMS_FORM_FOLDER_DETAIL_REPOSITORY_ALIAS,
    name: "Form Folder Detail Repository",
    api: FormsFormFolderDetailRepository,
  },
];

const stores: Array<ManifestStore> = [
  {
    type: "store",
    alias: FORMS_FORM_DETAIL_STORE_ALIAS,
    name: "Form Detail Store",
    api: FormsFormDetailStore,
  },
  {
    type: "store",
    alias: FORMS_FORM_FOLDER_DETAIL_STORE_ALIAS,
    name: "Form Folder Detail Store",
    api: FormsFormFolderDetailStore,
  },
];

export const manifests = [...repositories, ...stores];
