import type { FormsSettingValueConverterApi } from "./setting-value-converter.interface.js";
import type { UmbPropertyValueData } from "@umbraco-cms/backoffice/property";
import type { Setting } from "@umbraco-forms/generated";
import type { UmbPropertyEditorConfig } from "@umbraco-cms/backoffice/property-editor";

export class FormsSliderSettingValueConverter
  implements FormsSettingValueConverterApi
{
  async getSettingValueForEditor(
    setting: Setting,
    alias: string,
    value: string,
  ) {
    // Multiply by 10 to get the integer value we need for the editor.
    if (!isNaN(parseFloat(value))) {
      const editorValue = Math.trunc(parseFloat(value) * 10);
      return Promise.resolve({ from: editorValue, to: editorValue });
    }

    return Promise.resolve(undefined);
  }

  async getSettingValueForPersistence(
    setting: Setting,
    valueData: UmbPropertyValueData,
  ) {
    // Divide by 10 to get the 0.0 to 1.0 range we actually want.
    const value = (
      (valueData.value ? parseInt(valueData.value["from"]) : 5) / 10
    ).toFixed(1);
    return Promise.resolve(value);
  }

  async getSettingPropertyConfig(
    setting: Setting,
    alias: string,
    values: UmbPropertyValueData[],
  ) {
    const config: UmbPropertyEditorConfig = [];

    // Min, max, step and default are provided in prevalues.
    // As the slider only supports integers, we have to multiply by 10 for the UI and then divide again when we save.
    config.push({
      alias: "enableRange",
      value: false,
    });

    let settingValue =
      values.find((s) => s.alias === alias)?.value?.toString() || "";
    if (isNaN(parseFloat(settingValue))) {
      settingValue = "";
    }

    if (setting.prevalues.length >= 1) {
      config.push({
        alias: "minVal",
        value: parseFloat(setting.prevalues[0]) * 10,
      });
      if (setting.prevalues.length >= 2) {
        config.push({
          alias: "maxVal",
          value: parseFloat(setting.prevalues[1]) * 10,
        });
        if (setting.prevalues.length >= 3) {
          config.push({
            alias: "step",
            value: parseFloat(setting.prevalues[2]) * 10,
          });
          if (setting.prevalues.length >= 3 && settingValue.length === 0) {
            config.push({
              alias: "initVal1",
              value: parseFloat(setting.prevalues[3]) * 10,
            });
          } else {
            config.push({
              alias: "initVal1",
              value: parseFloat(settingValue),
            });
          }
        }
      }
    }

    return Promise.resolve(config);
  }

  destroy(): void {}
}
