import { FORMS_FORM_WORKSPACE_CONTEXT } from "../../../form-workspace.context-token.js";
import { FORMS_STRUCTURE_MANAGER } from "../../../form-structure-manager.class.js";
import { FormDesignScaffoldsGenerator } from "../../../form-design-scaffolds-generator.class.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import { LitElement, property } from "@umbraco-cms/backoffice/external/lit";
import { UMB_MODAL_MANAGER_CONTEXT } from "@umbraco-cms/backoffice/modal";
import type {
  Field,
  FieldPreValueSource,
  FieldTypeWithSettings,
  ValidationPattern,
} from "@umbraco-forms/generated";
import type { FormsEditFieldModalValue } from "@umbraco-forms/modals";
import { FORMS_EDIT_FIELD_MODAL } from "@umbraco-forms/modals";

export abstract class FormsFormDesignElementBaseElement extends UmbElementMixin(
  LitElement,
) {
  workspaceContext?: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;
  modalContext?: typeof UMB_MODAL_MANAGER_CONTEXT.TYPE;
  structureManager?: typeof FORMS_STRUCTURE_MANAGER.TYPE;

  @property({ type: Number })
  index: number = 0;

  @property({ type: Array })
  allFields: Array<Field> = [];

  @property({ type: Array })
  allFieldTypes: Array<FieldTypeWithSettings> = [];

  #sortModeActive = false;

  @property({ type: Boolean, reflect: true, attribute: "sort-mode-active" })
  set sortModeActive(val: boolean) {
    this.#sortModeActive = val;

    if (this.#sortModeActive) {
      this.sortModeEnabled?.();
    } else {
      this.sortModeDisabled?.();
    }
  }

  get sortModeActive() {
    return this.#sortModeActive;
  }

  sortModeEnabled?(): void;
  sortModeDisabled?(): void;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_WORKSPACE_CONTEXT, (instance) => {
      this.workspaceContext = instance;
    });

    this.consumeContext(UMB_MODAL_MANAGER_CONTEXT, (instance) => {
      this.modalContext = instance;
    });

    this.consumeContext(FORMS_STRUCTURE_MANAGER, (instance) => {
      this.structureManager = instance;
    });
  }

  async editNewOrExistingField(
    field: Field,
    isNew: boolean,
    onSubmit: (value: FormsEditFieldModalValue) => Promise<void>,
  ) {
    if (!this.modalContext) return;

    const fieldType = await this.workspaceContext!.loadFieldType(
      field.fieldTypeId,
    );
    if (!fieldType) {
      throw new Error(
        "Field type with id " + field.fieldTypeId + " could not be found.",
      );
    }

    const pages = this.workspaceContext!.getAllPages();

    let validationPatterns: Array<ValidationPattern> = [];
    if (fieldType.supportsRegex) {
      const validationPatternsResponse =
        await this.workspaceContext!.loadValidationPatterns();
      if (validationPatternsResponse) {
        validationPatterns = validationPatternsResponse;
      }
    }

    let prevalueSources: Array<FieldPreValueSource> = [];
    if (fieldType.supportsPrevalues) {
      const prevalueSourcesResponse =
        await this.workspaceContext!.loadPrevalueSources();
      if (prevalueSourcesResponse) {
        prevalueSources = prevalueSourcesResponse;
      }
    }

    const caption = field.caption;
    const alias = field.alias;
    const tooltip = field.tooltip;
    const containsSensitiveData = field.containsSensitiveData;
    const allowedUploadTypes = field.allowedUploadTypes;
    const allowMultipleFileUploads = field.allowMultipleFileUploads;
    const prevalues = field.preValues;
    const prevalueSourceId = field.prevalueSourceId;
    const mandatory = field.mandatory;
    const requiredErrorMessage = field.requiredErrorMessage;
    const regex = field.regex;
    const invalidErrorMessage = field.invalidErrorMessage;
    const condition = field.condition
      ? structuredClone(field.condition)
      : FormDesignScaffoldsGenerator.getConditionScaffold();
    const settings = field.settings;

    // Initialize settings based on configured default values.
    if (isNew) {
      for (let i = 0; i < fieldType.settings.length; i++) {
        const setting = fieldType.settings[i];
        if (setting.defaultValue) {
          settings[setting.alias] = setting.defaultValue;
        }
      }
    }

    const containerIndexPath =
      this.workspaceContext!.getContainerIndexPathForField(field.id);

    const modalHandler = this.modalContext.open(this, FORMS_EDIT_FIELD_MODAL, {
      data: {
        fields: this.allFields,
        pages,
        validationPatterns,
        prevalueSources,
        isNew,
      },
      value: {
        caption,
        alias,
        tooltip,
        fieldType,
        containsSensitiveData,
        allowedUploadTypes,
        allowMultipleFileUploads,
        prevalues,
        prevalueSourceId,
        mandatory,
        requiredErrorMessage,
        regex,
        invalidErrorMessage,
        condition,
        settings,
        containerIndexPath,
      },
    });

    modalHandler
      .onSubmit()
      .then(onSubmit)
      .catch(() => undefined);
  }
}
