import type { OperatorType } from "../../../../types.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import {
  LitElement,
  customElement,
  html,
  unsafeHTML,
  when,
  property,
  css,
} from "@umbraco-cms/backoffice/external/lit";
import type { Field, FieldCondition } from "@umbraco-forms/generated";

const elementName = "forms-form-condition-summary";

@customElement(elementName)
export class FormsFormConditionSummaryElement extends UmbElementMixin(
  LitElement,
) {
  @property({ type: Object })
  condition?: FieldCondition;

  @property({ type: Array })
  allFields: Array<Field> = [];

  @property({ type: Array })
  operatorTypes: Array<OperatorType> = [];

  @property()
  formElement = "field";

  #getFieldCaption(id: string) {
    return this.allFields.find((f) => f.id === id)?.caption;
  }

  #getRuleOperatorName(value: string) {
    return this.operatorTypes.find((o) => o.value === value)?.name;
  }

  render() {
    return html` ${when(
      this.condition?.enabled,
      () =>
        html`<div class="condition-summary">
          ${unsafeHTML(
            `<span>${this.localize.term("formConditions_" + this.formElement + "ConditionStatus", this.condition?.actionType, this.condition?.logicType.toLowerCase())}</span`,
          )}
          ${this.condition?.rules.map(
            (rule, idx) =>
              html`<span>
              <strong>${this.#getFieldCaption(rule.field)}</strong>
              ${this.#getRuleOperatorName(rule.operator)}</span>
              <strong>${
                rule.value.length > 0
                  ? html`${rule.value}`
                  : this.localize.term("formConditions_empty")
              }</strong>${when(idx < this.condition!.rules.length - 1, () => html`<span>, </span>`)}
            </span>`,
          )}
        </div>`,
    )}`;
  }

  static styles = [
    css`
      .condition-summary {
        font-size: 12px;
        margin-top: 8px;
        padding-left: 6px;
        padding-right: 6px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        background: #f6f3fd;
        border: 1px solid #413659;
        border-radius: 3px;
        display: inline-block;
        max-width: 800px;

        span {
          font-style: italic;
        }
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormConditionSummaryElement;
  }
}
