import { FORMS_FORM_ENTRY_ENTITY_TYPE } from "../../../entities.js";
import type { FormSettingsConditionConfig } from "../../../conditions/index.js";
import { FORMS_FORM_SETTINGS_CONDITION } from "../../../conditions/manifests.js";
import type { FormSettings } from "../../../conditions/types.js";
import { FORMS_FORM_ENTRY_COLLECTION_ALIAS } from "../../../entries/collection/manifests.js";
import type { FormsSecurityPermissionConditionConfig } from "../../../../security/conditions/index.js";
import { FORMS_SECURITY_PERMISSION_CONDITION } from "../../../../security/conditions/manifests.js";
import { FormsFormEntryDeleteEntityBulkAction } from "./entity-bulk-actions/delete/delete.action.js";
import { FormsFormEntryApproveEntityBulkAction } from "./entity-bulk-actions/approve/approve.action.js";
import { FormsFormEntryRejectEntityBulkAction } from "./entity-bulk-actions/reject/reject.action.js";
import type { FormSecurityForUser } from "@umbraco-forms/generated";
import type { ManifestEntityBulkAction } from "@umbraco-cms/backoffice/extension-registry";
import { UMB_COLLECTION_ALIAS_CONDITION } from "@umbraco-cms/backoffice/collection";

// FormSettingsConditionConfig is necessary as ConditionType is defined in core, and we don't have the ability to extend it.

// TODO: In Forms 13 these bulk actions are requested from the server, as they are based on collection builders and could be added to or removed
// from there.
// They are exposed at: /umbraco/forms/management/api/v1/record-set-actions
// But currently we aren't doing anything with these, they are hardcoded in the manifests below.
// Ideally we would generate these manifests dynamically from the data provided from the server.
const bulkActionsData = [
  {
    name: "Approve",
    weight: 100,
    api: FormsFormEntryApproveEntityBulkAction,
    conditions: [
      {
        alias: FORMS_FORM_SETTINGS_CONDITION,
        match: (form: FormSettings) => form.manualApproval,
      } as FormSettingsConditionConfig,
    ],
  },
  {
    name: "Reject",
    weight: 90,
    api: FormsFormEntryRejectEntityBulkAction,
    conditions: [
      {
        alias: FORMS_FORM_SETTINGS_CONDITION,
        match: (form: FormSettings) => form.manualApproval,
      } as FormSettingsConditionConfig,
    ],
  },
  {
    name: "Delete",
    weight: 10,
    api: FormsFormEntryDeleteEntityBulkAction,
    conditions: [
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.deleteEntries,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
];

const bulkActions: Array<ManifestEntityBulkAction> = bulkActionsData.map(
  (data) => ({
    type: "entityBulkAction",
    alias: `Forms.EntityBulkAction.Entry.${data.name}`,
    name: `${data.name} Form Entry Bulk Action`,
    weight: data.weight,
    api: data.api,
    meta: {
      label: data.name,
    },
    forEntityTypes: [FORMS_FORM_ENTRY_ENTITY_TYPE],
    conditions: [
      {
        alias: UMB_COLLECTION_ALIAS_CONDITION,
        match: FORMS_FORM_ENTRY_COLLECTION_ALIAS,
      },
      ...(data.conditions ?? []),
    ],
  }),
);

export const manifests = [...bulkActions];
