import type { SubmitMessageDetail } from "../../section/forms/workspace/views/design/types.js";
import type { FormsEditWorkflowModalValue } from "../token/index.js";
import {
  FORMS_CHOOSE_WORKFLOW_TYPE_MODAL,
  FORMS_EDIT_SUBMIT_MESSAGE_MODAL,
  FORMS_EDIT_WORKFLOW_MODAL,
} from "../token/index.js";
import { FORMS_FORM_WORKSPACE_CONTEXT } from "../../section/forms/workspace/form-workspace.context-token.js";
import { FormDesignScaffoldsGenerator } from "../../section/forms/workspace/form-design-scaffolds-generator.class.js";
import { FormsSorterConfig } from "../../section/forms-sorter-config.js";
import { UmbSorterController } from "@umbraco-cms/backoffice/sorter";
import { UMB_MODAL_MANAGER_CONTEXT } from "@umbraco-cms/backoffice/modal";
import {
  IncludeSensitiveData,
  type Field,
  type FormWorkflowWithTypeSettings,
} from "@umbraco-forms/generated";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import {
  css,
  customElement,
  html,
  property,
  when,
} from "@umbraco-cms/backoffice/external/lit";

const elementName = "form-configure-workflow-stage";

@customElement(elementName)
export class FormsConfigureWorkflowStageElement extends UmbLitElement {
  #modalContext?: typeof UMB_MODAL_MANAGER_CONTEXT.TYPE;
  #workspaceContext?: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;

  @property()
  collectionName = "";

  @property({ type: Array })
  set workflows(value: Array<FormWorkflowWithTypeSettings>) {
    this.#workflows = structuredClone(value);
    for (let index = 0; index < this.#workflows.length; index++) {
      const workflow = this.#workflows[index];
      this.#workflowStages[workflow.id] = this.collectionName;
    }
    this.#setSorter();
    this.#setSortModel();
  }

  get workflows(): Array<FormWorkflowWithTypeSettings> {
    return this.#workflows;
  }

  #workflows: Array<FormWorkflowWithTypeSettings> = [];

  @property()
  get workflowStages(): Record<string, string> {
    return this.#workflowStages;
  }

  #workflowStages: Record<string, string> = {};

  @property({ type: Object })
  set submitMessageDetail(value: SubmitMessageDetail) {
    this.#submitMessageDetail = structuredClone(value);
  }

  get submitMessageDetail(): SubmitMessageDetail | undefined {
    return this.#submitMessageDetail;
  }

  #submitMessageDetail?: SubmitMessageDetail;

  @property()
  label = "";

  @property()
  description = "";

  @property()
  icon = "";

  @property({ type: Array })
  allFields: Array<Field> = [];

  #sortModel: Array<string> = [];

  #sorter?: UmbSorterController<string, HTMLElement>;

  constructor() {
    super();

    this.consumeContext(UMB_MODAL_MANAGER_CONTEXT, (instance) => {
      this.#modalContext = instance;
    });
    this.consumeContext(FORMS_FORM_WORKSPACE_CONTEXT, (instance) => {
      this.#workspaceContext = instance;
    });
  }

  #setSorter() {
    this.#sorter = new UmbSorterController(this, {
      ...new FormsSorterConfig(
        "Forms.SorterIdentifier.Workflows",
        `.workflow-${this.collectionName}`,
        `.workflows-${this.collectionName}`,
      ).config,

      onChange: ({ model }) => {
        this.#sortModel = model;
      },
      onEnd: () => {
        this.#workflows = [...this.#workflows].sort(
          (a, b) =>
            this.#sortModel.indexOf(a.id) - this.#sortModel.indexOf(b.id),
        );
        this.#dispatchChangeEvent();
      },
    });
  }

  #setSortModel() {
    this.#sortModel = [];

    for (let i = 0; i < this.#workflows.length; i++) {
      const workflow = this.#workflows[i];
      this.#sortModel.push(workflow.id);
    }

    this.#sorter?.setModel(this.#sortModel);
  }

  async #onEditSubmitMessageClick() {
    if (!this.#modalContext) return;
    if (!this.#workspaceContext) return;

    const richTextConfiguration =
      await this.#workspaceContext.getRichTextConfiguration();

    const modalHandler = this.#modalContext.open(
      this,
      FORMS_EDIT_SUBMIT_MESSAGE_MODAL,
      {
        data: {
          richTextConfiguration,
        },
        value: {
          messageOnSubmit: this.#submitMessageDetail?.messageOnSubmit,
          messageOnSubmitIsHtml:
            this.#submitMessageDetail?.messageOnSubmitIsHtml || false,
          goToPageOnSubmit: this.#submitMessageDetail?.goToPageOnSubmit,
        },
      },
    );

    const value = await modalHandler.onSubmit().catch(() => undefined);
    if (!value || !this.#submitMessageDetail) return;

    this.#submitMessageDetail.messageOnSubmit = value.messageOnSubmit;
    this.#submitMessageDetail.messageOnSubmitIsHtml =
      value.messageOnSubmitIsHtml;
    this.#submitMessageDetail.goToPageOnSubmit = value.goToPageOnSubmit;
    this.#dispatchChangeEvent();
  }

  async #onEditWorkflowClick(index: number) {
    if (!this.#modalContext) return;

    const workflow = this.#workflows[index];
    const modalHandler = await this.#openEditWorkflowModal(workflow, false);

    const value = await modalHandler.onSubmit().catch(() => undefined);
    if (!value) return;

    this.#populateEditedWorkflow(this.#workflows[index], value);
    this.#workflowStages[this.#workflows[index].id] = value.collectionName;
    this.#dispatchChangeEvent();
  }

  async #onAddWorkflowClick() {
    if (!this.#modalContext) return;
    if (!this.#workspaceContext) throw new Error("No workspace context");

    // Open a dialog to get the new workflow's type.
    const selectTypeModalHandler = this.#modalContext!.open(
      this,
      FORMS_CHOOSE_WORKFLOW_TYPE_MODAL,
    );

    const value = await selectTypeModalHandler
      .onSubmit()
      .catch(() => undefined);
    if (!value?.selectedValue) return;

    // Create new workflow from scaffold and selected type.
    const workflow = FormDesignScaffoldsGenerator.getWorkflowScaffold();
    workflow.form = this.#workspaceContext?.getUnique();
    workflow.sortOrder = this.#workflows.length;
    workflow.workflowTypeId = value.selectedValue.id;
    workflow.workflowTypeName = value.selectedValue.name;
    workflow.workflowTypeIcon = value.selectedValue.icon;
    workflow.workflowTypeDescription = value.selectedValue.description;
    workflow.workflowTypeGroup = value.selectedValue.group;

    // Open the "edit" modal to populate the details.
    const editModalHandler = await this.#openEditWorkflowModal(workflow, true);
    const editedWorkflow = await editModalHandler
      .onSubmit()
      .catch(() => undefined);
    if (!editedWorkflow) return;

    // Populate and add the new workflow.
    this.#populateEditedWorkflow(workflow, editedWorkflow);

    this.#workflows.push(workflow);
    this.#setSortModel();
    this.#dispatchChangeEvent();
  }

  async #openEditWorkflowModal(
    workflow: FormWorkflowWithTypeSettings,
    isNew: boolean,
  ) {
    const workflowType = await this.#workspaceContext!.loadWorkflowType(
      workflow.workflowTypeId,
    );
    if (!workflowType) {
      throw new Error(
        "Workflow type with id " +
          workflow.workflowTypeId +
          " could not be found.",
      );
    }

    // Initialize settings based on configured default values.
    const settings = workflow.settings;
    if (isNew) {
      for (let i = 0; i < workflowType.settings.length; i++) {
        const setting = workflowType.settings[i];
        if (setting.defaultValue) {
          settings[setting.alias] = setting.defaultValue;
        }
      }
    }

    return this.#modalContext!.open(this, FORMS_EDIT_WORKFLOW_MODAL, {
      data: {
        fields: this.allFields,
        workflowType,
        isNew,
      },
      value: {
        name: workflow.name,
        active: workflow.active,
        includeSensitiveData:
          workflow.includeSensitiveData === IncludeSensitiveData.TRUE,
        collectionName: this.collectionName,
        settings: settings,
        condition: workflow.condition,
      },
    });
  }

  #populateEditedWorkflow(
    workflow: FormWorkflowWithTypeSettings,
    value: FormsEditWorkflowModalValue,
  ) {
    workflow.name = value.name;
    workflow.active = value.active;
    workflow.includeSensitiveData = value.includeSensitiveData
      ? IncludeSensitiveData.TRUE
      : IncludeSensitiveData.FALSE;
    workflow.settings = value.settings;
    workflow.condition = value.condition;
  }

  #onDeleteWorkflowClick(index: number) {
    this.#workflows.splice(index, 1);
    this.#dispatchChangeEvent();
  }

  #dispatchChangeEvent() {
    this.requestUpdate();
    this.dispatchEvent(
      new CustomEvent("change", { composed: true, bubbles: true }),
    );
  }

  #renderStageBlock(args: {
    icon?: string;
    label?: string;
    description?: string;
    iconClass?: string;
  }) {
    return html`<div class="stage-block">
      <uui-icon
        .name=${args.icon ?? null}
        class="stage-icon ${args.iconClass}"
      ></uui-icon>
      <div>
        ${when(args.label, () => html`<strong>${args.label}</strong>`)}
        ${when(
          args.description,
          () => html`<small> ${args.description} </small>`,
        )}
      </div>
    </div>`;
  }

  render() {
    return html` ${this.#renderStageBlock({
        icon: this.icon,
        label: this.label,
        description: this.description,
      })}
      ${when(
        this.submitMessageDetail,
        () =>
          html`<uui-button @click=${this.#onEditSubmitMessageClick}>
            ${this.#renderStageBlock({
              icon: "icon-document",
              label: this.localize.term("formWorkflows_submitMessage"),
              description: this.localize.term(
                "formWorkflows_defaultWorkflowDescription",
              ),
              iconClass: "square",
            })}
          </uui-button>`,
      )}
      <div class="workflows-${this.collectionName}">
        ${this.workflows.map(
          (workflow, idx) =>
            html` <div
              class="sortable-stage workflow-${this.collectionName}"
              sort-unique=${workflow.id}
            >
              <uui-button
                @click=${async () => await this.#onEditWorkflowClick(idx)}
              >
                ${this.#renderStageBlock({
                  icon: workflow.workflowTypeIcon,
                  label: workflow.name,
                  description: workflow.workflowTypeDescription,
                  iconClass: "square",
                })}
              </uui-button>
              ${when(
                !workflow.isMandatory,
                () =>
                  html` <uui-action-bar>
                    <uui-button
                      label=${this.localize.term("general_remove")}
                      @click=${() => this.#onDeleteWorkflowClick(idx)}
                    ></uui-button>
                  </uui-action-bar>`,
              )}
            </div>`,
        )}
      </div>
      <uui-button @click=${this.#onAddWorkflowClick}>
        ${this.#renderStageBlock({
          label: this.localize.term("formWorkflows_addWorkflow"),
          iconClass: "square dashed",
        })}
      </uui-button>`;
  }

  static styles = [
    css`
      :host {
        display: block;
        position: relative;
        z-index: 1;
      }

      :host:after {
        content: "";
        display: block;
        width: 1px;
        background-color: var(--uui-color-border-standalone);
        position: absolute;
        top: var(--uui-size-5);
        bottom: var(--uui-size-5);
        left: 27px;
        z-index: -1;
      }

      uui-button {
        --uui-button-padding-left-factor: 1;
        --uui-button-padding-right-factor: 1;
        --uui-button-padding-bottom-factor: 1;
        --uui-button-padding-top-factor: 1;
        text-align: left;
      }

      /* uui-button internally multiplies the above factors by --uui-size 2,
        but since we're setting the factor to 1, we don't need the calc()
      */
      :host > .stage-block {
        padding: var(--uui-size-2);
      }

      .stage-block {
        display: flex;
        column-gap: var(--uui-size-3);
        align-items: center;
        --icon-radius: 50%;
        --border-style: solid;
      }

      .stage-block > div {
        display: flex;
        flex-direction: column;
        line-height: 1.4;
      }

      .stage-icon {
        position: relative;
        background: white;
        padding: var(--uui-size-3);
        border-radius: var(--icon-radius);
        border-width: 1px;
        border-style: var(--border-style);
        border-color: var(--uui-color-border-standalone);
        width: 22px;
        height: 22px;
      }

      .square {
        --icon-radius: var(--uui-border-radius);
      }

      .dashed {
        --border-style: dashed;
      }

      .sortable-stage {
        display: flex;
        align-items: center;
        cursor: move;
      }

      .sortable-stage > uui-button {
        margin-right: auto;
      }

      uui-action-bar {
        opacity: 0;
        transition: opacity 120ms;
        margin-left: auto;
      }

      .sortable-stage:hover uui-action-bar {
        opacity: 1;
      }
    `,
  ];
}

export default FormsConfigureWorkflowStageElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsConfigureWorkflowStageElement;
  }
}
