import { FORMS_FORM_ENTRY_COLLECTION_CONTEXT } from "./form-entry-collection.context-token.js";
import { UmbTextStyles } from "@umbraco-cms/backoffice/style";
import {
  css,
  html,
  customElement,
  state,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import type { UUIInputEvent } from "@umbraco-cms/backoffice/external/uui";

@customElement("form-entry-filter-text")
export class FormsFormEntryFilterTextElement extends UmbLitElement {
  @state()
  private _filterText = "";

  #collectionContext?: typeof FORMS_FORM_ENTRY_COLLECTION_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_ENTRY_COLLECTION_CONTEXT, (context) => {
      this.#collectionContext = context;
    });
  }

  #onTextChange(e: UUIInputEvent) {
    const value = e.target.value;
    this._filterText = value.toString();
    this.#collectionContext?.setFilter({ filter: this._filterText });
  }

  render() {
    return html`
      <uui-input
        label="Filter entries"
        placeholder=${this.localize.term("formEntries_filterEntries")}
        .value=${this._filterText}
        @change=${this.#onTextChange}
      ></uui-input>
    `;
  }

  static styles = [
    UmbTextStyles,
    css`
      :host,
      uui-input {
        width: 100%;
      }
    `,
  ];
}

export default FormsFormEntryFilterTextElement;

declare global {
  interface HTMLElementTagNameMap {
    "form-entry-filter-text": FormsFormEntryFilterTextElement;
  }
}
