import { FORMS_FORM_ENTITY_TYPE } from "../entities.js";
import type { FormsFormWorkspaceContext } from "./form-workspace.context.js";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbWorkspaceContext } from "@umbraco-cms/backoffice/workspace";

export const FORMS_FORM_WORKSPACE_CONTEXT = new UmbContextToken<
  UmbWorkspaceContext,
  FormsFormWorkspaceContext
>(
  "UmbWorkspaceContext",
  undefined,
  (context): context is FormsFormWorkspaceContext =>
    context.getEntityType?.() === FORMS_FORM_ENTITY_TYPE,
);
