import { FormsSettingsElementBaseElement } from "./forms-settings-element-base.element.js";
import { customElement, html } from "@umbraco-cms/backoffice/external/lit";

const elementName = "forms-settings-store-records";

@customElement(elementName)
export class FormsSettingsStoreRecordsElement extends FormsSettingsElementBaseElement {
  readonly alias = "storeRecordsLocally";

  render() {
    return html` <umb-property-layout
      .alias=${this.alias}
      .label=${this.localize.term("formSettings_storeRecords")}
      .description=${this.localize.term("formSettings_storeRecordsDescription")}
    >
      <div slot="editor">
        ${this.renderToggle(
          this.form.storeRecordsLocally,
          this.alias,
          "formSettings_storeRecordsConfirm",
        )}
      </div>
    </umb-property-layout>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsSettingsStoreRecordsElement;
  }
}
