import { FormsSecurityUserDetailStore } from "./security-user-detail.store.js";
import { FormsSecurityUserDetailRepository } from "./security-user-detail.repository.js";
import type {
  ManifestRepository,
  ManifestStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_SECURITY_USER_DETAIL_REPOSITORY_ALIAS =
  "Forms.Repository.Security.User.Detail";
export const FORMS_SECURITY_USER_DETAIL_STORE_ALIAS =
  "Forms.Store.Security.User.Detail";

const repository: ManifestRepository = {
  type: "repository",
  alias: FORMS_SECURITY_USER_DETAIL_REPOSITORY_ALIAS,
  name: "Form User Source Detail Repository",
  api: FormsSecurityUserDetailRepository,
};

const store: ManifestStore = {
  type: "store",
  alias: FORMS_SECURITY_USER_DETAIL_STORE_ALIAS,
  name: "Form User Security Detail Store",
  api: FormsSecurityUserDetailStore,
};

export const manifests = [repository, store];
