import {
  FORMS_EMAIL_TEMPLATE_ENTITY_TYPE,
  FORMS_EMAIL_TEMPLATE_FOLDER_ENTITY_TYPE,
  FORMS_EMAIL_TEMPLATE_ROOT_ENTITY_TYPE,
} from "../entity.js";
import type {
  ManifestTree,
  ManifestTreeItem,
} from "@umbraco-cms/backoffice/tree";
import type {
  ManifestRepository,
  ManifestTreeStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_EMAIL_TEMPLATE_TREE_REPOSITORY_ALIAS =
  "Forms.Repository.EmailTemplate.Tree";
export const FORMS_EMAIL_TEMPLATE_TREE_STORE_ALIAS =
  "Forms.Store.EmailTemplate.Tree";
export const FORMS_EMAIL_TEMPLATE_TREE_ALIAS = "Forms.Tree.EmailTemplate";

const treeRepository: ManifestRepository = {
  type: "repository",
  alias: FORMS_EMAIL_TEMPLATE_TREE_REPOSITORY_ALIAS,
  name: "Email Template Tree Repository",
  api: () => import("./email-template-tree.repository.js"),
};

const treeStore: ManifestTreeStore = {
  type: "treeStore",
  alias: FORMS_EMAIL_TEMPLATE_TREE_STORE_ALIAS,
  name: "Email Template Tree Store",
  api: () => import("./email-template-tree.store.js"),
};

const tree: ManifestTree = {
  type: "tree",
  kind: "default",
  alias: FORMS_EMAIL_TEMPLATE_TREE_ALIAS,
  name: "Email Template Tree",
  meta: {
    repositoryAlias: FORMS_EMAIL_TEMPLATE_TREE_REPOSITORY_ALIAS,
  },
};

const treeItem: ManifestTreeItem = {
  type: "treeItem",
  kind: "default",
  alias: "Forms.TreeItem.EmailTemplate",
  name: "Email Template Tree Item",
  forEntityTypes: [
    FORMS_EMAIL_TEMPLATE_ROOT_ENTITY_TYPE,
    FORMS_EMAIL_TEMPLATE_ENTITY_TYPE,
    FORMS_EMAIL_TEMPLATE_FOLDER_ENTITY_TYPE,
  ],
};

export const manifests: Array<UmbExtensionManifest> = [
  treeRepository,
  treeStore,
  tree,
  treeItem,
];
