import {
  FORMS_DATASOURCE_ENTITY_TYPE,
  FORMS_DATASOURCE_ROOT_ENTITY_TYPE,
} from "../entities.js";
import type { FormsSecurityPermissionConditionConfig } from "../../security/conditions/security-permission-condition.config.js";
import type { FormSecurityForUser } from "@umbraco-forms/generated";
import { FORMS_SECURITY_PERMISSION_CONDITION } from "../../security/conditions/manifests.js";
import { FormsDataSourceTreeRepository } from "./datasources-tree.repository.js";
import { FormsDataSourceTreeStore } from "./datasources-tree.store.js";
import { manifests as menuManifests } from "./menu-item/manifests.js";
import type {
  ManifestTree,
  ManifestTreeItem,
} from "@umbraco-cms/backoffice/tree";
import type {
  ManifestRepository,
  ManifestTreeStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_DATASOURCE_TREE_REPOSITORY_ALIAS =
  "Forms.Repository.DataSources.Tree";
export const FORMS_DATASOURCE_TREE_STORE_ALIAS = "Forms.Store.DataSources.Tree";
export const FORMS_DATASOURCE_TREE_ALIAS = "Forms.Tree.DataSources";

const treeRepository: ManifestRepository = {
  type: "repository",
  alias: FORMS_DATASOURCE_TREE_REPOSITORY_ALIAS,
  name: "Data Source Tree Repository",
  api: FormsDataSourceTreeRepository,
};

const treeStore: ManifestTreeStore = {
  type: "treeStore",
  alias: FORMS_DATASOURCE_TREE_STORE_ALIAS,
  name: "Data Source Tree Store",
  api: FormsDataSourceTreeStore,
};

const tree: ManifestTree = {
  type: "tree",
  kind: "default",
  alias: FORMS_DATASOURCE_TREE_ALIAS,
  name: "Data Source Tree",
  meta: {
    repositoryAlias: FORMS_DATASOURCE_TREE_REPOSITORY_ALIAS,
  },
  conditions: [
    {
      alias: FORMS_SECURITY_PERMISSION_CONDITION,
      match: (security: FormSecurityForUser) =>
        security.userSecurity.manageDataSources,
    } as FormsSecurityPermissionConditionConfig,
  ],
};

const treeItem: ManifestTreeItem = {
  type: "treeItem",
  kind: "default",
  alias: "Forms.TreeItem.DataSource",
  name: "Data Source Tree Item",
  forEntityTypes: [
    FORMS_DATASOURCE_ROOT_ENTITY_TYPE,
    FORMS_DATASOURCE_ENTITY_TYPE,
  ],
};

export const manifests = [
  treeRepository,
  treeStore,
  tree,
  treeItem,
  ...menuManifests,
];
