import { FORMS_FORM_WORKSPACE_CONTEXT } from "../../../form-workspace.context-token.js";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import { LitElement, css, state } from "@umbraco-cms/backoffice/external/lit";
import type { FormDesign } from "@umbraco-forms/generated";

export class FormsAdvancedElementBaseElement extends UmbElementMixin(
  LitElement,
) {
  @state()
  form!: FormDesign;

  #workspaceContext!: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_WORKSPACE_CONTEXT, (instance) => {
      if (!instance) return;
      this.#workspaceContext = instance;
      this.#observeForm();
    });
  }

  #observeForm() {
    this.observe(this.#workspaceContext.data, (instance) => {
      if (!instance) return;
      this.form = instance;
      this.onObserveForm();
    });
  }

  protected onObserveForm() {}

  getAllFieldsForForm() {
    return this.#workspaceContext?.getAllFields();
  }

  setPropertyValue(alias: string, value: unknown) {
    this.#workspaceContext?.setFormProperty(alias, value);
    this.dispatchEvent(new CustomEvent("valueChange"));
  }

  static styles = [
    css`
      .flex {
        display: flex;
        flex-direction: column;
      }

      .flex + .flex {
        margin-top: var(--uui-size-3);
      }

      .flex.gap {
        margin-top: var(--uui-size-5);
      }

      .flex + uui-toggle {
        display: block;
        margin-top: var(--uui-size-3);
      }
    `,
  ];
}
