import { FormsPrevalueSourceDetailRepository } from "./prevaluesource-detail.repository.js";
import { FormsPrevalueSourceDetailStore } from "./prevaluesource-detail.store.js";
import type {
  ManifestRepository,
  ManifestStore,
} from "@umbraco-cms/backoffice/extension-registry";

export const FORMS_PREVALUESOURCE_DETAIL_REPOSITORY_ALIAS =
  "Forms.Repository.PrevalueSource.Detail";
export const FORMS_PREVALUESOURCE_DETAIL_STORE_ALIAS =
  "Forms.Store.PrevalueSource.Detail";

const repository: ManifestRepository = {
  type: "repository",
  alias: FORMS_PREVALUESOURCE_DETAIL_REPOSITORY_ALIAS,
  name: "Prevalue Source Detail Repository",
  api: FormsPrevalueSourceDetailRepository,
};

const store: ManifestStore = {
  type: "store",
  alias: FORMS_PREVALUESOURCE_DETAIL_STORE_ALIAS,
  name: "PRevalue Source Detail Store",
  api: FormsPrevalueSourceDetailStore,
};

export const manifests = [repository, store];
