import type {
  FormsCreateFormFromDataSourceModalData,
  FormsCreateFormFromDataSourceModalValue,
} from "../token/form-create-from-datasource-modal.token.js";
import { customElement, html, map } from "@umbraco-cms/backoffice/external/lit";
import { UmbModalBaseElement } from "@umbraco-cms/backoffice/modal";
import { UMB_NOTIFICATION_CONTEXT } from "@umbraco-cms/backoffice/notification";
import type {
  UUIBooleanInputEvent,
  UUIInputEvent,
  UUISelectEvent,
} from "@umbraco-cms/backoffice/external/uui";
import { DataSourceService } from "@umbraco-forms/generated";
import { tryExecute } from "@umbraco-cms/backoffice/resources";
import { GUID_EMPTY } from "@umbraco-forms/entities";

const elementName = "form-create-from-datasource-modal";

@customElement(elementName)
export class FormsFormCreateFromDataSourceModalElement extends UmbModalBaseElement<
  FormsCreateFormFromDataSourceModalData,
  FormsCreateFormFromDataSourceModalValue
> {
  #notificationContext?: typeof UMB_NOTIFICATION_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(UMB_NOTIFICATION_CONTEXT, (instance) => {
      this.#notificationContext = instance;
    });
  }

  #onFormNameChange(e: UUIInputEvent) {
    if (!this.value?.wizard) return;
    const formName = e.target.value.toString();
    const wizard = structuredClone(this.value?.wizard);
    wizard.formName = formName;
    this.modalContext?.updateValue({ wizard });
  }

  #onMappingIncludeChange(e: UUIBooleanInputEvent, index: number) {
    this.#updateMapping(index, "include", e.target.checked);
  }

  #onMappingFieldTypeChange(e: UUISelectEvent, index: number) {
    this.#updateMapping(index, "fieldTypeId", e.target.value.toString());
  }

  #onMappingDefaultValueChange(e: UUIInputEvent, index: number) {
    this.#updateMapping(index, "defaultValue", e.target.value.toString());
  }

  #updateMapping(index: number, alias: string, value: unknown) {
    if (!this.value?.wizard) return;
    const wizard = structuredClone(this.value?.wizard);
    wizard.mappings[index][alias] = value;
    this.modalContext?.updateValue({ wizard });
  }

  async #handleSubmit() {
    if (!this.value?.wizard) return;

    if (this.#isWizardValid()) {
      const { error } = await tryExecute(
        this,
        DataSourceService.postDatasourceWizardCreateForm({
          body: this.value.wizard,
        }),
        {
          disableNotifications: true,
        },
      );

      if (!error) {
        const notification = {
          data: {
            message: "Form '" + this.value.wizard.formName + "' created.",
          },
        };

        this.#notificationContext?.peek("positive", notification);

        this.modalContext?.submit();
      } else {
        const notification = { data: { message: "Could not create form." } };

        this.#notificationContext?.peek("danger", notification);
      }
    } else {
      const notification = {
        data: {
          message:
            "Could not create form. Please select a type for each included field.",
        },
      };

      this.#notificationContext?.peek("danger", notification);
    }
  }

  #isWizardValid() {
    if (!this.value?.wizard) return false;

    for (let i = 0; i < this.value.wizard.mappings.length; i++) {
      const mapping = this.value.wizard.mappings[i];
      if (mapping.include && mapping.fieldTypeId === GUID_EMPTY) {
        return false;
      }
    }

    return true;
  }

  render() {
    return html`<umb-body-layout headline=${this.localize.term("formDataSources_createFormFromDataSource")}>
      <uui-box>
        <umb-property-layout
          alias="name"
          label=${this.localize.term("formDataSources_formName")}
        >
          <uui-input
            slot="editor"
            id="name"
            .value=${this.value?.wizard?.formName}
            @change=${this.#onFormNameChange}
            label="name"
          >
        </umb-property-layout>
        <umb-property-layout
          alias="mapping"
          label=${this.localize.term("formDataSources_selectFields")}
          description=${this.localize.term(
            "formDataSources_selectFieldsDescription",
          )}
        >
          <table
            slot="editor"
            >
            <thead>
              <tr>
                <th>${this.localize.term("general_name")}</th>
                <th>Include</th>
                <th>Field Type</th>
                <th>${this.localize.term("formDataSources_defaultValue")}</th>
              </tr>
            </thead>
            <tbody>
              ${map(
                this.value?.wizard?.mappings,
                (mapping, idx) => html`
                  <tr>
                    <td>${mapping.name}</td>
                    <td>
                      <uui-toggle
                        ?checked=${mapping.include}
                        @change=${(e) => this.#onMappingIncludeChange(e, idx)}
                      ></uui-toggle>
                    </td>
                    <td>
                      <uui-select
                        .disabled=${!mapping.include}
                        @change=${(e) => this.#onMappingFieldTypeChange(e, idx)}
                        .options=${
                          this.data?.fieldTypes.map((ft) => ({
                            name: ft.name,
                            value: ft.id,
                            selected: ft.id === mapping.fieldTypeId,
                          })) ?? []
                        }
                      >
                      </uui-select>
                    </td>
                    <td>
                      <uui-input
                        id="defaultValue"
                        .value=${mapping.defaultValue}
                        @change=${(e) =>
                          this.#onMappingDefaultValueChange(e, idx)}
                        label=${this.localize.term(
                          "formDataSources_defaultValue",
                        )}
                      >
                    </td>
                  </tr>
                `,
              )}
            </tbody>
          </table>
        </umb-property-layout>
      </uui-box>
      <div slot="actions">
        <uui-button
          .label=${this.localize.term("general_close")}
          @click=${this._rejectModal}
        ></uui-button>
        <uui-button
          color="positive"
          look="primary"
          label=${this.localize.term("general_submit")}
          @click=${this.#handleSubmit}
        ></uui-button>
      </div>
    </umb-body-layout>`;
  }
}

export default FormsFormCreateFromDataSourceModalElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsFormCreateFromDataSourceModalElement;
  }
}
