import type { FormItemModel } from "./types.js";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbControllerHost } from "@umbraco-cms/backoffice/controller-api";
import { UmbItemStoreBase } from "@umbraco-cms/backoffice/store";

export class FormsFormItemStore extends UmbItemStoreBase<FormItemModel> {
  constructor(host: UmbControllerHost) {
    super(host, FORMS_FORM_ITEM_STORE_CONTEXT.contextAlias);
  }
}

export const FORMS_FORM_ITEM_STORE_CONTEXT =
  new UmbContextToken<FormsFormItemStore>("FormsFormItemStore");
