import { FORMS_DATASOURCE_ENTITY_TYPE } from "../entities.js";
import { FormsCreateFormFromDataSourceWorkspaceAction } from "../workspace-actions/create-form-workspace-action.js";
import type { FormsSecurityPermissionConditionConfig } from "../../security/conditions/index.js";
import { FORMS_SECURITY_PERMISSION_CONDITION } from "../../security/conditions/manifests.js";
import type { FormsDataSourceCreatedConditionConfig } from "../conditions/index.js";
import { FORMS_DATASOURCE_CREATED_CONDITION } from "../conditions/manifests.js";
import type { FormSecurityForUser } from "@umbraco-forms/generated";
import { UmbSaveWorkspaceAction } from "@umbraco-cms/backoffice/workspace";
import type {
  ManifestWorkspace,
  ManifestWorkspaceAction,
  ManifestWorkspaceView,
} from "@umbraco-cms/backoffice/workspace";

export const DATASOURCE_WORKSPACE_ALIAS = "Forms.Workspace.DataSource";

const workspace: ManifestWorkspace = {
  type: "workspace",
  kind: "routable",
  alias: DATASOURCE_WORKSPACE_ALIAS,
  name: "Data Source Workspace",
  api: () => import("./datasource-workspace.context.js"),
  meta: {
    entityType: FORMS_DATASOURCE_ENTITY_TYPE,
  },
};

const workspaceViews: Array<ManifestWorkspaceView> = [
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.DataSource.Design",
    name: "Form Workspace Design View",
    element: () =>
      import("./views/design/workspace-view-datasource-design.element.js"),
    weight: 90,
    meta: {
      label: "Design",
      pathname: "design",
      icon: "document",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: DATASOURCE_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.manageDataSources,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
  {
    type: "workspaceView",
    alias: "Forms.WorkspaceView.DataSource.Info",
    name: "Form Workspace Info View",
    element: () =>
      import("./views/info/workspace-view-datasource-info.element.js"),
    weight: 90,
    meta: {
      label: "Info",
      pathname: "info",
      icon: "info",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: DATASOURCE_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_SECURITY_PERMISSION_CONDITION,
        match: (security: FormSecurityForUser) =>
          security.userSecurity.manageDataSources,
      } as FormsSecurityPermissionConditionConfig,
    ],
  },
];

const workspaceActions: Array<ManifestWorkspaceAction> = [
  {
    type: "workspaceAction",
    kind: "default",
    alias: "Forms.WorkspaceAction.DataSource.Save",
    name: "Save Data Source Workspace Action",
    api: UmbSaveWorkspaceAction,
    meta: {
      label: "Save",
      look: "primary",
      color: "positive",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: DATASOURCE_WORKSPACE_ALIAS,
      },
    ],
  },
  {
    type: "workspaceAction",
    kind: "default",
    alias: "Forms.WorkspaceAction.DataSource.CreateForm",
    name: "Create Form From Data Source Workspace Action",
    api: FormsCreateFormFromDataSourceWorkspaceAction,
    meta: {
      label: "Create Form",
    },
    conditions: [
      {
        alias: "Umb.Condition.WorkspaceAlias",
        match: DATASOURCE_WORKSPACE_ALIAS,
      },
      {
        alias: FORMS_DATASOURCE_CREATED_CONDITION,
      } as FormsDataSourceCreatedConditionConfig,
    ],
  },
];

export const manifests = [workspace, ...workspaceViews, ...workspaceActions];
