import type { FormItemModel } from "../../section/forms/repository/index.js";
import type { FormTreeItemModel } from "../../section/forms/tree/index.js";
import type { UserSecurity } from "@umbraco-forms/generated";
import { SecurityService } from "@umbraco-forms/generated";
import { FORMS_FORM_ENTITY_TYPE } from "../../section/forms/entities.js";
import { FormsFormPickerContext } from "./input-form.context.js";
import { splitStringToArray } from "@umbraco-cms/backoffice/utils";
import { UUIFormControlMixin } from "@umbraco-cms/backoffice/external/uui";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";
import {
  css,
  html,
  customElement,
  property,
  state,
  repeat,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { tryExecute } from "@umbraco-cms/backoffice/resources";

const elementName = "forms-input-form";

@customElement(elementName)
export class FormsInputFormElement extends UUIFormControlMixin(
  UmbLitElement,
  "",
) {
  @property({ type: Array })
  public get selection(): Array<string> {
    return this.#pickerContext.getSelection();
  }
  public set selection(ids: Array<string>) {
    this.#pickerContext.setSelection(ids ?? []);
  }

  @property({ type: Boolean })
  multiple: boolean = false;

  @property({ type: Array })
  allowedFolders: Array<string> = [];

  @property({ type: Array })
  allowedForms: Array<string> = [];

  @state()
  private _userSecurity?: UserSecurity;

  @state()
  private _items?: Array<FormItemModel>;

  #pickerContext = new FormsFormPickerContext(this);

  constructor() {
    super();

    this.observe(
      this.#pickerContext.selectedItems,
      (selectedItems) => (this._items = selectedItems),
    );
  }

  async connectedCallback(): Promise<void> {
    super.connectedCallback();

    const { data } = await tryExecute(
      this,
      SecurityService.getSecurityUserCurrentFormSecurity({
        query: { includeFormFieldDetails: false },
      }),
    );

    this._userSecurity = data.userSecurity;
  }

  protected getFormElement() {
    return undefined;
  }

  #editForm(id: string) {
    history.pushState(
      {},
      "",
      `/umbraco/section/forms/workspace/${FORMS_FORM_ENTITY_TYPE}/edit/${id}`,
    );
  }

  #viewSubmissions(id: string) {
    history.pushState(
      {},
      "",
      `/umbraco/section/forms/workspace/${FORMS_FORM_ENTITY_TYPE}/edit/${id}/view/entries`,
    );
  }

  #pickableFilter: (item: FormTreeItemModel) => boolean = (item) => {
    return (
      !item.isFolder &&
      (this.allowedForms.length === 0 ||
        this.allowedForms.indexOf(item.unique) > -1) &&
      (this.allowedFolders.length === 0 ||
        this.allowedFolders.some((f) =>
          splitStringToArray(item.path).includes(f),
        ))
    );
  };

  #openPicker() {
    this.#pickerContext.openPicker({
      hideTreeRoot: true,
      multiple: this.multiple,
      pickableFilter: this.#pickableFilter,
    });
  }

  render() {
    return html`${this.#renderItems()} ${this.#renderAddButton()}`;
  }

  #renderItems() {
    if (!this._items?.length) return;
    return html`<uui-ref-list>
      ${repeat(
        this._items,
        (item) => item.unique,
        (item) => this.#renderItem(item),
      )}
    </uui-ref-list>`;
  }

  #renderAddButton() {
    return html`<uui-button
      id="add-button"
      look="placeholder"
      @click=${this.#openPicker}
      label=${this.localize.term("general_choose")}
    ></uui-button>`;
  }

  #renderItem(item: FormItemModel) {
    if (!item.unique) return;

    return html`
      <uui-ref-node name=${item.name}>
        <umb-icon slot="icon" name="icon-umb-contour"></umb-icon>
        <uui-action-bar slot="actions">
          ${when(
            this._userSecurity?.manageForms,
            () =>
              html`<uui-button
                @click=${() => this.#editForm(item.unique)}
                label=${this.localize.term("general_edit")}
              ></uui-button>`,
          )}
          ${when(
            this._userSecurity?.viewEntries,
            () =>
              html`<uui-button
                @click=${() => this.#viewSubmissions(item.unique)}
                label=${this.localize.term("general_open")}
              ></uui-button>`,
          )}
          <uui-button
            @click=${() => this.#pickerContext.requestRemoveItem(item.unique)}
            label=${this.localize.term(
              "formPicker_removeItemButtonLabel",
              item.name,
            )}
          >
            ${this.localize.term("general_remove")}
          </uui-button>
        </uui-action-bar>
      </uui-ref-node>
    `;
  }

  static styles = [
    css`
      #add-button {
        width: 100%;
      }

      uui-ref-node[drag-placeholder] {
        opacity: 0.2;
      }
    `,
  ];
}

export default FormsInputFormElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: FormsInputFormElement;
  }
}
